<template>
  <div>
    <j-title-header :options="title"/>

    <j-number-format-form
      ref="form"
      mode="create"
      @data="getData"/>

    <div class="float-to-left m__t--50">
      <b-button
        class="btn blue m__r--15"
        @click="saveCreateNumberFormat">Save</b-button>
      <b-button
        class="btn grey"
        @click="$router.replace('/number-format')">Cancel</b-button>
    </div>
    <br>
  </div>
</template>

<script>
import jNumberFormatForm from '@/components/number-format-form/jNumberFormatForm';
import { jMixModelCreateNumberFormat } from '@/mixins/model/number-format/jMixModelCreateNumberFormat';

export default {
  components: {
    jNumberFormatForm,
  },
  mixins: [
    jMixModelCreateNumberFormat,
  ],
  data() {
    return {
      number_format_form: null,
      title: {
        icon: 'fa-user',
        main_title: 'CREATE NUMBER FORMAT',
      },
    };
  },
  methods: {

    saveCreateNumberFormat() {
      this.$refs.form.save();
      if (this.number_format_form !== null) {
        this.sendData();
      }
    },

    getData(data) {
      this.number_format_form = data;
    },
  },
};
</script>



