import jFeature from '@/views/company/feature/jFeature';
import jUserRestriction from '@/views/company/user-restriction/jGlobalUserRestriction';
// import auth from '@/middleware/auth';
import admin from '@/middleware/admin';

const features = [
  {
    path: '/company/feature',
    name: 'jFeature',
    component: jFeature,
    meta: {
      title: 'Company Feature',
      layout: 'jWrapper',
    },
  },
  {
    path: '/company/user-restriction',
    name: 'jUserRestriction',
    component: jUserRestriction,
    meta: {
      title: 'User Restriction',
      layout: 'jWrapper',
    },
  },
];

features.map((dataPath) => {
  const eachData = dataPath;
  eachData.meta.middleware = [admin];
  return eachData;
});


export default features;



