<template>
  <div id="app">
    <j-headful :title="$route.meta.title"/>
    <j-header/>
    <router-view/>

    <!-- notification screen -->
    <j-loading-screen
      ref="loadingScreen"
      :message="loading_message"/>
    <j-info-screen
      ref="infoScreen"
      :message="info_message"/>
    <j-problem-screen
      ref="problemScreen"
      :message="problem_message"/>
    <j-success-screen
      ref="successScreen"
      :message="success_message"/>
  </div>
</template>

<script>
/* eslint-disable vue/name-property-casing */
import { jNotificationScreen } from '@/mixins/components/notification-screen/jNotificationScreen';

export default {
  name: 'default',
  mixins: [jNotificationScreen],
  data() {
    return {
    };
  },
};
</script>



