<template>
  <div>
    <j-floating-prompt ref="floating">

      <b-row>
        <b-col md="3">
          <b-row>
            <h1 class="f-blue">{{ selectedRows.length }}</h1>
            <h4> rows <br> selected </h4>
          </b-row>
        </b-col>
        <b-col md="7">
          <h4>Number Format Name : </h4>
          <p> {{ number_format_name }} </p>
        </b-col>
        <b-col md="2">
          <b-button
            class="btn red"
            @click="deleteNumberFormats">DELETE</b-button>
        </b-col>
      </b-row>

    </j-floating-prompt>

    <j-progress-screen
      ref="deleteNumberFormat"
      :data-kirim="delete_data"
      :url-api="url"
      :headers="headers"
      @finish="finish"/>

  </div>
</template>

<script>
import { jMixModelDeleteNumberFormats } from '@/mixins/model/number-format/jMixModelDeleteNumberFormat';
// import Vue from 'vue';
import jApi from '@/services/jApi';
import jProgressScreen from '@/components/progress-screen/jProgressScreen';

export default {
  components: {
    jProgressScreen,
  },
  mixins: [
    jMixModelDeleteNumberFormats,
  ],
  props: {
    selectedRows: {
      type: Array,
      default: () => [],
    },
  },
  computed: {

    headers() {
      return null;
    },

    api_axios() {
      return jApi.generateApi();
    },

    url() {
      // eslint-disable-next-line
      const getEnv = 'numbering/delete';
      return getEnv;
    },

    number_format_name() {
      const name = [];
      this.selectedRows.forEach((a) => {
        name.push(a.name);
      });
      return name.join(', ');
    },
  },

  methods: {
    show() {
      this.$refs.floating.show();
    },

    hide() {
      this.$refs.floating.hide();
    },

    finish() {
      this.hide();
      this.$emit('finish');
    },

  },
};
</script>



