<template>
  <div class="organigram-chart">
    <j-title-header :options="options"/>
    <j-nested
      v-if="division_data"
      :data="division_data"
      @click="getChild">
      <template slot-scope="props">
        <div
          v-if="!props.row.show_more && props.row.name"
          class="nestable-content">
          <b-row>
            <b-col
              cols="5"
              style="align-self:center;">
              <div
                :id="props.row.name"
                class="division-name">
                {{ props.row.name }}
              </div>
              <b-tooltip
                v-if="props.row.name.length > 22"
                :target="props.row.name">
                {{ props.row.name }}
              </b-tooltip>
            </b-col>
            <b-col cols="7">
              <table class="table-division">
                <tbody>
                  <tr>
                    <td
                      v-if="props.row.head_organigram.name"
                      class="td1">
                      <div
                        :id="props.row.head_organigram.name"
                        class="table-contain">
                        {{ props.row.head_organigram.name }}
                      </div>
                      <b-tooltip
                        v-if="props.row.head_organigram.name.length > 30"
                        :target="props.row.head_organigram.name">
                        {{ props.row.head_organigram.name }}
                      </b-tooltip>
                    </td>
                    <td
                      v-else
                      class="td1">
                      -
                    </td>
                  </tr>
                </tbody>
                <tbody>
                  <tr>
                    <td
                      v-if="props.row.head_organigram.user"
                      class="td3">
                      <div
                        :id="props.row.head_organigram.user.email"
                        class="table-contain">
                        {{ props.row.head_organigram.user.email }}
                      </div>
                      <b-tooltip
                        v-if="props.row.head_organigram.user.email.length > 30"
                        :target="props.row.head_organigram.user.email">
                        {{ props.row.head_organigram.user.email }}
                      </b-tooltip>
                    </td>
                    <td
                      v-else
                      class="td3">
                      -
                    </td>
                  </tr>
                </tbody>
              </table>
            </b-col>
          </b-row>
        </div>
        <div v-else-if="props.row.show_more">
          <div>
            <a
              v-if="props.row.show_more.parent.children[props.row.show_more.length-1].is_next
              === true"
              style="cursor:pointer;"
              @click="showMore(props.row.show_more.parent)">
              Show more . . .
            </a>
            <a
              v-else
              style="cursor:pointer;"
              @click="showMore(props.row.show_more.parent)">
              Refresh . . .
            </a>
          </div>
        </div>
      </template>
    </j-nested>
    <j-loading-nested-table v-else/>
  </div>
</template>
<script>
import jLoadingNestedTable from '@/components/skeleton-loading/jLoadingNestedTable';
import { jMixModeViewChart } from '@/mixins/mode-form/division-chart/jMixModeViewChart';

export default {
  components: {
    jLoadingNestedTable,
  },
  mixins: [
    jMixModeViewChart,
  ],
  data() {
    return {
      options: {
        icon: 'fa-bar-chart',
        main_title: 'DIVISION CHART',
        opt_title: '',
      },
    };
  },
  created() {
    this.$root.$on('click', (val) => {
      this.getChild(val);
    });
  },
  methods: {

  },
};
</script>



