<template>
  <div>
    <j-floating-prompt ref="floating">

      <b-row>
        <b-col md="3">
          <b-row>
            <h1 class="f-blue">{{ selectedRows.length }}</h1>
            <h4> rows <br> selected</h4>
          </b-row>
        </b-col>
        <b-col md="7">
          <h4>Project Name : </h4>

          <p>
            <!-- eslint-disable-next-line -->
            <label
              v-for="(row, index) in selectedRows"
              :key="index">
              {{ row.name }},
            </label>
          </p>

        </b-col>
        <b-col md="2">
          <b-button
            class="btn red"
            @click="deleteProjects">
            DELETE
          </b-button>
        </b-col>
      </b-row>

    </j-floating-prompt>

    <jProgressScreen
      ref="deleteProject"
      :data-kirim="delete_data"
      :url-api="url"
      :headers="headers"
      @finish="finish"/>

  </div>
</template>

<script>
import jProgressScreen from '@/components/progress-screen/jProgressScreen';
import { jMixModelDeleteProjects } from '@/mixins/model/project/jMixModelDeleteProject';
import Vue from 'vue';

export default {
  components: {
    jProgressScreen,
  },
  mixins: [
    jMixModelDeleteProjects,
  ],
  props: {
    selectedRows: {
      type: Array,
      default: () => [],
    },
  },
  computed: {

    headers() {
      const token = Vue.ls.get('Token');
      const headers = {
        Authorization: `Bearer ${token}`,
      };
      return headers;
    },

    url() {
      const getEnv = `${process.env.GATE_URL}project/delete`;
      return getEnv;
    },
  },
  methods: {
    show() {
      this.$refs.floating.show();
    },

    hide() {
      this.$refs.floating.hide();
    },

    finish() {
      this.hide();
      this.$emit('finish');
    },

  },
};
</script>



