

import Vue from 'vue';
import Router from 'vue-router';
import Login from './modules/login/jLogin';
import Logout from './modules/logout/jLogout';

import FlowandRules from './modules/flow-and-rules/jFlowRules';
import Organigram from './modules/organigram/jOrganigram';
import JojoForm from './modules/jojoform/jJojoForm';
import jRouterProfile from './modules/profile/jRouterProfile';
import jRouterProject from './modules/project/jRouterProject';
import jRouterPeriod from './modules/period/jRouterPeriod';
import jRouterNumberFormat from './modules/number-format/jRouterNumberFormat';
import jRouterCompany from './modules/company/jRouterCompany';
import jRouterCategory from './modules/category/jRouterCategory';

Vue.use(Router);

const moduleRoutes = [
  Login,
  Logout,
  FlowandRules,
  Organigram,
  JojoForm,
  jRouterProfile,
  jRouterProject,
  jRouterPeriod,
  jRouterNumberFormat,
  jRouterCompany,
  jRouterCategory,
];

const baseRoutes = [
  {
    path: '*',
    redirect: '/',
  },
];

const routes = baseRoutes.concat(...moduleRoutes);

const router = new Router({
  mode: 'hash',
  routes,
});

function nextFactory(context, middleware, index) {
  const subsequentMiddleware = middleware[index];
  if (!subsequentMiddleware) return context.next;
  return () => {
    const nextMiddleware = nextFactory(context, middleware, index + 1);
    subsequentMiddleware({ ...context, next: nextMiddleware });
  };
}

router.beforeEach((to, from, next) => {
  if (to.meta.middleware) {
    const middleware = Array.isArray(to.meta.middleware)
      ? to.meta.middleware
      : [to.meta.middleware];

    const context = {
      from,
      next,
      router,
      to,
    };
    const nextMiddleware = nextFactory(context, middleware, 1);
    return middleware[0]({ ...context, next: nextMiddleware });
  }
  return next();
});

export default router;



