import axios from 'axios';
import Vue from 'vue';
import jApi from './jApi';

const apii = {
  generateMockApi() {
    const token = Vue.ls.get('Token');

    const api = axios.create({
      baseURL: 'https://1a585e1f-aa8d-4f98-bb1c-6fa8493e23f1.mock.pstmn.io',
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });

    return api;
  },
};

export default {
  getCategoryList(req) {
    const api = jApi.generateApi();
    return api.post('category/list', req)
      .then(res => res);
  },

  createCategory(req) {
    const api = jApi.generateApi();
    return api.post('category/create', req)
      .then(res => res.data);
  },

  getCategoryDetail(req) {
    const api = jApi.generateApi();
    return api.post('category/detail', req)
      .then(res => res.data);
  },

  updateCategory(req) {
    const api = jApi.generateApi();
    return api.post('category/update', req)
      .then(res => res.data);
  },

  deleteCategory(req) {
    const api = jApi.generateApi();
    return api.post('category/delete', req)
      .then(res => res.data);
  },

  getListLayer(req) {
    const api = apii.generateMockApi();
    return api.post('/layer/detail-by-type', req)
      .then(res => res.data);
  },
};



