import Vue from 'vue';
import Vuex from 'vuex';
// Modules import downhere
import jSelectModule from './modules/jSelectModule';
import jFloatingPromptModule from './modules/jFloatingPromptModule';
import jLogin from './modules/jLogin';
import jFlows from './modules/jFlows';
import jRules from './modules/jRules';
import jOrganigram from './modules/jOrganigram';
import jNotificationScreen from './modules/jNotificationScreen';
import jStoreProject from './modules/jStoreProject';
import jStoreNumberFormat from './modules/jStoreNumberFormat';
import jStoreFeature from './modules/jStoreFeature';
import jStoreCategory from './modules/jStoreCategory';

// Initialize vuex
Vue.use(Vuex);

const debug = process.env.NODE_ENV !== 'production';

export default new Vuex.Store({
  modules: {
    // Include imported modules in here
    jSelectModule,
    jFloatingPromptModule,
    jLogin,
    jNotificationScreen,
    jFlows,
    jRules,
    jOrganigram,
    jStoreProject,
    jStoreNumberFormat,
    jStoreFeature,
    jStoreCategory,
  },
  strict: debug,
});



