import { mapState, mapActions } from 'vuex';

const jMixNumberFormat = {
  computed: {
    ...mapState('jStoreNumberFormat', ['list_type_form']),
  },
  methods: {
    ...mapActions('jStoreNumberFormat', ['getTypeListForm']),
  },
  created() {
    const rootId = {
      pagination: {
        limit: 0,
        page: 0,
        column: '',
        ascending: false,
        query: '',
      },
      biztrip: {
        root_id: this.$route.params.root ? Number(this.$route.params.root) : 0,
      },
      type: 1,
    };

    this.getTypeListForm(rootId);
  },
};

export default jMixNumberFormat;



