/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';
import jApi from '@/services/jApi';

export const jMixPaginationProject = {
  data() {
    return {
      select_type: 'name',
      options_search: [
        {
          text: 'Name',
          value: 'name',
        },
        {
          text: 'Code',
          value: 'code',
        },
      ],
      filter_data: '',
      env: 'project/list',
      selected_rows: [],
      data_table: [],
      load_first: false,
      columns: ['checkbox', 'name', 'code', 'description'],
      options: {
        headings: {
          name: 'NAME',
          code: 'CODE',
          period: 'PERIOD',
          description: 'DESCRIPTION',
          position: 'POSITION',
          // rule: 'RULE',
          // eslint-disable-next-line
          checkbox(h) {
            // eslint-disable-next-line
            const _this = this;
            if (_this.selected_rows.length === _this.data_table.length) {
              return h(
                'i',
                {
                  attrs: {
                    id: 'select-all-unchecked',
                    class: 'fa fa-square-o i--center-block cursor--pointer',
                    style: 'display: block; font-size: 20px;',
                  },
                  on: {
                    click() {
                      _this.selected_rows = [];
                    },
                  },
                },
                [''],
              );
            } if (_this.selected_rows.length !== _this.data_table.length) {
              return h(
                'i',
                {
                  attrs: {
                    id: 'select-all-checked',
                    class: 'fa fa-check-square i--center-block cursor--pointer',
                    style: 'display: block; font-size: 20px;',
                  },
                  on: {
                    click() {
                      _this.selected_rows = _this.data_table;
                    },
                  },
                },
                [''],
              );
            }
          },
        },
        // eslint-disable-next-line
        texts: {
          filterPlaceholder: 'Search',
          limit: 'Rows per page',
        },
        sortIcon: {
          base: 'fa',
          up: 'fa-chevron-up',
          down: 'fa-chevron-down',
          is: 'fa-sort',
        },
        filterable: true,
        sortable: ['name', 'created_date'],
        perPage: 5,
        debounce: 1000,
        perPageValues: [5, 10, 20, 50, 100],
        requestAdapter(data) {
          return {
            pagination: {
              limit: data.limit,
              page: data.page,
              column: data.orderBy ? data.orderBy : '',
              ascending: !!data.ascending,
              query: data.query ? data.query : '',
            },
          };
        },
        requestFunction(data) {
          const api = jApi.generateApi();
          return api.post(this.url, data).catch((e) => {
            this.$store.commit('jNotificationScreen/getProblemMessage', e.message).bind(this);
          });
        },
        responseAdapter({ data }) {
          return {
            data: data.data ? data.data : [],
            count: data.length ? data.length : 0,
          };
        },
      },
    };
  },
  computed: {
    ...mapState('jStoreProject', ['project_data']),
  },
  methods: {
    ...mapActions('jNotificationScreen', ['toggleProblem', 'showLoading', 'hideLoading']),

    loading() {
      if (this.load_first) {
        this.showLoading();
      }
    },

    loaded(data) {
      this.data_table = data.data.data;
      if (this.load_first) {
        this.hideLoading();
      } else {
        this.load_first = !this.load_first;
      }
    },

    refreshTable() {
      this.$refs.projectServer.getData();
    },

    projectsDeleted() {
      this.refreshTable();
      this.selected_rows = [];
    },

    filter(val) {
      this.$refs.projectServer.setFilter(val);
    },

  },
  watch: {
    filter_data(val) {
      if (val === '') {
        this.$refs.projectServer.setFilter('');
      }
    },

    selected_rows() {
      if (this.selected_rows.length === 0) {
        this.$refs.floating.hide();
      } else {
        this.$refs.floating.show();
      }
    },

    // eslint-disable-next-line
    'project_data.project_updated': function() {
      this.refreshTable();
    },

    // eslint-disable-next-line
    'project_data.project_deleted': function() {
      this.refreshTable();
    },

    // eslint-disable-next-line
    'project_data.project_created': function() {
      this.refreshTable();
    },
  },
};



