<template>

  <div class="jCreateProject">
    <b-modal
      ref="createProjectPeriod"
      class="mod-form"
      centered
      no-close-on-backdrop
      no-close-on-esc>

      <div class="float-to-right c--blue cursor--pointer">
        <i
          class="fa fa-times"
          @click="backRoute"/>
      </div>

      <h3 class="float-to-center">
        Edit Period
      </h3>

      <jFormProjectPeriod
        ref="formPeriod"
        mode="edit"
        @getData="getData"/>

      <div class="float-to-right">
        <b-button
          class="btn blue m__r--15"
          @click="createPeriod">SAVE</b-button>
        <b-button
          class="btn grey"
          @click="backRoute">CANCEL</b-button>
      </div>

    </b-modal>
  </div>


</template>

<script>
import jFormProjectPeriod from '@/components/form/project/jFormProjectPeriod';
import { jMixModelEditProjectPeriod } from '@/mixins/model/project/jMixModelEditProjectPeriod';
import { mapState } from 'vuex';

export default {
  components: {
    jFormProjectPeriod,
  },
  mixins: [
    jMixModelEditProjectPeriod,
  ],
  data() {
    return {
      period: null,
    };
  },
  computed: {
    ...mapState('jStoreProject', ['temporary_data_project']),
  },
  mounted() {
    this.$refs.createProjectPeriod.show();
  },
  methods: {
    createPeriod() {
      this.$refs.formPeriod.validateForm();
      if (this.project !== null) {
        this.sendData();
      }
    },
    getData(val) {
      this.period = val;
    },

    backRoute() {
      if (this.temporary_data_project.mode === 'create') {
        this.$router.replace('/project/create');
      } else {
        this.$router.replace(`/project/edit/${this.temporary_data_project.params.id}`);
      }
    },
  },
};
</script>



