/* eslint-disable no-shadow,consistent-return,no-param-reassign */
import apiCategory from '@/services/jApiCategory';
import router from '@/router';

const state = {
  // Set default state(s) here
  detail_category: null,
  list_category: null,
  layer_list: null,
};

const getters = {
  // Change value of state(s) with getter
};

const mutations = {
  categoryList() {
    router.push('/category/list');
  },

  showDetailCategory(state, data) {
    state.detail_category = data.data;
  },

  getCategoryList(state, data) {
    const res = [];
    data.data.data.forEach((e) => {
      res.push({
        id: e.id,
        name: e,
      });
    });
    state.list_category = res;
  },

  getLayerList(state, data) {
    state.layer_list = data.data;
  },
};

const actions = {
  async createCategory({ commit, dispatch }, req) {
    dispatch('jNotificationScreen/showLoading', 'Please Wait', { root: true });
    try {
      await apiCategory.createCategory(req);
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      dispatch('jNotificationScreen/toggleSuccess', 'Category Created', { root: true });
      commit('categoryList');
    } catch (err) {
      if (err.response.data.errors) {
        dispatch('jNotificationScreen/hideLoading', '', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
      } else {
        dispatch('jNotificationScreen/hideLoading', '', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
      }
    }
  },

  async getCategoryDetail({ commit, dispatch }, req) {
    dispatch('jNotificationScreen/showLoading', 'Please Wait', { root: true });
    try {
      const res = await apiCategory.getCategoryDetail(req);
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      commit('showDetailCategory', res);
    } catch (err) {
      dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },

  async updateCategory({ commit, dispatch }, req) {
    dispatch('jNotificationScreen/showLoading', 'Please Wait', { root: true });
    try {
      await apiCategory.updateCategory(req);
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      commit('categoryList');
    } catch (err) {
      dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },

  async deleteCategory({ commit, dispatch }, categoryId) {
    dispatch('jNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      await apiCategory.deleteCategory(categoryId);
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      dispatch('jNotificationScreen/toggleSuccess', 'Category Deleted', { root: true });
      commit('categoryList');
    } catch (err) {
      if (err.response.data.errors) {
        dispatch('jNotificationScreen/hideLoading', '', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
      } else {
        dispatch('jNotificationScreen/hideLoading', '', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
      }
    }
  },

  async getListCategory({ commit, dispatch }, req) {
    dispatch('jNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const res = await apiCategory.getCategoryList(req);
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      commit('getCategoryList', res);
    } catch (err) {
      if (err.response.data.errors) {
        dispatch('jNotificationScreen/hideLoading', '', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
      } else {
        dispatch('jNotificationScreen/hideLoading', '', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
      }
    }
  },

  async getListLayer({ commit, dispatch }, req) {
    dispatch('jNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const res = await apiCategory.getListLayer(req);
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      commit('getLayerList', res);
    } catch (err) {
      if (err.response.data.errors) {
        dispatch('jNotificationScreen/hideLoading', '', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
      } else {
        dispatch('jNotificationScreen/hideLoading', '', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
      }
    }
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};



