<template>
  <div class="jFormSetCostcenter">
    <h5>
      Cost Center
    </h5>
    <hr>
    <div style="width: 600px">

      <b-card>
        <h4 class="c--primary">
          GENERAL CONFIGURATION
        </h4>
        <hr>
        <b-row>
          <b-col
            cols="8"
            class="p__r--1">
            <h4> Period Increment </h4>
            <label class="m__b--1">By turning ON this setting, you can
            set period increment percentages of cost center
            </label>
          </b-col>
          <b-col
            cols="4"
            class="p__l--1">
            <b-form-group class="m__b--1">
              <j-toggle
                v-model="set_cost_center.auto_increment"
                @input="setAutoIncrement"/>
            </b-form-group>
          </b-col>
        </b-row>
        <hr>
        <b-row>
          <b-col
            cols="8"
            class="p__r--1">
            <h4> Percentages </h4>
            <label class="m__b--1">
              you can set period increment percentages</label>
          </b-col>
          <b-col
            cols="4"
            class="p__l--1">
            <b-form-group class="m__b--1">
              <b-row>
                <b-col cols="8">
                  <j-currency
                    v-model="set_cost_center.percentage"
                    :options="option"
                    :input_mode="true"
                    :disabled="!set_cost_center.auto_increment"/>
                </b-col>
                <b-col
                  cols="4"
                  class="p__l--1">
                  <b-button
                    v-show="set_cost_center.auto_increment"
                    class="btn btn blue outline btn-secondary btn-edit"
                    @click="setPercentage">
                    <i class="fa fa-pencil"/>
                  </b-button>
                </b-col>
              </b-row>
            </b-form-group>
          </b-col>
        </b-row>
      </b-card>
    </div>
  </div>
</template>
<script>
import { jMixModeDetailFeatureCostcenter } from '@/mixins/mode-form/feature/jMixModeDetailFeatureCostcenter';

export default {
  mixins: [
    jMixModeDetailFeatureCostcenter,
  ],
  data() {
    return {
      halo: 2.22,
      set_cost_center: {
        percentage: 0,
        auto_increment: false,
      },
    };
  },
};
</script>



