/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapState, mapActions } from 'vuex';

export const jMixModeDetailFeatureCostcenter = {
  data() {
    return {
      option: {
        digitGroupSeparator: '.',
        decimalCharacter: ',',
        decimalCharacterAlternative: '.',
        currencySymbol: '%',
        currencySymbolPlacement: 's',
        minimumValue: '0',
      },
    };
  },
  computed: {
    ...mapState('jStoreFeature', ['list_feature', 'update_res']),
  },
  watch: {
    list_feature(val) {
      const listFeature = this.generateData(val);
      this.set_cost_center = listFeature;
    },
    update_res() {
      this.getList();
    },
  },
  created() {
    this.getList();
  },
  methods: {
    ...mapActions('jStoreFeature', ['getListFeature', 'updateFeature']),
    getList() {
      const req = {
        pagination: {
          limit: 0,
          page: 1,
          column: 'id',
          ascending: false,
          query: '',
        },
      };
      this.getListFeature(req);
    },
    generateData(val) {
      const setting = val.filter(i => i.setting === 1);
      let setFeature;
      if (setting.length > 0) {
        setFeature = {
          percentage: parseFloat(setting[0].value),
          auto_increment: setting[0].status === 1,
        };
      } else {
        setFeature = {
          percentage: 0,
          auto_increment: false,
        };
      }
      return setFeature;
    },
    setPercentage() {
      const req = {
        company_features: [
          {
            setting: 1,
            status : 1,
            value: this.set_cost_center.percentage,
          },
        ],
      };
      this.updateFeature(req);
    },
    setAutoIncrement(val) {
      const req = {
        company_features: [
          {
            setting: 1,
            status : val ? 1 : 0,
            value: this.set_cost_center.percentage,
          },
        ],
      };
      this.updateFeature(req);
    },
  },
};



