/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions } from 'vuex';

export const jMixModelEditNumberFormat = {
  computed: {
    generateData() {
      if (this.number_format_form !== null) {
        const genData = {
          id: this.number_format_form.id,
          name: this.number_format_form.name ? this.number_format_form.name : '',
          pattern: this.number_format_form.patterns,
          type: this.number_format_form.type.id ? this.number_format_form.type.id : 0,
          is_default: this.number_format_form.is_default ?
            this.number_format_form.is_default : false,
        };
        return genData;
      }
      return null;
    },
  },
  methods: {
    ...mapActions('jStoreNumberFormat', ['updateNumberFormatDetail']),

    sendData() {
      this.updateNumberFormatDetail(this.generateData);
    },
  },
};



