<template>
  <div class="jCreateProject">
    <b-modal
      ref="createProject"
      class="mod-form"
      centered
      no-close-on-backdrop
      no-close-on-esc>

      <div class="float-to-right c--blue cursor--pointer">
        <i
          class="fa fa-times"
          @click="$router.replace('/project')"/>
      </div>

      <h3 class="float-to-center">
        Create Project
      </h3>

      <jFormProject
        ref="formProject"
        mode="create"
        @getData="getData"/>

      <div class="float-to-right">
        <b-button
          class="btn blue"
          @click="createProject">
          Create Project
        </b-button>
      </div>

    </b-modal>
  </div>
</template>

<script>
import jFormProject from '@/components/form/project/jFormProject';
import { jMixModelCreateProject } from '@/mixins/model/project/jMixModelCreateProject';

export default {
  components: {
    jFormProject,
  },
  mixins: [
    jMixModelCreateProject,
  ],
  data() {
    return {
      project: null,
    };
  },

  mounted() {
    this.$refs.createProject.show();
  },
  methods: {
    createProject() {
      this.$refs.formProject.validateForm();
      if (this.project !== null) {
        this.sendData();
      }
    },
    getData(val) {
      this.project = val;
    },
  },
};
</script>



