<template>
  <div class="jEditProject">
    <b-modal
      ref="editProject"
      class="mod-form"
      centered
      no-close-on-backdrop
      no-close-on-esc>

      <div class="float-to-right c--blue cursor--pointer">
        <i
          class="fa fa-times"
          @click="$router.replace('/project')"/>
      </div>

      <h3 class="float-to-center">
        Edit Project
      </h3>

      <jFormProject
        ref="formProject"
        mode="edit"
        @getData="getData"/>

      <div>
        <b-row>
          <b-col class="float-to-left">
            <b-button
              class="btn red"
              @click="deleteProjectData">
              Delete Project
            </b-button>
          </b-col>

          <b-col class="float-to-right">
            <b-button
              class="btn blue"
              @click="editProject">
              Edit Project
            </b-button>
          </b-col>
        </b-row>

      </div>

    </b-modal>
  </div>
</template>

<script>
import jFormProject from '@/components/form/project/jFormProject';
import { jMixModelEditProject } from '@/mixins/model/project/jMixModelEditProject';
import { jMixModelDeleteProject } from '@/mixins/model/project/jMixModelDeleteProject';
import { mapActions, mapState } from 'vuex';

export default {
  components: {
    jFormProject,
  },

  mixins: [
    jMixModelEditProject,
    jMixModelDeleteProject,
  ],

  data() {
    return {
      project: null,
    };
  },

  computed: {
    ...mapState('jStoreProject', ['project_data']),
  },

  mounted() {
    this.$refs.editProject.show();
  },

  methods: {
    ...mapActions('jStoreProject', ['deleteProject']),

    editProject() {
      this.$refs.formProject.validateForm();
      if (this.project !== null) {
        this.sendData();
      }
    },

    getData(val) {
      this.project = val;
    },

    deleteProjectData() {
      const text = `You want to delete ${this.project_data.detail_project.name} project`;

      this.$swal({
        title: 'Are you sure?',
        text,
        type: 'question',
        showCancelButton: true,
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes',
      }).then((result) => {
        if (result.value) {
          this.deleteProject(this.delete_data);
        }
      });
    },
  },
};
</script>



