              {{ flow.order }}
            </div>
          </div>
          <div v-else>
            {{ flow.order }}
          </div> -->
        <!-- </div> -->
        <!-- </div> -->
        <div
          slot="created_date"
          slot-scope="props">
          {{ props.row.created_date | moment("DD/MM/YYYY") }}
        </div>

        <div
          slot="type_name"
          slot-scope="props">
          {{ props.row.type === 1 ? 'ORDER' : '' }}
          {{ props.row.type === 2 ? 'NO ORDER' : '' }}
          {{ props.row.type === 3 ? 'MIN' : '' }}
          {{ props.row.minimum_action !== 0 ? props.row.minimum_action : '' }}
        </div>

      </v-server-table>
    </div>

