      ref="createFlow"
      class="mod-form"
      centered
      no-close-on-esc>
      <div class="float-to-center p--20">
        <b-button
          class="btn blue m__r--20"
          @click="$router.replace('/flow/new')">
          Create Flow Default
        </b-button>
        <b-button
