      rowing: null,
      opsi: {
        icon: 'fa-user',
        main_title: 'FLOWS',
        opt_title: '',
      },
    };
  },
  computed: {
    ...mapState('jFlows', ['flows_table']),
    ...mapState('jRules', ['upload_rules_finished']),
  },
  watch: {
    rowing(newData, oldData) {
      if (oldData) {
        this.removeRowClick(oldData);
      }
    },

    upload_rules_finished() {
      this.refreshTable();
    },
    // flows_table() {
    //   const widthFlow = document.querySelector
    // (`.row-${this.flows_table[0].id}`).childNodes[3].offsetWidth;
    //   for (let x = 0; x < this.flows_table.length; x++) {
    //     let total = 0;
    //     if (document.querySelector(`.row-${this.flows_table[x].id}`)) {
    //       const flowList = document.querySelector(`.row-$
