    // (`.row-${this.flows_table[x + 1].id}`)) {
    //               this.stop.push(null);
    //             }
    //           }
    //         }
    //       }
    //       if (this.stop[x] === null &&
    // document.querySelector(`.row-${this.flows_table[x + 1].id}`)) {
    //         this.stop.push(null);
    //       }
    //     }
    //   }
    // },
  },
  created() {
    // this.getListFlow();
  },
  methods: {
    ...mapActions('jFlows', ['getListFlow']),
    // flowRowClass(index, rowIndex) {
    //   // console.log('kesatu');
    //   // console.log(index);
    //   // console.log('kedua');
    //   // console.log(rowIndex);
    //   // console.log(this.stop[rowIndex] !== null);
    //   // return 'light-blue-flow flow-table flow-arrow';
    //   // },
    //   //   console.log(rowIndex);
    //   // if (index % 2 === 0) {
    //   //   return 'light-blue-flow flow-table flow-arrow';
    //   // }
    //   // if (this.stop[rowIndex] !== null) {
