    //     }
    //     return 'flow-table flow-arrow';
    //   }
    // },
    createFlow() {
      this.$refs.createFlow.show();
    },
    rowClick(data) {
      const elem = document.getElementsByClassName(`row-${data.row.id}`);
      elem[0].setAttribute('style', 'background-color: #b3e8f8;');
      elem[0].classList.add('mystyle');
      this.rowing = data.row.id;
      const dataa = data;
      const isCreatedByDivision = dataa.row.create_by_division
        ? dataa.row.create_by_division : false;
      dataa.row.create_by_division = isCreatedByDivision;
      this.$emit('row-click', data);
    },
    removeRowClick(data) {
      const elem = document.getElementsByClassName(`row-${data}`);
      if (elem.length === 1) {
        elem[0].setAttribute('style', 'background-color: none;');
      }
    },
    removeRowBackground() {
      if (this.rowing) {
        const elem = document.getElementsByClassName(`row-${this.rowing}`);
        // console.log(elem.length);
        if (elem.length === 1) {
          elem[0].setAttribute('style', 'background-color: none;');
        }
      }
      this.rowing = null;
      this.$emit('pagination');
    },

    refreshTable() {
      this.$refs.flowServer.getData();
    },
  },
};
</script>

