// import jFlow from '@/views/flow-and-rules/flow/jFlow';
import jMyRecords from '@/views/jojoform/my-records/jMyRecords';
import jFormCreator from '@/views/jojoform/form-creator/jFormCreator';
import jFormReport from '@/views/jojoform/form-report/jFormReport';
// import jNewForm from '@/views/jojoform/my-records/jNewForm';
// import jNewForma from '@/views/jojoform/form-creator/new/form/jNewForm';
import jNewReport from '@/views/jojoform/form-report/new/jNewReport';
import jBoard from '@/views/jojoform/my-board/jBoard';
import jMyRecordsDetail from '@/views/jojoform/my-records/detail/jDetailRecords';
import jCreatorNew from '@/views/jojoform/form-creator/new/jCreatorNew';
import auth from '@/middleware/auth';
// import jRules from '@/views/flow-and-rules/rules/jRules';
// import jRulesNew from '@/views/flow-and-rules/rules/jRulesNew';
// import jIndexFR from '@/views/flow-and-rules/index';


const jojoform = [
  {
    path: '/jojoform/my-board',
    name: 'My Board',
    component: jBoard,
    meta: {
      title: 'My Board',
      layout: 'jWrapper',
    },
  },
  {
    path: '/jojoform/my-records',
    name: 'My Records',
    component: jMyRecords,
    meta: {
      title: 'My Records',
      layout: 'jWrapper',
    },
  },
  {
    path: '/jojoform/my-records/detail/:id',
    name: 'My Records Detail',
    component: jMyRecordsDetail,
    meta: {
      title: 'Records Detail',
      layout: 'jWrapper',
    },
  },
  // {
  //   path: '/jojoform/my-records/new-form',
  //   name: 'New Form',
  //   component: jNewForm,
  //   meta: {
  //     title: 'Create Form',
  //   },
  // },
  {
    path: '/jojoform/form-creator',
    name: 'Form Creator',
    component: jFormCreator,
    meta: {
      title: 'Form Creator',
      layout: 'jWrapper',
    },
  },
  {
    path: '/jojoform/form-creator/new/:id',
    name: 'New Form Creator',
    component: jCreatorNew,
    meta: {
      title: 'New Form Creator',
      layout: 'jWrapper',
    },
  },
  // {
  //   path: '/jojoform/form-creator/new-form',
  //   name: 'New Form',
  //   component: jNewForma,
  //   meta: {
  //     title: 'Create Form',
  //   },
  // },
  {
    path: '/jojoform/form-report',
    name: 'Form Report',
    component: jFormReport,
    meta: {
      title: 'Form Report',
      layout: 'jWrapper',
    },
  },
  {
    path: '/jojoform/form-report/new-report/:id',
    name: 'New Report',
    component: jNewReport,
    meta: {
      title: 'New Report',
    },
  },
  // {
  //   path: '/rules/new',
  //   name: 'New Rules',
  //   component: jRulesNew,
  //   meta: {
  //     title: 'Create Rule',
  //   },
  // },
  // {
  //   path: '/flows',
  //   name: 'Flows',
  //   component: jIndexFR,
  //   meta: {
  //     title: 'Flows and Rules',
  //     layout: 'jWrapper',
  //   },
  // },
];

jojoform.map((dataPath) => {
  const eachData = dataPath;
  eachData.meta.middleware = auth;
  return eachData;
});


export default jojoform;



