<template lang="html">
  <div class="jIndexOrganigram">
    <router-view/>
    <div>
      <j-title
        ref="title"
        @row-click="rowClickTitle"
        @createTitle="createTitle"
        @doneUpload="doneUpload"
        @pagination="hideFloatingPrompt"
        @search-button="hideFloatingPrompt"/>
      <jFloatingPromptTitle
        v-if="title"
        :title="title"
        @hideFloatingPrompt="hideFloatingPromptTitle"/>
    </div>

    <br>
    <br>

    <div>
      <j-division
        ref="division"
        @row-click="rowClickDivision"
        @createDivision="createDivision"
        @pagination="hideFloatingPrompt"/>
      <jFloatingPromptDivision
        v-if="division"
        :division="division"
        @hideFloatingPrompt="hideFloatingPromptDivision"/>
    </div>

    <div>
      <div
        class="space-floating"/>
    </div>

  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import jFloatingPromptTitle from './title/jFloatingPromptTitle';
import jFloatingPromptDivision from './division/jFloatingPromptDivision';
import jTitle from './title/jTitle';
import jDivision from './division/jDivision';
import jRules from '../flow-and-rules/rules/jRules';


export default {
  name: 'JIndex',
  components: { jTitle, jDivision, jFloatingPromptTitle, jFloatingPromptDivision, jRules },
  data() {
    return {
      title: null,
      division: null,
      lip: null,
    };
  },
  computed: {
    ...mapState('jOrganigram', ['title_deleted', 'division_deleted', 'new_title_added', 'new_division_added', 'division_updated', 'title_updated']),
    ...mapState('jFlows', ['divisions_list', 'users_list']),
  },
  watch: {
    // refresh table title after deleted
    title_deleted() {
      this.hideFloatingPromptTitle();
      this.$refs.title.refreshTable();
      this.$refs.title.nullOfRowing();
      this.$refs.division.refreshTable();
    },

    // refresh table division after deleted
    division_deleted() {
      this.hideFloatingPromptDivision();
      this.$refs.division.refreshTable();
      this.$refs.division.nullOfRowing();
      this.$refs.title.refreshTable();
      this.getListDivisions();
    },

    // refresh after add new title
    new_title_added() {
      this.$refs.title.refreshTable();
      this.$refs.division.refreshTable();
    },

    // refresh after add new division
    new_division_added() {
      this.$refs.division.refreshTable();
      this.getListDivisions();
    },

    // refresh after division update
    division_updated() {
      this.hideFloatingPromptDivision();
      this.$refs.division.refreshTable();
      this.$refs.title.refreshTable();
      this.getListDivisions();
    },

    // refresh after title update
    title_updated() {
      this.hideFloatingPromptTitle();
      this.$refs.division.refreshTable();
      this.$refs.title.refreshTable();
    },
  },
  // mounted() {
  //   this.$refs.division.refreshTable();
  //   this.$refs.title.refreshTable();
  // },
  methods: {
    ...mapActions('jFlows', ['getListDivisions']),
    rowClickTitle(data) {
      const divisions = data.row.divisions.map((dataDiv) => {
        const division = {
          id: dataDiv.id,
          name: dataDiv.name,
        };
        return division;
      });
      const TitleData = {
        id: data.row.id,
        name: data.row.name,
        code: data.row.code,
        description: data.row.description,
        user: data.row.user ? {
          email: data.row.user.email,
          id: data.row.user.id,
        } : null,
        divisions,
      };
      this.title = TitleData;
      if (!this.title.user) {
        this.title.user = null;
      }
      if (this.division) {
        this.hideFloatingPromptDivision();
      }
    },

    rowClickDivision(data) {
      // this.$router.replace(`/organigram/division/edit/${data.row.id}`);

      const divisionData = {
        id: data.row.id,
        name: data.row.name,
        parent_division: typeof data.row.root_division.id === 'undefined' ?
          { id: 0 } : { id: data.row.root_division.id },
        head_division: typeof data.row.head_organigram.id === 'undefined' ?
          null : { id: data.row.head_organigram.id },
        flow_before: [],
        flow_after: [],
        code: data.row.code ? data.row.code : '',
        description: data.row.description,
        total_member: data.row.total_member,
      };
      if (data.row.dynamic_flow.length > 0) {
        data.row.dynamic_flow.forEach((e) => {
          const flow = {
            id: e.id,
            flow: {
              id: e.flow_id,
            },
            type: null,
            is_delete: e.is_delete ? e.is_delete : false,
          };
          if (e.type === 1) {
            this.$set(flow, 'type', 1);
            divisionData.flow_before.push(flow);
          } else {
            this.$set(flow, 'type', 2);
            divisionData.flow_after.push(flow);
          }
        });
      }
      this.division = divisionData;
      if (this.title) {
        this.hideFloatingPromptTitle();
      }
    },

    createDivision() {
      this.$router.replace('/organigram/division/new');
      if (this.title) {
        this.hideFloatingPromptTitle();
      } else if (this.division) {
        this.hideFloatingPromptDivision();
      }
    },

    createTitle() {
      this.$router.replace('/organigram/title/new');
      if (this.title) {
        this.hideFloatingPromptTitle();
      } else if (this.division) {
        this.hideFloatingPromptDivision();
      }
    },

    doneUpload() {
      this.$refs.division.refreshTable();
    },
    // deleteTitle() {
    //   this.$refs.title.refreshTable();
    //   this.hideFloatingPromptTitle();
    // },
    // updateTitle() {
    //   this.$refs.title.refreshTable();
    //   // this.hideFloatingPromptTitle();
    // },

    hideFloatingPromptTitle() {
      this.$refs.title.removeRowClick(this.title.id);
      setTimeout(() => {
        this.title = null;
      }, 200);
    },

    hideFloatingPromptDivision() {
      this.$refs.division.removeRowClick(this.division.id);
      setTimeout(() => {
        this.division = null;
      }, 200);
    },

    hideFloatingPrompt() {
      if (this.title) {
        setTimeout(() => {
          this.title = null;
        }, 200);
      } else {
        setTimeout(() => {
          this.division = null;
        }, 200);
      }
    },
  },
};
</script>

<style lang="scss">
.space-floating {
  height: 215px;
  width: 100%;
}
</style>



