<template>
  <div>
    <div v-if="type_list.type === 1">
      <b-form-group>
        <label> {{ type_list.field_name }}</label>
        <j-input
          v-model="type_list.value"

          placeholder="Short Text"/>
      </b-form-group>
    </div>

    <div v-else-if="type_list.type === 2">
      <b-form-group>
        <label> {{ type_list.field_name }}</label>
        <j-textarea
          v-model="type_list.value"

          placeholder="Descriptive"/>
      </b-form-group>
    </div>

    <div v-else-if="type_list.type === 3">
      <b-form-group>
        <label> {{ type_list.field_name }}</label>
        <j-input
          v-model="type_list.value"

          placeholder="Add Number"/>
      </b-form-group>
    </div>

    <div v-else-if="type_list.type === 4">
      <b-form-group>
        <label> {{ type_list.field_name }}</label>
        <j-input
          v-model="type_list.value"

          placeholder="Add Currency"/>
      </b-form-group>
    </div>

    <div v-else-if="type_list.type === 5">
      <b-form-group>
        <label> {{ type_list.field_name }}</label>
        <j-datepicker
          v-model="type_list.value"

          placeholder="Add Date"/>
      </b-form-group>
    </div>

    <div v-else-if="type_list.type === 6">
      <b-form-group>
        <label> {{ type_list.field_name }}</label>
        <j-clockpicker
          v-model="type_list.value"

          placeholder="Add Time"/>
      </b-form-group>
    </div>

    <div v-else-if="type_list.type === 7">
      <b-form-group>
        <label> {{ type_list.field_name }}</label>
        <j-clockpicker
          v-model="type_list.value"
          placeholder="Add Time"/>
      </b-form-group>
    </div>

    <div v-else-if="type_list.type === 8">
      <b-form-group>
        <label> {{ type_list.field_name }}</label>
        <b-row>
          <b-col
            cols="1"
            class="float-to-left icon-answer">
            <i class="fa fa-dot-circle-o"/>
          </b-col>
          <b-col cols="7">
            <j-input placeholder="Add multiple answer"/>
          </b-col>
          <b-col class="float-to-left">
            <i class="fa fa-plus-circle"/>
            <i class="fa fa-minus-circle"/>
          </b-col>
        </b-row>

      </b-form-group>
    </div>

    <div v-else-if="type_list.type === 9">
      <b-form-group>
        <label> {{ type_list.field_name }}</label>
        <b-row>
          <b-col
            cols="1"
            class="float-to-left">
            <i class="fa fa-check-square-o"/>
          </b-col>
          <b-col cols="7">
            <j-input placeholder="Add answer"/>
          </b-col>
          <b-col class="float-to-left"/>
        </b-row>

      </b-form-group>
    </div>

    <div v-else-if="type_list.type === 10">
      <b-form-group>
        <label> {{ type_list.field_name }}</label>
        <b-row>
          <b-col cols="7">
            <j-input placeholder="Add answer"/>
          </b-col>
          <b-col class="float-to-left">
            <i class="fa fa-plus-circle"/>
            <i class="fa fa-minus-circle"/>
          </b-col>
        </b-row>

      </b-form-group>
    </div>

    <div v-else-if="type_list.type === 11">
      <b-form-group>
        <label> {{ type_list.field_name }}</label>
        <j-singleupload/>
      </b-form-group>
    </div>

    <div v-else/>

  </div>
</template>

<script>
export default {
  props: {
    type_list: {
      type: Object,
      default: null,
    },
  },
};
</script>

<style lang="scss">
</style>



