// import jFlow from '@/views/flow-and-rules/flow/jFlow';
import jFlowNew from '@/views/flow-and-rules/flow/jFlowNew';
import jFlowCreateType2 from '@/views/flow-and-rules/flow/create/jFlowCreateType2';
import jFlowEditType2 from '@/views/flow-and-rules/flow/edit/jFlowEditType2';
// import jRules from '@/views/flow-and-rules/rules/jRules';
import jRulesNew from '@/views/flow-and-rules/rules/jRulesNew';
import jIndexFR from '@/views/flow-and-rules/index';
import auth from '@/middleware/auth';
import admin from '@/middleware/admin';
import jRulesUpdate from '@/views/flow-and-rules/rules/jRulesUpdate';
import jFlowtest from '@/views/flow-and-rules/flow/jFlowUpdate';

const jojoflows = [
  {
    path: '/flow/update-by-division/:id',
    name: 'Edit Flow Type 2',
    component: jFlowEditType2,
    meta: {
      title: 'Edit Flow Type 2',
    },
  },
  {
    path: '/flow/new-by-division',
    name: 'New Flow Type 2',
    component: jFlowCreateType2,
    meta: {
      title: 'Create Flow By Division',
    },
  },
  {
    path: '/flow/new',
    name: 'New Flow',
    component: jFlowNew,
    meta: {
      title: 'Create Flow',
    },
  },
  {
    path: '/flow/update/:id',
    name: 'Update Flow',
    component: jFlowtest,
    meta: {
      title: 'Update Flow',
    },
  },
  {
    path: '/rules/new',
    name: 'New Rules',
    component: jRulesNew,
    meta: {
      title: 'Create Rule',
    },
  },
  {
    path: '/rules/update/:id',
    name: 'Update Rules',
    component: jRulesUpdate,
    meta: {
      title: 'Update Rule',
    },
  },
  {
    path: '/flows',
    name: 'Flows',
    component: jIndexFR,
    meta: {
      title: 'Flows and Rules',
      layout: 'jWrapper',
    },
  },
];

jojoflows.map((dataPath) => {
  const eachData = dataPath;
  eachData.meta.middleware = [auth, admin];
  return eachData;
});


export default jojoflows;



