<template>
  <div class="jIndexFlows">
    <!-- table of flow -->
    <div>
      <j-flow
        ref="flow"
        @row-click="flowRowClick"
        @pagination="hideFloatingPrompt"/>

      <jFlowFloatingPrompt
        v-if="flow"
        ref="flowFloating"
        :flow="flow"
        @hideFloatingPrompt="hideFloatingPrompt"
        @deleteFlow="deleteFlow"/>
    </div>

    <br>
    <br>

    <div>
      <j-rules
        ref="rules"
        @row-click="rulesRowClick"
        @pagination="hideFloatingPrompt"/>

      <jRulesFloatingPrompt
        v-if="rules"
        ref="rulesFloating"
        :rules="rules"
        class="rulesfloating"
        @hideFloatingPrompt="hideFloatingPrompt"
        @deleteRules="deleteRules"/>
    </div>

    <!-- space after floating prompt appear -->
    <div>
      <div
        class="space-floating"/>
    </div>

  </div>
</template>

<script>
import jLoadingTable from '@/components/skeleton-loading/jLoadingTable';
import { mapActions, mapState } from 'vuex';
import jFlow from './flow/jFlow';
import jRules from './rules/jRules';
import jFlowFloatingPrompt from './flow/jFlowFloatingPrompt';
import jRulesFloatingPrompt from './rules/jRulesFloatingPrompt';

export default {
  name: 'Index',
  components: {
    jFlow,
    jRules,
    jFlowFloatingPrompt,
    jRulesFloatingPrompt,
    jLoadingTable,
  },
  data() {
    return {
      flow: null,
      rules: null,
    };
  },
  computed: {
    ...mapState('jFlows', ['flows_table', 'is_flow_deleted']),
    ...mapState('jRules', ['rules_table', 'is_rules_deleted']),
    flow_id() {
      if (this.flow) {
        return this.flow.id;
      }
      return null;
    },
    rules_id() {
      if (this.rules) {
        return this.rules.id;
      }
      return null;
    },
  },
  watch: {
    is_flow_deleted() {
      if (this.is_flow_deleted) {
        this.$refs.flow.refreshTable();
        this.$store.commit('jFlows/flowDeleted');
      }
    },
    is_rules_deleted() {
      if (this.is_rules_deleted) {
        this.$refs.rules.refreshTable();
        this.$store.commit('jRules/rulesDeleted');
      }
    },
  },
  created() {
    // this.getListFlow();
    // this.getListRules();
  },
  methods: {
    ...mapActions('jFlows', ['removeFlow', 'getListFlow']),
    ...mapActions('jRules', ['removeRules', 'getListRules']),
    flowRowClick(data) {
      this.flow = data.row;
      this.flow.flow_type = 1;
      if (this.rules) {
        this.$refs.rules.removeRowClick(this.rules_id);
      }
      this.rules = null;
    },
    rulesRowClick(data) {
      this.rules = data.row;
      if (this.flow) {
        this.$refs.flow.removeRowClick(this.flow_id);
      }
      this.flow = null;
    },

    hideFloatingPrompt() {
      if (this.flow) {
        if (this.flow_id) {
          this.$refs.flow.removeRowClick(this.flow_id);
          setTimeout(() => {
            this.flow = null;
          }, 200);
        }
        setTimeout(() => {
          this.flow = null;
        }, 200);
      } else {
        if (this.rules_id) {
          this.$refs.rules.removeRowClick(this.rules_id);
          setTimeout(() => {
            this.rules = null;
          }, 200);
        }
        setTimeout(() => {
          this.rules = null;
        }, 200);
      }
    },

    deleteFlow() {
      const deleteFlow = {
        id: this.flow_id,
      };
      this.removeFlow(deleteFlow);
      // this.$refs.flow.refreshTable();
      this.hideFloatingPrompt();
    },
    deleteRules() {
      const deleteRules = {
        id: this.rules_id,
      };
      // this.$refs.rules.refreshTable();
      this.removeRules(deleteRules);
      this.hideFloatingPrompt();
    },
  },
};
</script>

<style lang="scss">

</style>



