<template>
  <div>
    <j-floating-prompt ref="floating">

      <b-row>
        <b-col md="3">
          <b-row>
            <h1 class="f-blue">{{ selectedRows.length }}</h1>
            <h4> Row <br> selected </h4>
          </b-row>
        </b-col>
        <b-col md="7">
          <h4>Category Name : </h4>
          <p> {{ category_name }} </p>
        </b-col>
        <b-col md="2">
          <b-button
            class="btn red"
            @click="deleteCategorys">Delete</b-button>
        </b-col>
      </b-row>

    </j-floating-prompt>

    <j-progress-screen
      ref="deleteCategory"
      :data-kirim="delete_data"
      :url-api="url"
      :headers="headers"
      @finish="finish"/>

  </div>
</template>

<script>
import { jMixModelDeleteCategorys } from '@/mixins/model/category/jMixModelDeleteCategory';
import Vue from 'vue';

export default {
  mixins: [
    jMixModelDeleteCategorys,
  ],
  props: {
    selectedRows: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
    };
  },
  computed: {

    headers() {
      const token = Vue.ls.get('Token');
      const headers = {
        // eslint-disable-next-line
        Authorization: 'Bearer ' + token,
      };
      return headers;
    },

    url() {
      // eslint-disable-next-line
      const getEnv = process.env.GATE_URL + 'category/delete';
      return getEnv;
    },

    category_name() {
      const name = [];
      this.selectedRows.forEach((a) => {
        name.push(a.name);
      });
      return name.join(', ');
    },
  },

  methods: {
    show() {
      this.$refs.floating.show();
    },

    hide() {
      this.$refs.floating.hide();
    },

    finish() {
      this.hide();
      this.$emit('finish');
    },

  },
};
</script>



