<template>
  <div>
    <v-server-table
      v-show="load_first"
      ref="periodServer"
      :url="env"
      :columns="columns"
      :options="options"
      class="jTable"
      @loading="loading"
      @loaded="loaded">

      <div
        slot="checkbox"
        slot-scope="props"
        class="jTableReport">
        <b-form-group>
          <b-form-checkbox-group v-model="selected_rows">
            <b-form-checkbox :value="props.row"/>
          </b-form-checkbox-group>
        </b-form-group>
      </div>

      <div
        slot="name"
        slot-scope="props">
        <div @click="$router.replace(`/project/edit/${props.row.id}`)">
          -
        </div>
      </div>

      <div
        slot="created_date"
        slot-scope="props">
        {{ props.row.created_date | moment("DD/MM/YYYY") }}
      </div>

    </v-server-table>

    <div>
      <jLoadingTable v-if="!load_first"/>
    </div>

    <j-floating-prompt ref="floating">

      <b-row>
        <b-col md="3">
          <b-row>
            <h1 class="f-blue">{{ selected_rows.length }}</h1>
            <h4> rows <br> selected</h4>
          </b-row>
        </b-col>
        <b-col md="7">
          <h4>Period Name : </h4>

          <p>
            <!-- eslint-disable-next-line -->
            <label
              v-for="(row, index) in selected_rows"
              :key="index">
              {{ row.name }},
            </label>
          </p>

        </b-col>
        <b-col md="2">
          <b-button
            class="btn red">DELETE</b-button>
        </b-col>
      </b-row>

    </j-floating-prompt>

  </div>
</template>

<script>
import { jMixPaginationPeriod } from '@/mixins/paginations/period/jMixPaginationPeriod';
import jLoadingTable from '@/components/skeleton-loading/jLoadingTableV1';

export default {
  components: {
    jLoadingTable,
  },
  mixins: [
    jMixPaginationPeriod,
  ],
};
</script>



