<template>
  <div>
    <v-server-table
      v-show="load_first"
      ref="projectServer"
      :url="env"
      :columns="columns"
      :options="options"
      class="jTable"
      @loading="loading"
      @loaded="loaded">

      <div
        slot="beforeFilter"
        class="custom-search">
        <b-row>
          <b-col
            cols="3"
            class="option p__r--2">
            <j-select
              v-model="select_type"
              :disabled="false"
              :options="options_search"
              placeholder=""/>
          </b-col>
          <b-col
            cols="4"
            class="p__l--2">
            <b-form
              @keyup.enter.prevent="filter(filter_data)">
              <b-form-group>
                <j-input
                  v-model="filter_data"/>
              </b-form-group>
            </b-form>
          </b-col>
          <b-col
            cols="5"
            class="option p__l--30">
            <b-button
              class="btn blue outline"
              @click="filter(filter_data)"> <i class="fa fa-search bold" /> Search </b-button>
          </b-col>
        </b-row>
      </div>

      <div
        slot="checkbox"
        slot-scope="props"
        class="jTableReport">
        <b-form-group>
          <b-form-checkbox-group v-model="selected_rows">
            <b-form-checkbox :value="props.row"/>
          </b-form-checkbox-group>
        </b-form-group>
      </div>

      <div
        slot="name"
        slot-scope="props">
        <div @click="$router.replace(`/project/edit/${props.row.id}`)">
          {{ props.row.name }}
        </div>
      </div>

      <div
        slot="code"
        slot-scope="props">
        <div @click="$router.replace(`/project/edit/${props.row.id}`)">
          {{ props.row.code }}
        </div>
      </div>

      <!-- <div
        slot="period"
        slot-scope="props">
        <div @click="$router.replace(`/project/edit/${props.row.id}`)">
          -
        </div>
      </div> -->

      <div
        slot="description"
        slot-scope="props">
        <div @click="$router.replace(`/project/edit/${props.row.id}`)">
          {{ props.row.description }}
        </div>
      </div>

      <!-- <div
        slot="position"
        slot-scope="props">
        <div @click="$router.replace(`/project/edit/${props.row.id}`)">
          -
        </div>
      </div> -->

      <!-- <div
        slot="rule"
        slot-scope="props">
        <div @click="$router.replace(`/project/edit/${props.row.id}`)">
          <div v-if="props.row.rule">
            {{ props.row.rule.name }}
          </div>
          <div v-else>
            -
          </div>
        </div>
      </div> -->

    </v-server-table>

    <div>
      <jLoadingTable v-if="!load_first"/>
    </div>

    <jDeleteProject
      ref="floating"
      :selected-rows="selected_rows"
      @finish="projectsDeleted" />

  </div>
</template>

<script>
import { jMixPaginationProject } from '@/mixins/paginations/project/jMixPaginationProject';
import jLoadingTable from '@/components/skeleton-loading/jLoadingTableV1';
import jDeleteProject from '@/views/project/delete-project/jDeleteProject';

export default {
  components: {
    jLoadingTable,
    jDeleteProject,
  },
  mixins: [
    jMixPaginationProject,
  ],
};
</script>



