/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';

export const jMixModelDeleteCategorys = {
  computed: {
    delete_data() {
      if (this.selectedRows.length > 0) {
        return this.selectedRows.map(data => ({
          categories: [{
            id: data.id,
          }],
        }));
      }
      return null;
    },
  },

  methods: {
    deleteCategorys() {
      if (this.delete_data !== null) {
        const text = `want to delete ${this.category_name}`;

        this.$swal({
          title: 'Are you sure',
          text,
          type: 'question',
          showCancelButton: true,
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes',
        }).then((result) => {
          if (result.value) {
            this.$refs.deleteCategory.toggleIsVisible();
          }
        });
      }
    },
  },

};

export const jMixModelDeleteCategory = {
  computed: {
    ...mapState('jStoreCategory', ['detail_category']),

    delete_data() {
      if (this.detail_category !== null) {
        return {
          categories: [{
            id: Number(this.$route.params.id),
          }],
        };
      }
      return null;
    },
  },

  methods: {
    ...mapActions('jStoreCategory', ['deleteCategory']),

    deleteData(val) {
      this.$swal({
        title: 'Are you sure',
        text: 'to delete this category',
        type: 'warning',
        showCancelButton: true,
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes',
      }).then((result) => {
        if (result.value) {
          this.deleteCategory(this.delete_data);
        }
      });
    },
  },

};



