import jConfigNumberFormatList from '@/views/number-format/jNumberFormat';
import jConfigNumberFormatCreate from '@/views/number-format/create/jCreateNumberFormat';
import jConfigNumberFormatEdit from '@/views/number-format/edit/jEditNumberFormat';

export default [
  {
    path: '/number-format',
    name: 'jConfigNumberFormatList',
    component: jConfigNumberFormatList,
    meta: {
      title: 'Number Format',
      layout: 'jWrapper',
    },
  },
  {
    path: '/number-format/create',
    name: 'jConfigNumberFormatCreate',
    component: jConfigNumberFormatCreate,
    meta: {
      title: 'Create Number Format',
      layout: 'jWrapper',
    },
  },
  {
    path: '/number-format/edit/:id',
    name: 'jConfigNumberFormatEdit',
    component: jConfigNumberFormatEdit,
    meta: {
      title: 'Edit Number Format',
      layout: 'jWrapper',
    },
  },
];



