<template>
  <div class="space-bottom">
    <div class="transblack-background"/>
    <jFloatingPrompt
      ref="titleFloating"
      class="p__b--100">
      <b-row class="jobTitleFloat">
        <b-col>
          <h3>Create New Job Position</h3>
          <!-- eslint-disable-next-line -->
          <b-form-group :class="{'error-input' : $v.v_add_title.name.$error || !$v.v_add_title.name.minLength }">
            <label>Name*</label>
            <j-input
              v-model="v_add_title.name"
              :max-length="50"
              :error-message="err_message"
              placeholder="Job Title Name"/>
          </b-form-group>
          <b-form-group :class="{'error-input' : $v.v_add_title.email.$error}">
            <label>Email</label>
            <j-single-select
              v-model="v_add_title.email"
              :is-async="true"
              :load-options="loadOptions"
              value-format="object"
              class="v-singleselect"
              placeholder="Email"
              label="email"/>
          </b-form-group>
        </b-col>
        <b-col cols="6">
          <b-form-group :class="{'error-input' : $v.v_add_title.code.$error}">
            <label>Position ID*</label>
            <j-input
              v-model="v_add_title.code"
              :max-length="42"
              placeholder="Position ID"/>
          </b-form-group>
          <b-form-group
            :class="{'error-input' : $v.v_add_title.divisions.$error}"
            class="margin-division--">
            <label>Divisions</label>
            <j-multi-select
              :options="divisions_list"
              v-model="v_add_title.divisions"
              value-format="object"
              class="v-singleselect multi-height-auto"
              placeholder="Select Divisions"
              label="name"/>
          </b-form-group>
        </b-col>

        <b-col cols="2">
          <!-- button create -->
          <div class="wrap-button-flow">
            <b-button
              class="btn blue"
              @click="createDivision">
              CREATE
            </b-button>
            <i
              class="fa fa-times"
              @click="$refs.titleFloating.hide('/organigram')"/>
          </div>
        </b-col>
      </b-row>
    </jFloatingprompt>
  </div>

</template>

<script>
import jFloatingPrompt from '@/components/new-floating-templete/jFloatingPrompt';
import { mapActions, mapState } from 'vuex';
import { required, minLength } from 'vuelidate/lib/validators';
// eslint-disable-next-line
import { ASYNC_SEARCH } from '@riophae/vue-treeselect';
import vSelect from 'vue-select';
import apiUser from '@/services/jApiUser';

export default {
  components: { jFloatingPrompt, vSelect },
  data() {
    return {
      v_add_title: {
        name: '',
        email: null,
        divisions: null,
        code: '',
      },
    };
  },
  validations: {
    v_add_title: {
      name: { required, minLength: minLength(3) },
      email: { },
      divisions: { },
      code: { required },
    },
  },
  computed: {
    ...mapState('jFlows', ['divisions_list', 'users_list']),
    err_message() {
      if (!this.$v.v_add_title.name.required) {
        return 'Field is required';
      } else if (!this.$v.v_add_title.name.minLength) {
        return 'Minimum 3 characters';
      }
      return 'Field is required';
    },
    save_title() {
      let divisions = null;
      if (this.v_add_title.divisions !== null) {
        divisions = this.v_add_title.divisions.map(val => val.id);
      }
      return {
        user_company_id: this.v_add_title.email ? this.v_add_title.email.id : '',
        name: this.v_add_title.name,
        description: '',
        code: this.v_add_title.code,
        divisions: divisions !== null ? divisions : [],
      };
    },
  },
  watch: {
    // eslint-disable-next-line
    'v_add_title.code': function (val) {
      const upperCase = val.toUpperCase();
      this.v_add_title.code = upperCase;
    },
  },
  created() {
    this.getListDivisions();
  },
  methods: {
    ...mapActions('jOrganigram', ['createNewDivision', 'createNewTitle']),
    ...mapActions('jFlows', ['getListDivisions']),
    createDivision() {
      this.$v.v_add_title.$touch();
      if (!this.$v.v_add_title.$error) {
        this.$refs.titleFloating.hide('/organigram');
        setTimeout(() => {
          this.createNewTitle(this.save_title);
        }, 250);
      }
    },

    loadOptions({ action, searchQuery, callback }) {
      if (action === ASYNC_SEARCH) {
        const reqData = {
          pagination: {
            limit: 50,
            page: 1,
            column: '',
            ascending: true,
            query: searchQuery,
            query_type: 'email',
          },
        };
        this.getListUsers(reqData, callback);
      }
    },

    async getListUsers(req, callback) {
      let apiUserData;

      if (req.pagination.query.length >= 3) {
        apiUserData = await apiUser.listUser(req);
      } else {
        apiUserData = [];
      }

      if (typeof apiUserData !== 'undefined') {
        apiUserData.forEach((lab, index) => {
          apiUserData[index].label = lab.email;
        });
      }
      callback(null, apiUserData);
      return apiUserData;
    },

    // exitFloating() {
    //   const elem = document.getElementsByClassName('selected-bottom-rules');
    //   elem[0].classList.remove('show-floating');
    //   elem[0].classList.add('hide-floating');
    //   setTimeout(() => {
    //     elem[0].classList.remove('selected-bottom-rules');
    //     this.$router.replace('/organigram');
    //   }, 200);
    // },
  },
};
</script>

<style lang="scss">
.margin-division-- {
  margin-top: 0px !important;
}
</style>



