postmanpsotman<template>
  <div class="modal-floating">
    <b-modal
      ref="floatingSure"
      class="mod-form"
      centered
      no-close-on-backdrop
      no-close-on-esc>

      <!-- close button modal -->
      <div
        class="close-modal-flow"
        @click="hideFloating">
        <i class="fa fa-times"/>
      </div>

      <!-- title modal -->
      <div
        class="title-flow">
        <i class="fa fa-square"/>

        <slot />
      </div>

      <h3>Are you sure you want to delete "{{ data }}"?</h3>

      <div class="wrap-button-flow">
        <b-button
          class="btn blue outline"
          @click="sure">SURE</b-button>
        <b-button
          class="btn blue"
          @click="hideFloating">CANCEL</b-button>
      </div>

    </b-modal>
  </div>
</template>

<script>
export default {
  props: {
    data: {
      type: String,
      default: '',
    },
  },
  methods: {
    hideFloating() {
      this.$refs.floatingSure.hide();
    },
    showFloating() {
      this.$refs.floatingSure.show();
    },
    sure() {
      this.hideFloating();
      this.$emit('sure');
    },
  },
};
</script>

<style lang="scss" scoped>
.modal-floating {
  color: #42a5ce;
  cursor: pointer;
  position: absolute;
  right: 20px;
  top: 16px;
  .wrap-button-flow {
    position: relative;
    .btn {
      margin-left: 20px;
    }
  }
  .close-modal-flow {
    color: #42a5ce;
    cursor: pointer;
    font-size: 25px;
    position: absolute;
    right: 20px;
    top: 16px;
  }
  .title-flow {
    color: black;
    margin-bottom: 10px;
  }
  .modal-body {
    padding-bottom: 0px;
  }
}
h3 {
  text-align: center;
}
.mod-form .modal-body {
  padding-bottom: 0px;
}
</style>



