/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';

export const jMixModeEditProject = {
  data() {
    return {
      filter: 'name',
      option_filter: [
        { text: 'Position', value: 'name' },
        { text: 'Name', value: 'name_user' },
        { text: 'Email', value: 'email' },
      ],
    };
  },
  methods: {
    ...mapActions('jStoreProject', ['getListPositions', 'getListRules', 'detailProject', 'getListPeriods', 'getListDivision']),

    modeEdit() {
      const getAll = {
        pagination: {
          limit: 0,
          page: 0,
          column: 'id',
          ascending: false,
          query: '',
        },
        costcenter:{
          root_id: 0,
        },
        type: 2,
      };

      // get list position
      this.getListPositions(getAll);

      // get list rule
      this.getListRules(getAll);

      // get list period
      this.getListPeriods(getAll);

      // get list division
      this.getListDivision(getAll);

      // get detail Project
      if (this.temporary_data_project.valid !== true) {
        this.detailProject({ id: Number(this.$route.params.id) });
      }

      // temporary data
      if (this.temporary_data_project.valid === true) {
        this.project = this.temporary_data_project.data_project;
      }
    },

    convertOrganigramsEditDetail(val) {
      if (Array.isArray(val)) {
        const converData = val.map((data) => {
          const newData = {
            id: data.organigram_id,
            relation_id: data.id,
            project_id: data.project_id,
          };
          return newData;
        });
        return converData;
      }
      return [];
    },
  },
  computed: {
    ...mapState('jStoreProject', ['project_data', 'temporary_data_project']),
    default_organigrams() {
      if (this.project_data.detail_project !== null) {
        return this.convertOrganigramsEditDetail(this.project_data.detail_project.organigrams);
      }
      return [];
    },
    placeholder() {
      if (this.filter === 'code') {
        return 'Position';
      } else if (this.filter === 'name') {
        return 'Name';
      } else if (this.filter === 'email') {
        return 'Email';
      }
      return '';
    },
  },
  watch: {
    // eslint-disable-next-line
    'project_data.detail_project': function(newVal) {
      const detailProject = {
        id: newVal.id,
        name: newVal.name,
        code: newVal.code,
        period: newVal.period_id ? { id: newVal.period_id } : null,
        description: newVal.description ? newVal.description : null,
        // eslint-disable-next-line
        organigrams: newVal.organigrams ? this.convertOrganigramsEditDetail(newVal.organigrams) : [],
        division: newVal.division_id && newVal.division_id !== 0 ?
          { id: newVal.division_id } : null,
      };
      this.project = detailProject;
    },
  },
};



