<template>
  <div>
    <j-title-header :options="title"/>
    <div class="float-to-left">
      <j-import-file
        :href-sample="require('@/assets/file/numberformat.zip')"
        class="import-style"
        @import-data="importData"/>&nbsp;
      <div class="cc-btn">
        <b-button
          v-if="is_error"
          class="btn outline red"
          @click="$refs.errorTrans.show()">
          <i class="fa fa-close"/>
          Open Error Data
        </b-button>
        <b-button
          v-else-if="data_import && !is_error"
          class="btn green outline"
          @click="uploadData">
          Setting by CSV <i class="fa fa-angle-down"/>
        </b-button>&nbsp;
      </div>
      <b-button
        class="btn green"
        @click="downloadReport">EXPORT</b-button>&nbsp;
      <b-button
        class="btn blue"
        @click="$router.replace('/number-format/create')">
        Create Number Format
      </b-button>
    </div>
    <j-number-format-list/>
    <j-progress
      ref="ImportFile"
      :data-kirim="data_import"
      :url-api="url"
      :headers="headers"
      @finish="reloadPage"/>

    <div>
      <b-modal
        ref="errorTrans"
        class="mod-form"
        centered>
        <h3 class="float-to-center c--red">
          Error Data
        </h3>
        <v-client-table
          v-if="error_data[0]"
          :data="error_data"
          :columns="columns"
          :options="options"
          class="jTable">

          <div
            slot="ERROR"
            slot-scope="props">
            <div
              v-for="(err, index) in props.row.ERROR"
              :key="index">
              {{ err }}
            </div>
          </div>

        </v-client-table>
        <download-excel
          :data="error_data"
          class="float-to-right m__t--30"
          name="Transportation-data-error.xls">
          <b-button class="btn green">
            Export Error
          </b-button>
        </download-excel>
      </b-modal>
    </div>
  </div>
</template>

<script>
import jApi from '@/services/jApi';
import jProgress from '@/components/progress-screen/jProgress';
import jNumberFormatList from '@/views/number-format/list/jNumberFormatList';
import jMixNumberFormat from '@/mixins/components/get-list-option-form/jMixNumberFormat';

export default {
  components: {
    jProgress,
    jNumberFormatList,
  },
  mixins: [
    jMixNumberFormat,
  ],
  data() {
    return {
      title: {
        icon: 'fa-user',
        main_title: 'NUMBER FORMAT',
      },
      columns: ['ID', 'NAME', 'NUMBER_FORMAT', 'TYPE', 'ERROR'],
      options: {
        headings: {
          name: 'NAME',
          number_format: 'NUMBER_FORMAT',
          type: 'TYPE',
        },
        filterable: false,
        perPage: 5,
        perPageValues: [5, 10, 20, 50, 100],
      },
      data_import: null,
      list_error: [],
      error_data: [],
      is_error: false,
      is_finish: false,
      data_pattern: '',
    };
  },
  computed: {
    url() {
      const getEnv = `${process.env.GATE_URL}numbering/create`;
      return getEnv;
    },
    headers() {
      const token = localStorage.getItem('refreshToken');
      const headers = {
        Authorization: `Bearer ${token}`,
      };
      return headers;
    },
  },
  methods: {
    downloadReport() {
      this.createExport({
        type: 'xls',
      });
    },

    async createExport(data) {
      const api = jApi.generateApi();
      await api.post('numbering/export', data)
        .then((response) => {
          const responses = response;
          const fileUrl = responses.data.data.url;
          window.open(fileUrl, '_blank');
        });
    },

    importData(val) {
      this.is_error = false;
      this.list_error = [];
      this.error_data = val;

      const fileData = val;
      const genData = [];

      fileData.forEach((e, index) => {
        const ccData = this.convertData(e, index);
        if (ccData !== false) {
          genData.push(
            ccData,
          );
        }
      });
      this.data_import = genData;
    },

    convertData(val, indexData) {
      this.list_error[indexData] = [];
      this.error_data[indexData].ERROR = [];

      const packing = {
        id: 0,
        name: null,
        pattern: null,
        type: null,
        is_default: false,
      };

      if (val.NAME.length > 0) {
        this.$set(packing, 'name', val.NAME.toLowerCase().trim());
      } else {
        this.is_error = true;
        this.list_error[indexData].push(`- ${val.NAME} name is required`);
        this.error_data[indexData].ERROR.push(`- ${val.NAME} name is required`);
      }

      // eslint-disable-next-line
      const filType = this.list_type_form.filter(fildat => fildat.name.toLowerCase() === val.TYPE.toLowerCase().trim());
      if (filType.length < 1) {
        this.is_error = true;
        this.list_error[indexData].push(`- ${val.TYPE} type is not listed, please try another`);
        this.error_data[indexData].ERROR.push(`- ${val.TYPE} type is not listed, please try another`);
      } else if (filType.length > 0) {
        this.$set(packing, 'type', filType[0].id);
      }

      if (val.IS_DEFAULT.toLowerCase().trim() === 'yes') {
        this.$set(packing, 'is_default', true);
      } else if (val.IS_DEFAULT.toLowerCase().trim() === 'no') {
        this.$set(packing, 'is_default', false);
      } else {
        this.is_error = true;
        this.list_error[indexData].push(`- ${val.IS_DEFAULT} is default is not listed, please try another`);
        this.error_data[indexData].ERROR.push(`- ${val.IS_DEFAULT} is default is not listed, please try another`);
      }

      if (val.NUMBER_FORMAT.length > 0) {
        this.data_pattern = '';
        // eslint-disable-next-line
        var arrPattern = val.NUMBER_FORMAT.split('|');

        if (arrPattern.length > 0) {
        // eslint-disable-next-line
        for (var i in arrPattern) {
            const code = arrPattern[i];

            if (code === '.') {
              this.data_pattern = this.data_pattern.concat('.,');
            } else if (code === '/') {
              this.data_pattern = this.data_pattern.concat('/,');
            } else if (code === '-') {
              this.data_pattern = this.data_pattern.concat('-,');
            } else if (code === '29') {
              this.data_pattern = this.data_pattern.concat('date,');
            } else if (code === '12') {
              this.data_pattern = this.data_pattern.concat('month01,');
            } else if (code === 'DEC') {
              this.data_pattern = this.data_pattern.concat('month02,');
            } else if (code === '17') {
              this.data_pattern = this.data_pattern.concat('year01,');
            } else if (code === '2017') {
              this.data_pattern = this.data_pattern.concat('year02,');
            } else if (code === '003') {
              this.data_pattern = this.data_pattern.concat('id,');
            } else if (code.substr(0, 1) === '[' && code.substr(code.length - 1, 1) === ']') {
              // eslint-disable-next-line
              this.data_pattern = this.data_pattern.concat('[' + code.substr(1, code.length - 2) + '],');
            } else {
              this.is_error = true;
              this.list_error[indexData].push(`- ${val.NUMBER_FORMAT} wrong format`);
              this.error_data[indexData].ERROR.push(`- ${val.NUMBER_FORMAT} wrong format`);
            }
          }
        }

        this.$set(packing, 'pattern', this.data_pattern.replace(/,/g, '|'));
      } else {
        this.is_error = true;
        this.list_error[indexData].push(`- ${val.NUMBER_FORMAT} number format is required`);
        this.error_data[indexData].ERROR.push(`- ${val.NUMBER_FORMAT} number format is required`);
      }

      return packing;
    },

    reloadPage() {
      this.is_finish = true;
      this.$router.go();
    },

    uploadData() {
      this.$refs.ImportFile.toggleIsVisible();
    },
  },
};
</script>



