/* eslint-disable no-shadow,consistent-return,no-param-reassign */
// import router from '@/router';
import apiFeature from '@/services/jApiFeature';

const state = {
  list_feature: null,
  update_res: false,
  jojo_sales: {
    list_company_setting: null,
    update_res: false,
  },
};

const getters = {
  // Change value of state(s) with getter

};

const mutations = {
  showListFeature(state, data) {
    state.list_feature = data.data;
  },

  updateFeatureRes(state) {
    state.update_res = !state.update_res;
  },

  showListFeatureSales(state, data) {
    state.jojo_sales.list_company_setting = data.data;
  },

  updateFeatureSalesRes(state) {
    state.jojo_sales.update_res = !state.update_res;
  },
};

const actions = {
  // get list feature
  async getListFeature({ commit, dispatch }, req) {
    dispatch('jNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const listFeature = await apiFeature.listFeature(req);
      commit('showListFeature', listFeature);
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
    } catch (err) {
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },

  async updateFeature({ commit, dispatch }, req) {
    dispatch('jNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      await apiFeature.updateFeature(req);
      commit('updateFeatureRes');
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      dispatch('jNotificationScreen/toggleSuccess', 'Setting Updated', { root: true });
    } catch (err) {
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },

  async getListFeatureSales({ commit, dispatch }) {
    dispatch('jNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const listFeature = await apiFeature.listFeatureSales();
      commit('showListFeatureSales', listFeature);
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
    } catch (err) {
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },

  async updateFeatureSales({ commit, dispatch }, req) {
    dispatch('jNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      await apiFeature.updateFeatureSales(req);
      commit('updateFeatureSalesRes');
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      dispatch('jNotificationScreen/toggleSuccess', 'Setting Updated', { root: true });
    } catch (err) {
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};



