/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapState, mapActions } from 'vuex';

export const jMixModeViewChart = {
  data() {
    return {
      send_data: {
        division_id: 0,
        top_id: 0,
        bottom_id: 0,
        top_db_id: 0,
        bottom_db_id: 0,
        last_update: 0,
        count: 3,
      },
    };
  },
  computed: {
    ...mapState('jOrganigram', ['division_data']),
  },
  created() {
    this.getOrganigramChart(this.send_data);
  },
  methods: {
    ...mapActions('jOrganigram', ['getOrganigramChart']),
    async getChild(val) {
      const a = val;
      const sendData = {
        data_req: {
          division_id: a.id,
          top_id: 0,
          bottom_id: 0,
          top_db_id: 0,
          bottom_db_id: 0,
          last_update: 0,
          count: 3,
        },
        parent: a,
      };

      if (a.children[0].child) {
        try {
          this.getOrganigramChart(sendData);
          this.$root.$emit('toggleOpen', val);
        } catch (error) {
          throw error;
        }
      } else {
        this.$root.$emit('toggleOpen', val);
      }
    },

    async showMore(val) {
      const prnt = val;
      const chld = val.children;
      const sendData = {
        data_req: {
          division_id: prnt.id,
          top_id: 0,
          bottom_id: chld[chld.length - 2].id,
          top_db_id: chld[0].id,
          bottom_db_id: chld[chld.length - 2].id,
          last_update: 0,
          count: this.send_data.count,
        },
        parent: prnt,
        children: chld,
      };

      try {
        await this.getOrganigramChart(sendData);
      } catch (error) {
        throw error;
      }
    },
  },
};



