<template>
  <div>
    <div v-if="list.chart === 'BAR'">
      <ChartBar :data="list.viewChart"/>
    </div>
    <div v-else-if="list.chart === 'PIE'">
      <ChartPie :data="list.viewChart"/>
    </div>
    <div v-else-if="list.chart === 'LINE'">
      <ChartLine :data="list.viewChart"/>
    </div>
    <div v-else-if="list.chart === 'DOUGHNUT'">
      <ChartDoughnut :data="list.viewChart"/>
    </div>
    <div v-else>
      <ChartBar :data="list.viewChart"/>
    </div>
  </div>
</template>

<script>
import ChartBar from '@/components/chart/ChartBar';
import ChartPie from '@/components/chart/ChartPie';
import ChartLine from '@/components/chart/ChartLine';
import ChartDoughnut from '@/components/chart/ChartDoughnut';
import ChartBubble from '@/components/chart/ChartBubble';

export default {
  components: {
    ChartBar,
    ChartPie,
    ChartLine,
    ChartDoughnut,
    ChartBubble,
  },
  props: {
    list: {
      type: Object,
      default: () => {},
    },
  },
};
</script>

<style lang="css">
</style>



