<template>
  <div>
    <j-title-header :options="title"/>
    <j-tabs :tabs="tabs">
      <div
        id="containertabsActivityType"
        class="tab-content">
        <div
          v-for="i in tabs"
          :key="i.id"
          :id="i.idComp"
          :aria-labelledby="i.idTab"
          role="tabpanel"
          class="tab-pane" >
          <component :is="i.comp.name"/>
        </div>
      </div>
    </j-tabs>
    <router-view/>
  </div>
</template>

<script>
import jSales from '@/views/company/user-restriction/sales/jSales';


export default {
  // eslint-disable-next-line
  name: 'jGlobalUserRestriction',
  components: {
    jSales,
  },
  data() {
    return {
      title: {
        icon: 'fa-user',
        main_title: 'USER RESTRICTION',
        opt_title: 'Administrator',
      },
      tabs: [
        {
          idTab: 'tab-sales', title: 'JOJOSALES', idComp: 'comp-sales', comp: jSales, isShow: true,
        },
      ],
    };
  },
};
</script>



