<template>

  <jFloatingPrompt
    ref="titleFloating"
    class="space-bottom-more">


    <b-row class="jobTitleFloat">
      <b-col>
        <b-form-group :class="{'error-input' : $v.title.name.$error || !$v.title.name.minLength}">
          <label>Name*</label>
          <j-input
            v-model="title.name"
            :error-message="err_message"
            :max-length="50"
            placeholder="Job Position Name"/>
        </b-form-group>
        <b-form-group>
          <label>Email</label>
          <j-single-select
            v-if="is_changed"
            v-model="title.user"
            :is-async="true"
            :load-options="loadOptions"
            :default-options="default_options"
            value-format="object"
            placeholder="Email"
            label="email"/>
        </b-form-group>
      </b-col>
      <b-col>

        <b-form-group :class="{'error-input' : $v.title.code.$error}">
          <label>Position ID*</label>
          <j-input
            v-model="title.code"
            :max-length="42"
            placeholder="Position ID"/>
        </b-form-group>

        <b-form-group>
          <label>Division(s)</label>
          <j-multi-select
            :options="divisions_list"
            v-model="title.divisions"
            value-format="object"
            placeholder="Select Divisions"
            label="name"/>
        </b-form-group>
      </b-col>
      <b-col cols="4">
        <!-- button UPDATE or DELETE -->
        <div class="wrap-button-flow">
          <b-button
            class="btn blue"
            @click="upTitle">
            UPDATE
          </b-button>
          <b-button
            class="btn blue outline"
            @click="deleteTitle">
            DELETE
          </b-button>
          <i
            class="fa fa-times"
            @click="hideTitleFloating"/>
        </div>
      </b-col>
    </b-row>

    <!-- <hr>

    Temporary Position

    <br>
    <br>
    <b-row class="jobTitleFloat">
      <b-col>
        <b-form-group>
          <label>Email</label>
          <v-select
            :options="users_list"

            max-height="100px"
            class="v-singleselect"
            placeholder="Email"
            label="email"/>
        </b-form-group>
      </b-col>
      <b-col>
        <b-form-group>
          <label>Expire Date</label>
          <j-datepicker

            placeholder="Job Position Name"/>
        </b-form-group>
      </b-col> -->

  </jFloatingprompt>

</template>

<script>
import jFloatingPrompt from '@/components/new-floating-templete/jFloatingPrompt';
import { mapActions, mapState } from 'vuex';
import vSelect from 'vue-select';
import { required, minLength } from 'vuelidate/lib/validators';
// eslint-disable-next-line
import { ASYNC_SEARCH } from '@riophae/vue-treeselect';
import apiUser from '@/services/jApiUser';

export default {
  components: { jFloatingPrompt, vSelect },
  props: {
    title: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      is_changed: true,
    };
  },
  validations: {
    title: {
      name: { required, minLength: minLength(3) },
      user: { },
      divisions: { },
      code: { required },
    },
  },
  computed: {
    ...mapState('jFlows', ['divisions_list', 'users_list']),

    default_options() {
      if (this.title !== null) {
        if (typeof this.title.user === 'object' && this.title.user !== null) {
          return [this.title.user];
        }
        return false;
      }
      return false;
    },

    err_message() {
      if (!this.$v.title.name.required) {
        return 'Field is required';
      } else if (!this.$v.title.name.minLength) {
        return 'Minimum 3 characters';
      }
      return 'Field is required';
    },

    delete_title() {
      return {
        id: this.title.id,
      };
    },
    update_title() {
      const user = this.title.user ? this.title.user.id : null;
      const divisions = this.title.divisions.map(datadiv => datadiv.id);
      const data = {
        id: this.title.id,
        name: this.title.name,
        code: this.title.code,
        description: this.title.description,
        user_company_id: user,
        divisions,
      };
      return data;
    },
  },

  watch: {
    default_options() {
      this.is_changed = !this.is_changed;
      setTimeout(() => {
        this.is_changed = !this.is_changed;
      }, 100);
    },
    title() {
      this.is_changed = !this.is_changed;
      setTimeout(() => {
        this.is_changed = !this.is_changed;
      }, 100);
    },
  },

  created() {
    this.getListDivisions();
  },

  methods: {
    ...mapActions('jFlows', ['getListDivisions']),
    ...mapActions('jOrganigram', ['removeTitle', 'updateTitle']),

    isUserEmpty(user) {
      if (user.id) {
        return user.id;
      }
      return null;
    },

    deleteTitle() {
      const text = `You want to delete "${this.title.name}"`;

      this.$swal({
        title: 'Are you sure?',
        text,
        type: 'question',
        showCancelButton: true,
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes',
      }).then((result) => {
        if (result.value) {
          this.removeTitle(this.delete_title);
          this.$emit('deleteTitle');
        }
      });
    },
    hideTitleFloating() {
      this.$refs.titleFloating.hide();
      this.$emit('hideFloatingPrompt');
    },
    upTitle() {
      this.$v.title.$touch();
      if (!this.$v.title.$error) {
        const text = `You want to update "${this.title.name}"`;

        this.$swal({
          title: 'Are you sure?',
          text,
          type: 'question',
          showCancelButton: true,
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes',
        }).then((result) => {
          if (result.value) {
            this.updateTitle(this.update_title);
          }
        });
      }
      // alert('asdasd')
      // this.$emit('updateTitle', this.update_title);
      // this.updateTitle(this.update_title);
    },

    loadOptions({ action, searchQuery, callback }) {
      if (action === ASYNC_SEARCH) {
        const reqData = {
          pagination: {
            limit: 50,
            page: 1,
            column: '',
            ascending: true,
            query: searchQuery,
            query_type: 'email',
          },
        };
        this.getListUsers(reqData, callback);
      }
    },

    async getListUsers(req, callback) {
      let apiUserData;

      if (req.pagination.query.length >= 3) {
        apiUserData = await apiUser.listUser(req);
      } else {
        apiUserData = [];
      }

      if (typeof apiUserData !== 'undefined') {
        apiUserData.forEach((lab, index) => {
          apiUserData[index].label = lab.email;
        });
      }
      callback(null, apiUserData);
      return apiUserData;
    },
  },
};
</script>

<style lang="scss">

</style>



