import jIndexOR from '@/views/organigram/index';
import jNewDivision from '@/views/organigram/division/jNewDivision';
import jNewTitle from '@/views/organigram/title/jNewTitle';
import jEditDivision from '@/views/organigram/division/jEditDivision';
import jDivisionChart from '@/views/organigram/division/chart/jOrganigramChart';
import auth from '@/middleware/auth';
import admin from '@/middleware/admin';

const organigrams = [
  {
    path: '/organigram',
    name: 'Organigram',
    component: jIndexOR,
    meta: {
      title: 'Organigram',
      layout: 'jWrapper',
    },
    children: [
      {
        path: 'division/new',
        component: jNewDivision,
      },
      {
        path: 'title/new',
        component: jNewTitle,
      },
      {
        path: 'division/edit/:idDiv',
        component: jEditDivision,
      },
    ],
  },
  {
    path: '/organigram/chart',
    name: 'Organigram Chart',
    component: jDivisionChart,
    meta: {
      title: 'jDivisionChart',
      layout: 'jWrapper',
    },
  },
];

organigrams.map((dataPath) => {
  const eachData = dataPath;
  eachData.meta.middleware = [auth, admin];
  return eachData;
});


export default organigrams;



