/* eslint-disable no-shadow,no-param-reassign */
/**
 * Created by Arga Ghulam Ahmad - arga@jojonomic.com
 * Last update on 9 August 2018
 *
 * Modules hold floating prompt information.
 * These state, getters, mutations, and actions accessed by jTable, jTableUser, and jFloatingPrompt.
 */

/**
 * checkedId: check id
 * dataContainer: arrays hold table data
 * result: array contains result information
 * currentTable: string represent selected table
 *
 * @type {{checkedId: Array, dataContainer: Array, result: Array, currentTable: string}}
 */
const state = {
  checkedId: [],
  dataContainer: [],
  result: [],
  currentTable: 'jTable',
};

const getters = {
  getCheckedId: state => state.checkedId,
  getDataContainer: state => state.dataContainer,
  getResult: state => state.result,
  getCurrentTable: state => state.currentTable,
};

const mutations = {
  setCheckedId: (state, payload) => {
    state.checkedId = payload;
  },
  setDataContainer: (state, payload) => {
    state.dataContainer = payload;
  },
  setResult: (state, payload) => {
    state.result = payload;
  },
  setCurrentTable: (state, payload) => {
    state.currentTable = payload;
  },
};

const actions = {
  actionCheckedId: ({ commit }, payload) => {
    commit('setCheckedId', payload);
  },
  actionDataContainer: ({ commit }, payload) => {
    commit('setDataContainer', payload);
  },
  actionResult: ({ commit }, payload) => {
    commit('setResult', payload);
  },
  actionCurrentTable: ({ commit }, payload) => {
    commit('setCurrentTable', payload);
  },
};

export default {
  state,
  mutations,
  actions,
  getters,
};



