/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions } from 'vuex';

export const jMixModelEditCategory = {
  computed: {
    generateData() {
      if (this.category_form !== null) {
        const genData = {
          category: {
            id: this.category_form.id,
            name: this.category_form.name ? this.category_form.name : '',
            code: this.category_form.code ? this.category_form.code : '',
            description: this.category_form.description ? this.category_form.description : '',
            icon: this.category_form.icon.id ? this.category_form.icon.id : '',
            package: this.category_form.type.id,
            layer_id: this.category_form.layer ? this.category_form.layer.id : '',
            parent_category_id: this.category_form.parent ? this.category_form.parent.id : '',
          },
        };
        return genData;
      }
      return null;
    },
  },
  methods: {
    ...mapActions('jStoreCategory', ['updateCategory']),

    sendData() {
      this.updateCategory(this.generateData);
    },
  },
};



