/* eslint-disable no-shadow,consistent-return,no-param-reassign */
const _ = require('lodash');

const state = {
  // Set default state(s) here
  options1: [],
  options2: [],
  isDefaultData1: [],
  isDefaultData2: [],
};

const getters = {
  // Change value of state(s) with getter
  getOptions1(state) {
    if (state.options1) {
      return state.options1.map(i => ({
        id: i.id, title: i.title, icon: i.icon, isDeleted: i.isDeleted,
      }));
    }
    return null;
  },
  getOptions2(state) {
    if (state.options2) {
      return state.options2.map(i => ({
        id: i.id, title: i.title, icon: i.icon, isDeleted: i.isDeleted,
      }));
    }
  },
  getIsDefaultData1(state) {
    return state.isDefaultData1;
  },
  getIsDefaultData2(state) {
    return state.isDefaultData2;
  },

};

const mutations = {
  // Mutating the value of state(s) with event mutation
  mutateOptions1(state, newOptions1) {
    state.options1 = _.cloneDeep(newOptions1);
  },
  mutateOptions2(state, newOptions2) {
    state.options2 = _.cloneDeep(newOptions2);
  },
  mutateDefault1(state, newD) {
    state.isDefaultData1 = _.cloneDeep(newD);
  },
  mutateDefault2(state, newD) {
    state.isDefaultData2 = _.cloneDeep(newD);
  },


};

const actions = {
  actionOptions1: ({ commit }, payload) => {
    commit('mutateOptions1', payload);
  },
  actionOptions2: ({ commit }, payload) => {
    commit('mutateOptions2', payload);
  },
  actionDefault1: ({ commit }, payload) => {
    commit('mutateDefault1', payload);
  },
  actionDefault2: ({ commit }, payload) => {
    commit('mutateDefault2', payload);
  },

};
export default {
  state,
  getters,
  mutations,
  actions,
};



