/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions } from 'vuex';

export const jMixModelEditProject = {
  computed: {
    generateData() {
      if (this.project !== null) {
        // sort position by
        const genData = {
          id: this.project.id,
          name: this.project.name,
          code: this.project.code,
          period_id: this.project.period ? this.project.period.id : '',
          description: this.project.description ? this.project.description : '',
          // eslint-disable-next-line
          organigrams: this.project.organigrams_edit && this.project.organigrams_edit.length > 0 ? this.convertOrganigramsEdit(this.project.organigrams_edit, this.project.id) : this.convertOrganigramsEdit(this.project.organigrams, this.project.id),
          rule_id: this.project.rule ? this.project.rule.id : '',
          division_id: this.project.division ? this.project.division.id : null,
          company_users: [],
        };
        return genData;
      }
      return null;
    },
  },
  methods: {
    ...mapActions('jStoreProject', ['updateProject']),

    sendData() {
      this.updateProject(this.generateData);
    },

    convertOrganigramsEdit(val, projectId) {
      const converData = val.map((data) => {
        const newData = {
          id: data.relation_id ? data.relation_id : 0,
          organigram_id: data.id ? data.id : 0,
          project_id: projectId,
          is_delete: data.is_delete ? data.is_delete : false,
        };
        return newData;
      });
      return converData;
    },
  },
};



