/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapState, mapActions } from 'vuex';
import apiCategory from '@/services/jApiCategory';
import jReusableFunction from '@/mixins/paginations/jReusableFunction';


export const jMixPaginationCategory = {
  data() {
    return {
      filter_data: '',
      select_type: 'name',
      options_search: [
        {
          text: 'Name',
          value: 'name',
        },
        {
          text: 'Description',
          value: 'description',
        },
      ],
      data_table: [],
      selected_rows: [],
      load_first: false,
      columns: ['checkbox', 'icon', 'name', 'description'],
      options: {
        headings: {
          icon: 'ICON',
          name: 'NAME',
          description: 'DESCRIPTION',
          checkbox(h) {
            const objThis = this;
            return jReusableFunction.checkbox(h, objThis);
          },
        },
        texts: {
          filterPlaceholder: 'Search by name',
          limit: 'Rows per page',
        },
        sortIcon: {
          base: 'fa',
          up: 'fa-chevron-up',
          down: 'fa-chevron-down',
          is: 'fa-sort',
        },
        sortable: ['icon', 'name', 'description'],
        debounce: 1000,
        filterable: true,
        perPage: 5,
        perPageValues: [5, 10, 20, 50, 100],
        requestAdapter(data) {
          return {
            pagination: {
              limit: data.limit,
              page: data.page,
              column: data.orderBy ? data.orderBy : '',
              ascending: !data.ascending,
              query: data.query ? data.query : '',
              query_type: data.query_type ? data.query_type : '',
            },
          };
        },
        requestFunction(data) {
          const getListCategory = apiCategory.getCategoryList(data);
          return getListCategory.catch((e) => {
            this.$store.dispatch('jNotificationScreen/toggleProblem', e.response.data.errors[0].message).bind(this);
          });
        },
        responseAdapter({ data }) {
          return {
            data: data.data ? data.data : [],
            count: data.length ? data.length : 0,
          };
        },
      },
    };
  },
  watch: {
    filter_data(val) {
      if (val === '') {
        this.$refs.journeyModeList.setFilter('');
      }
    },
    created_res() {
      this.refreshTable();
    },
    updated_res() {
      this.refreshTable();
    },
    selected_rows() {
      if (this.selected_rows.length === 0) {
        this.$refs.floating.hide();
      } else {
        this.$refs.floating.show();
      }
    },
  },
  methods: {
    ...mapActions('jNotificationScreen', ['toggleProblem', 'showLoading', 'hideLoading']),
    loading(val) {
      const a = val;
      a.pagination.query_type = this.select_type;
      if (this.load_first) {
        this.showLoading();
      }
    },

    loaded(data) {
      this.data_table = data.data.data;
      if (this.load_first) {
        this.hideLoading();
      } else {
        this.load_first = !this.load_first;
      }
    },

    refreshTable() {
      this.$refs.journeyModeList.getData();
    },

    filter(val) {
      this.$refs.journeyModeList.setFilter(val);
    },

    categoryDeleted() {
      this.refreshTable();
      this.selected_rows = [];
    },
  },
};



