/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapState, mapActions } from 'vuex';

export const jMixModeEditCategory = {
  methods: {
    ...mapActions('jStoreCategory', ['getCategoryDetail']),
    modeEdit() {
      const categoryId = {
        category: {
          id: Number(this.$route.params.id),
        },
      };
      // get detail
      this.getCategoryDetail(categoryId);
    },
    generateData(val) {
      const category = {
        id: Number(this.$route.params.id),
        name: val.name,
        description: val.description,
        code: val.code,
        icon: {
          id: val.icon,
        },
        type: {
          id: val.package,
        },
        layer: val.layer_id ? {
          id: val.layer_id,
        } : null,
        parent: val.parent_category_id ? {
          id: val.parent_category_id,
        } : null,
      };
      return category;
    },
  },
  computed: {
    ...mapState('jStoreCategory', ['detail_category']),
  },
  watch: {
    detail_category(val) {
      const category = this.generateData(val);
      this.category_form = category;
    },
  },
};



