/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapState, mapActions } from 'vuex';

export const jMixModeEditNumberFormat = {
  methods: {
    ...mapActions('jStoreNumberFormat', ['getNumberFormatDetail']),
    modeEdit() {
      const numberFormatId = {
        numbering: {
          id: Number(this.$route.params.id),
        },
      };

      // get detail
      this.getNumberFormatDetail(numberFormatId);
    },
  },

  computed: {
    ...mapState('jStoreNumberFormat', ['number_format_data']),
  },

  watch: {
    'number_format_data.detail_number_format'(newVal) {
      const numberFormatForm = {
        id: newVal.id,
        name: newVal.name,
        patterns: newVal.pattern,
        type: {
          id: newVal.type,
        },
        is_default: newVal.is_default,
      };
      this.number_format_form = numberFormatForm;
      this.default_data = newVal.pattern;
    },
  },
};



