import auth from '@/middleware/auth';
import admin from '@/middleware/admin';
import jCreateProject from '@/views/project/create-project/jCreateProject';
import jCreateProjectPeriod from '@/views/project/create-project/jCreateProjectPeriod';
import jEditProject from '@/views/project/edit-project/jEditProject';
import jEditPeriodProject from '@/views/project/create-project/jEditProjectPeriod';
// import jListProject from '@/views/project/list-project/jListProject';
import jProject from '@/views/project/jProject';

const jRouterProject = [
  // {
  //   path: '/project/edit/:id',
  //   name: 'Edit Project',
  //   component: jEditProject,
  //   meta: {
  //     title: 'Edit Project',
  //     layout: 'jWrapper',
  //   },
  // },
  // {
  //   path: '/project/create',
  //   name: 'Create Project',
  //   component: jCreateProject,
  //   meta: {
  //     title: 'Create Project',
  //     layout: 'jWrapper',
  //   },
  // },
  {
    path: '/project',
    name: 'List Project',
    component: jProject,
    meta: {
      title: 'List Project',
      layout: 'jWrapper',
    },
    children: [
      {
        path: '/project/edit/:id',
        name: 'Edit Project',
        component: jEditProject,
        meta: {
          title: 'Edit Project',
        },
      },
      {
        path: '/project/create',
        name: 'Create Project',
        component: jCreateProject,
        meta: {
          title: 'Create Project',
          layout: 'jWrapper',
        },
      },
      {
        path: '/project/create/period',
        name: 'Create Period Project',
        component: jCreateProjectPeriod,
        meta: {
          title: 'Create Period Project',
          layout: 'jWrapper',
        },
      },
      {
        path: '/project/create/period/edit/:id',
        name: 'Edit Period Project',
        component: jEditPeriodProject,
        meta: {
          title: 'Edit Period Project',
          layout: 'jWrapper',
        },
      },
    ],
  },
];

jRouterProject.map((dataPath) => {
  const eachData = dataPath;
  eachData.meta.middleware = [auth, admin];
  return eachData;
});


export default jRouterProject;



