import auth from '@/middleware/auth';
// import jCreateProject from '@/views/project/create-project/jCreateProject';
// import jEditProject from '@/views/project/edit-project/jEditProject';
import jPeriod from '@/views/period/jPeriod';

const jRouterProject = [
  {
    path: '/period',
    name: 'List Period',
    component: jPeriod,
    meta: {
      title: 'List Period',
      layout: 'jWrapper',
    },
    // children: [
    //   {
    //     path: '/project/edit/:id',
    //     name: 'Edit Project',
    //     component: jEditProject,
    //     meta: {
    //       title: 'Edit Project',
    //     },
    //   },
    //   {
    //     path: '/project/create',
    //     name: 'Create Project',
    //     component: jCreateProject,
    //     meta: {
    //       title: 'Create Project',
    //       layout: 'jWrapper',
    //     },
    //   },
    // ],
  },
];

jRouterProject.map((dataPath) => {
  const eachData = dataPath;
  eachData.meta.middleware = auth;
  return eachData;
});


export default jRouterProject;



