<template>
  <div>
    <j-title-header :options="options"/>
    <div
      v-show="load_first"
      class="jTableDivision">

      <b-row>
        <b-col cols="5">
          <div class="float-to-left">
            <jExportDivision />
          </div>
        </b-col>

        <b-col cols="7">
          <div class="float-to-right">
            <div class="m__r--5">
              <b-button
                class="btn blue"
                @click="$router.replace('/organigram/chart')">
                SHOW DIVISION CHART
              </b-button>
            </div>
            <div>
              <b-button
                class="btn blue"
                @click="$emit('createDivision')">
                CREATE DIVISION
              </b-button>
            </div>

          </div>
        </b-col>

      </b-row>

      <v-server-table
        ref="divisionServer"
        :columns="columns"
        :options="options"
        :url="env"
        class="jTable"
        @loading="loading"
        @loaded="loaded"
        @row-click="rowClick"
        @pagination="removeRowBackground">

        <div
          slot="beforeFilter"
          class="custom-search">
          <b-row>
            <b-col cols="7">
              <b-form
                @keyup.enter="filter(filter_data)">
                <b-form-group>
                  <j-input
                    v-model="filter_data"/>
                </b-form-group>
              </b-form>
            </b-col>
            <b-col
              class="p--1 p__l--2"
              style="align-self: center;"
              cols="5">
              <b-button
                class="btn blue outline"
                @click="filter(filter_data)"> <i class="fa fa-search bold" /> Search </b-button>
            </b-col>
          </b-row>
        </div>

        <div
          slot="created_date"
          slot-scope="props">
          {{ props.row.created_date | moment("DD/MM/YYYY") }}
        </div>
      </v-server-table>
    </div>

    <div
      v-if="!load_first">
      <jLoadingTable/>
    </div>

  </div>
</template>

<script>
import { jDivision } from '@/mixins/components/organigram/jDivision';
import jLoadingTable from '@/components/skeleton-loading/jLoadingTable';
import jExportDivision from './jExportDivision';

export default {
  components: { jLoadingTable, jExportDivision },
  mixins: [jDivision],
  data() {
    return {
      rowing: null,
      options: {
        icon: 'fa-user',
        main_title: 'DIVISION',
        opt_title: '',
      },
    };
  },
  watch: {
    rowing(newData, oldData) {
      if (oldData) {
        this.removeRowClick(oldData);
      }
      // this.rowing = null;
    },
  },
  methods: {
    refreshTable() {
      this.$refs.divisionServer.getData();
    },
    nullOfRowing() {
      this.rowing = null;
    },
  },
};
</script>

<style lang="scss">

</style>



