<template>
  <div>
    <b-form-group :class="{'error-input' : $v.period_form.name.$error}">
      <label>Name*</label>
      <j-input
        v-model="$v.period_form.name.$model"
        :max-length="50"
        placeholder="Name"/>
    </b-form-group>

    <b-form-group :class="{'error-input' : $v.period_form.description.$error}">
      <label>Description</label>
      <j-textarea
        v-model="$v.period_form.description.$model"
        :max-length="300"
        placeholder="Description"/>
    </b-form-group>
    <b-row>
      <b-col
        md="6"
        sm="6"
        cols="12">
        <b-form-group :class="{'error-input' : $v.period_form.start_date.$error}">
          <label>Start date*</label>
          <j-datepicker
            v-model="$v.period_form.start_date.$model"/>
        </b-form-group>
      </b-col>
      <b-col
        md="6"
        sm="6"
        cols="12">
        <b-form-group :class="{'error-input' : $v.period_form.end_date.$error}">
          <label>End date*</label>
          <j-datepicker
            v-model="$v.period_form.end_date.$model"
            placeholder="ex: Jan 01, 2020"/>
        </b-form-group>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { jMixValidationFormProjectPeriod } from '@/mixins/validations/project/jMixValidationFormProjectPeriod';
import { jMixModeEditProjectPeriod } from '@/mixins/mode-form/project/jMixModeEditProjectPeriod';

export default {
  mixins: [
    jMixValidationFormProjectPeriod,
    jMixModeEditProjectPeriod,
  ],
  props: {
    mode: {
      type: String,
      default: 'create',
    },
  },
  data() {
    return {
      period_form: {
        id: 0,
        name: '',
        description: '',
        start_date: '',
        end_date: '',
      },
    };
  },
  computed: {
    sort_form_data() {
      const data = this.period_form;
      data.start_date = this.$moment(this.period_form.start_date).format('YYYY-MM-DD');
      data.end_date = this.$moment(this.period_form.end_date).format('YYYY-MM-DD');
      return data;
    },
  },
  methods: {
    validateForm() {
      this.$v.period_form.$touch();
      if (!this.$v.period_form.$error) {
        this.$emit('getData', this.sort_form_data);
      } else {
        this.$emit('getData', null);
      }
    },
  },
};
</script>



