<template>
  <b-container fluid>
    <b-form-group class="profile-page">
      <span class="span--title-icon">
        <i class="fa fa-clipboard"/>
        <h2 class="bold">Profile Detail</h2>
      </span>
      <div class="profile-page__body">
        <b-container fluid>
          <b-row>
            <b-col cols="8">
              <div class="profile-page__section">
                <h3 class="h3--blue">Account</h3>
                <b-form-group :class="{'error-input' : $v.email.$error}">
                  <label
                    class="bold"
                    for="email">Email</label>
                  <j-input
                    id="email"
                    :disabled="true"
                    v-model="$v.email.$model"
                    type="email"/>
                </b-form-group>
              </div>
              <div class="profile-page__section">
                <h3 class="h3--blue">Employee Detail</h3>
                <b-row>
                  <b-col>
                    <b-form-group :class="{'error-input' : $v.first_name.$error}">
                      <label
                        class="bold"
                        for="first-name">First Name</label>
                      <j-input
                        id="first-name"
                        v-model="$v.first_name.$model"
                        type="text"/>
                    </b-form-group>
                  </b-col>
                  <b-col>
                    <b-form-group :class="{'error-input' : $v.last_name.$error}">
                      <label
                        class="bold"
                        for="last-name">Last Name</label>
                      <j-input
                        id="last-name"
                        v-model="$v.last_name.$model"
                        type="text"/>
                    </b-form-group>
                  </b-col>
                </b-row>

                <b-form-group :class="{'error-input' : $v.gender.$error}">
                  <label
                    class="bold"
                    for="gender">Gender</label>
                  <j-input
                    id="gender"
                    v-model="$v.gender.$model"
                    type="text"/>
                </b-form-group>

                <b-form-group :class="{'error-input' : $v.date_of_birth.$error}">
                  <label
                    class="bold"
                    for="date_of_birth">Date of birth</label>
                  <j-input
                    id="date_of_birth"
                    v-model="$v.date_of_birth.$model"
                    type="text"/>
                </b-form-group>

                <b-form-group :class="{'error-input' : $v.phone_number.$error}">
                  <label
                    class="bold"
                    for="phone_number">Phone Number</label>
                  <j-input
                    id="phone_number"
                    v-model="$v.phone_number.$model"
                    type="text"/>
                </b-form-group>

                <b-form-group :class="{'error-input' : $v.address.$error}">
                  <label
                    class="bold"
                    for="address">Address</label>
                  <j-input
                    id="address"
                    v-model="$v.address.$model"
                    type="text"/>
                </b-form-group>

                <b-form-group :class="{'error-input' : $v.city.$error}">
                  <label
                    class="bold"
                    for="city">City</label>
                  <j-input
                    id="city"
                    v-model="$v.city.$model"
                    type="text"/>
                </b-form-group>

                <b-form-group :class="{'error-input' : $v.country.$error}">
                  <label
                    class="bold"
                    for="country">Country</label>
                  <j-input
                    id="country"
                    v-model="$v.country.$model"
                    type="text"/>
                </b-form-group>

                <b-form-group :class="{'error-input' : $v.created_date.$error}">
                  <label
                    class="bold"
                    for="created_date">Created date</label>
                  <j-input
                    id="created_date"
                    v-model="$v.created_date.$model"
                    type="text"/>
                </b-form-group>

                <b-form-group :class="{'error-input' : $v.updated_date.$error}">
                  <label
                    class="bold"
                    for="updated_date">Updated date</label>
                  <j-input
                    id="updated_date"
                    v-model="$v.updated_date.$model"
                    type="text"/>
                </b-form-group>
              </div>
              <b-container>
                <b-row>
                  <b-col cols="8">
                    <b-button
                      class="btn blue icon"
                      @click="update">
                      <i class="fa fa-cloud-upload"/>
                      Update
                    </b-button>
                    <b-button class="btn green icon">Change Password</b-button>
                  </b-col>
                  <b-col cols="4">
                    <b-button
                      class="btn grey icon align-right"
                      @click="$router.go(-1)">
                      <i class="fa fa-times-circle"/>
                      Cancel
                    </b-button>
                  </b-col>
                </b-row>
              </b-container>
            </b-col>
            <b-col cols="4">
              <j-singleupload :accepted-file-type="['jpg', 'png', 'jpeg', 'bmp', 'xlsx']"/>
            </b-col>
          </b-row>
        </b-container>
      </div>
    </b-form-group>
    <!--Error Screen-->
    <j-problem-screen
      ref="problemScreen"
      :message="'Please try again later!'"/>
    <!--Loading Screen-->
    <j-loading-screen ref="loadingScreen"/>
    <!--Success Screen-->
    <j-success-screen
      ref="successScreen"
      :message="'Profile has been successfully uploaded'"/>
  </b-container>
</template>

<script>
/* eslint-disable import/no-duplicates,no-underscore-dangle */

import { required, email } from 'vuelidate/lib/validators';
import axios from 'axios';

export default {
  /**
     * Created by Arga Ghulam Ahmad - arga@jojonomic.com
     * Last update 21 august 2018
     *
     * jProfilePage
     */
  name: 'JProfilePage',
  data() {
    return {
      id: -1,
      email: '',
      first_name: '',
      last_name: '',
      gender: '',
      date_of_birth: '',
      phone_number: '',
      address: '',
      city: '',
      country: '',
      created_date: '',
      updated_date: '',
    };
  },
  beforeCreate() {
    const USER_PROFILE_API = 'http://private-2b77d-jojopro30.apiary-mock.com/user/profile';

    axios({
      async: true,
      crossDomain: true,
      url: USER_PROFILE_API,
      method: 'GET',
    }).then((response) => {
      const data = response.data.data;

      this.id = data.id;
      this.email = data.email;
      this.first_name = data.first_name;
      this.last_name = data.last_name;
      this.gender = data.gender;
      this.date_of_birth = data.date_of_birth;
      this.phone_number = data.phone_number;
      this.address = data.address;
      this.city = data.city;
      this.country = data.country;
      this.created_date = data.created_date;
      this.updated_date = data.updated_date;
    });
  },
  methods: {
    // update() {
    //   this.$refs.loadingScreen.showLoadingScreen();
    //   const USER_PROFILE_API = 'http://private-2b77d-jojopro30.apiary-mock.com/user/profile';
    //   const _this = this;
    //
    //   axios.post(USER_PROFILE_API, {
    //     id: _this.id,
    //     email: _this.email,
    //     first_name: _this.first_name,
    //     last_name: _this.last_name,
    //     gender: _this.gender,
    //     date_of_birth: _this.date_of_birth,
    //     phone_number: _this.phone_number,
    //     address: _this.address,
    //     city: _this.city,
    //     country: _this.country,
    //     created_date: _this.create_date,
    //     updated_date: _this.updated_date,
    //   })
    //     .then(() => {
    //       this.$refs.loadingScreen.closeLoadingScreen();
    //       this.$refs.successScreen.toggleSuccessScreen();
    //     })
    //     .catch(() => {
    //       this.$refs.loadingScreen.closeLoadingScreen();
    //       this.$refs.problemScreen.toggleProblemScreen();
    //     });
    // },
  },
  validations: {
    id: {
      required,
    },
    email: {
      required,
      email,
    },
    first_name: {
      required,
    },
    last_name: {
      required,
    },
    gender: {
      required,
    },
    date_of_birth: {
      required,
    },
    phone_number: {
      required,
    },
    address: {
      required,
    },
    city: {
      required,
    },
    country: {
      required,
    },
    created_date: {
      required,
    },
    updated_date: {
      required,
    },
  },
};
</script>



