/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';

export const jMixModelDeleteNumberFormats = {
  computed: {
    delete_data() {
      if (this.selectedRows.length > 0) {
        return this.selectedRows.map(data => ({
          numberings: [{
            id: data.id,
          }],
        }));
      }
      return null;
    },
  },

  methods: {
    deleteNumberFormats() {
      if (this.delete_data !== null) {
        // eslint-disable-next-line
        const text = 'You want to delete ' + this.number_format_name;

        this.$swal({
          title: 'Are you sure?',
          text,
          type: 'question',
          showCancelButton: true,
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes',
        }).then((result) => {
          if (result.value) {
            this.$refs.deleteNumberFormat.toggleIsVisible();
          }
        });
      }
    },
  },

};

export const jMixModelDeleteNumberFormat = {
  computed: {
    ...mapState('jStoreNumberFormat', ['number_format_data']),

    delete_data() {
      if (this.number_format_data.detail_number_format !== null) {
        return {
          numberings: [{
            id: Number(this.$route.params.id),
          }],
        };
      }
      return null;
    },
  },

  methods: {
    ...mapActions('jStoreNumberFormat', ['deleteNumberFormat']),

    deleteData(val) {
      this.$swal({
        title: 'Are you sure?',
        text: 'to delete this number format',
        type: 'warning',
        showCancelButton: true,
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes',
      }).then((result) => {
        if (result.value) {
          this.deleteNumberFormat(this.delete_data);
        }
      });
    },
  },

};



