<template>
  <div>
    <b-row>
      <b-col cols="9">
        <div class="float-to-left">
          <b-button
            class="btn green"
            @click="exportProject">
            Export Project
          </b-button>
        </div>
      </b-col>

      <b-col>
        <div class="float-to-right">
          <b-button
            class="btn blue"
            @click="$router.replace('/project/create')">
            Create Project
          </b-button>
        </div>
      </b-col>
    </b-row>

    <jListProject />

    <router-view/>

  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import jListProject from './list-project/jListProject';

export default {
  components: {
    jListProject,
  },

  computed: {
    ...mapState('jStoreProject', ['temporary_data_project', 'project_data']),
  },

  watch: {
    // eslint-disable-next-line
    '$route' (to, from) {
      if ((to.path === '/project/create' && from.path === '/project/create/period')
    || (to.name === 'Create Project' && from.name === 'Edit Period Project')
    || (to.name === 'Edit Project' && from.name === 'Create Period Project')
    || (to.name === 'Edit Project' && from.name === 'Edit Period Project')) {
        this.mutateTemporaryProjectValid(true);
      } else {
        this.mutateTemporaryProjectValid(false);
      }
    },
    // eslint-disable-next-line
    'project_data.export_project' (newVal) {
      window.open(newVal.url);
    },
  },

  methods: {
    ...mapMutations('jStoreProject', ['mutateTemporaryProjectValid']),

    exportProject() {
      this.$store.dispatch('jStoreProject/exportProject', { type: 'xls' });
    },
  },
};
</script>



