<template>
  <div>
    <b-form-group
      :class="{'error-input' : $v.project.name.$error}">
      <label>Name*</label>
      <j-input
        v-model="project.name"
        :max-length="50"
        placeholder="Name"/>
    </b-form-group>

    <b-form-group
      :class="{'error-input' : $v.project.code.$error}">
      <label>Code*</label>
      <j-input
        v-model="project.code"
        :max-length="42"
        placeholder="Code"/>
    </b-form-group>

    <b-form-group
      :class="{'error-input' : $v.project.period.$error}">
      <label>Period*</label>
      <b-row>
        <b-col
          :cols="project.period !== null && project.period !== undefined ? '11' : '12'">
          <j-single-select
            :options="project_data.list_periods"
            v-model="project.period"
            value-format="object"
            label="name"
            placeholder="Select Period"/>
        </b-col>
        <b-col
          v-if="project.period !== null && project.period !== undefined"
          cols="1">
          <b-button
            class="btn btn blue outline btn-secondary btn-edit pull-right"
            @click="EditPeriod(project.period.id)">
            <i class="fa fa-pencil"/>
          </b-button>
        </b-col>
      </b-row>
      <label
        class="c--blue float-right p__t--10"
        style="cursor: pointer;"
        @click="addNewPeriod"> + Add New Period </label>
    </b-form-group>

    <b-form-group
      :class="{'error-input' : $v.project.description.$error}">
      <label>Description</label>
      <j-textarea
        v-model="project.description"
        :max-length="300"
        placeholder="Description"/>
    </b-form-group>
    <b-form-group
      :class="{'error-input' : $v.project.division.$error}">
      <label>Division</label>
      <j-single-select
        :options="project_data.list_division"
        v-model="project.division"
        value-format="object"
        label="name"
        placeholder="Select Division"/>
    </b-form-group>
    <label>Project Member</label>
    <br>
    <p>Filter By: </p>
    <j-radio
      v-model="filter"
      :options="option_filter"
      :stacked="false"
      :disabled="false"/>
    <b-form-group
      :class="{'error-input' : $v.project.organigrams.$error}">
      <j-multi-select
        v-if="project_data.list_positions !== null"
        :options="project_data.list_positions"
        :default-data="default_organigrams"
        v-model="project.organigrams"
        :label="filter"
        :placeholder="`Select ${placeholder}`"
        value-format="object"
        @getData="getDataEditOrganigram"/>
    </b-form-group>
  </div>
</template>

<script>
import { jMixValidationFormProject } from '@/mixins/validations/project/jMixValidationFormProject';
import { jMixOptionsFormProject } from '@/mixins/list-options/project/jMixOptionsFormProject';
import { jMixModeCreateProject } from '@/mixins/mode-form/project/jMixModeCreateProject';
import { jMixModeEditProject } from '@/mixins/mode-form/project/jMixModeEditProject';
import { mapMutations } from 'vuex';

export default {
  mixins: [
    jMixValidationFormProject,
    jMixOptionsFormProject,
    jMixModeCreateProject,
    jMixModeEditProject,
  ],
  props: {
    mode: {
      type: String,
      default: 'create',
    },
  },
  data() {
    return {
      project: {
        id: 0,
        name: null,
        code: null,
        period: null,
        description: null,
        organigrams: null,
        rule: null,
        organigrams_edit: null,
        division: null,
      },
    };
  },
  methods: {
    ...mapMutations('jStoreProject', ['mutateTemporaryProjectData', 'mutateTemporaryProjectMode']),

    validateForm() {
      this.$v.project.$touch();
      if (!this.$v.project.$error) {
        this.$emit('getData', this.project);
      } else {
        this.$emit('getData', null);
      }
    },

    getDataEditOrganigram(val) {
      this.project.organigrams_edit = val;
    },

    addNewPeriod() {
      const mode = {
        mode: this.mode,
        params: this.$route.params ? this.$route.params : null,
      };
      this.mutateTemporaryProjectMode(mode);
      this.mutateTemporaryProjectData(this.project);
      this.$router.replace('/project/create/period');
    },

    EditPeriod(id) {
      const mode = {
        mode: this.mode,
        params: this.$route.params ? this.$route.params : null,
      };
      this.mutateTemporaryProjectMode(mode);
      this.mutateTemporaryProjectData(this.project);
      this.$router.replace(`/project/create/period/edit/${id}`);
    },
  },
};
</script>



