<template>
  <div class="p--10">
    <b-row>
      <b-col>
        <h4 class="c--blue">
          General
        </h4>
        <b-form-group :class="{'error-input' : $v.category_form.name.$error}">
          <label>Name*</label>
          <j-input
            v-model="$v.category_form.name.$model"
            placeholder="Category Name"/>
        </b-form-group>
        <b-form-group :class="{'error-input' : $v.category_form.description.$error}">
          <label>Description</label>
          <j-textarea
            v-model="$v.category_form.description.$model"
            placeholder="Category Description"/>
        </b-form-group>
        <b-form-group :class="{'error-input' : $v.category_form.code.$error}">
          <label>Code*</label>
          <j-input
            v-model="$v.category_form.code.$model"
            placeholder="Category Code"/>
        </b-form-group>
        <b-form-group :class="{'error-input' : $v.category_form.type.$error}">
          <label>Type*</label>
          <j-single-select
            v-model="$v.category_form.type.$model"
            :options="list_type"
            placeholder="Select Type"
            value-format="object"
            label="name"/>
        </b-form-group>
        <b-form-group :class="{'error-input' : $v.category_form.icon.$error}">
          <label>Icon*</label>
          <j-single-select
            v-model="$v.category_form.icon.$model"
            :options="list_symbols"
            :custom="true"
            placeholder="Select Icon"
            value-format="object"
            label="name">
            <template
              slot-scope="node"
              slot="node-custom">
              <img
                :src="
                require(`@jojocoders/jojonomic-ui/src/Public/Assets/icon/${ node.node.id }.png`)"
                style="width:20px;height:20px;margin:5px;">&nbsp; {{ node.node.label }}
            </template>
          </j-single-select>
        </b-form-group>
      </b-col>
      <b-col>
        <h4 class="c--blue">
          Category Layer
        </h4>
        <b-form-group :class="{'error-input' : $v.category_form.layer.$error}">
          <label>Layer*</label>
          <j-single-select
            v-model="$v.category_form.layer.$model"
            :options="layer_list"
            placeholder="Select Layer"
            value-format="object"
            label="name"/>
        </b-form-group>
        <b-form-group :class="{'error-input' : $v.category_form.parent.$error}">
          <label>Parent</label>
          <j-single-select
            v-model="$v.category_form.parent.$model"
            :options="list_category"
            :disabled="parent_disabled"
            :custom="true"
            placeholder="Select Category"
            value-format="object"
            label="name">
            <template
              slot-scope="node"
              slot="node-custom">
              <img
                :src="
                  require(
                `@jojocoders/jojonomic-ui/src/Public/Assets/icon/${ node.node.label.icon }.png`)"
                style="width:20px;height:20px;margin:5px;">
              &nbsp; {{ node.node.label.name }}
            </template>
          </j-single-select>
        </b-form-group>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { jMixValidationFormCategory } from '@/mixins/validations/category/jMixValidationFormCategory';
import { jMixOptionsFormCategory } from '@/mixins/list-options/category/jMixOptionsFormCategory';
import { jMixModeEditCategory } from '@/mixins/mode-form/category/jMixModeEditCategory';
import { jMixCategory } from '@/mixins/components/get-list-option-form/jMixCategory';

export default {
  mixins: [
    jMixCategory,
    jMixValidationFormCategory,
    jMixOptionsFormCategory,
    jMixModeEditCategory,
  ],
  props: {
    mode: {
      type: String,
      default: 'create',
    },
  },
  data() {
    return {
      category_form: {
        name: '',
        description: '',
        code: '',
        icon: null,
        layer: null,
        parent: null,
        type: null,
      },
      parent_disabled: true,
    };
  },
  watch: {
    // eslint-disable-next-line func-names
    'category_form.code': function (newVal, oldVal) {
      if (!newVal.match(/^[A-Za-z0-9-_]+$/g) && newVal.length !== 0) {
        setTimeout(() => {
          this.category_form.code = oldVal;
        }, 0);
      }
    },
    // eslint-disable-next-line func-names
    'category_form.layer': function (val) {
      if (val !== undefined && val !== null) {
        if (val.id === this.layer_list[0].id) {
          this.category_form.parent = null;
          this.parent_disabled = true;
        } else {
          this.parent_disabled = false;
        }
      } else {
        this.parent_disabled = true;
      }
    },
  },
  methods: {
    save() {
      this.$v.category_form.$touch();
      if (!this.$v.category_form.$error) {
        this.$emit('data', this.category_form);
      } else {
        this.$emit('data', null);
      }
    },
  },
};
</script>



