<template>
  <div class="newDiv">
    <div class="transblack-background"/>
    <jFloatingPrompt ref="divisionFloating">
      <b-row class="jobTitleFloat">
        <b-col>
          <h3>Create New Division</h3>
          <!-- eslint-disable-next-line -->
          <b-form-group :class="{'error-input' : $v.v_add_division.name.$error || !$v.v_add_division.name.minLength}">
            <label>Name*</label>
            <j-input
              v-model="v_add_division.name"
              :error-message="err_message"
              :max-length="50"
              placeholder="Division Name"/>
          </b-form-group>
          <b-form-group :class="{'error-input' : $v.v_add_division.code.$error}">
            <label>Code*</label>
            <j-input
              v-model="v_add_division.code"
              :max-length="42"
              placeholder="Code"/>
          </b-form-group>

          <b-form-group :class="{'error-input' : $v.v_add_division.parent_division.$error}">
            <label>Parent Division</label>
            <j-single-select
              v-model="v_add_division.parent_division"
              :options="parent_divisions_list"
              placeholder="Ex: C-Level"
              value-format="object"
              label="name" />
          </b-form-group>

          <b-form-group :class="{'error-input' : $v.v_add_division.description.$error}">
            <label>Description</label>
            <j-textarea
              v-model="v_add_division.description"
              :max-length="300"
              placeholder="Description"/>
          </b-form-group>

          <b-form-group :class="{'error-input' : $v.v_add_division.head_division.$error}">
            <label>Head of Division</label>
            <j-single-select
              v-model="v_add_division.head_division"
              :options="title_list"
              placeholder="Ex: CTO"
              value-format="object"
              label="name" />
          </b-form-group>
          <label>Dynamic Flow</label>
          <br>
          <draggable
            :list="v_add_division.flow_before"
            :disabled="false"
            class="list-group"
            ghost-class="ghost"
            @start="dragging = true"
            @end="dragging = false">

            <div
              v-for="(flow, index) in $v.v_add_division.flow_before.$each.$iter"
              :key="index">

              <b-row>
                <b-col cols="1">
                  <i class="fa fa-bars drag-icon-bars"/>
                </b-col>
                <b-col>
                  <b-form-group
                    :class="{'error-input' : flow.flow.$error}">
                    <label>Flow</label>
                    <j-single-select
                      v-model="flow.flow.$model"
                      :options="flow_list"
                      placeholder="Ex: CIO"
                      value-format="object"
                      label="name" />
                  </b-form-group>
                </b-col>

                <b-col cols="1">
                  <div
                    v-if="v_add_division.flow_before.length > 1"
                    class="delete--flow--icon cursor--pointer"
                    @click="deleteFlow(index)">
                    <i class="fa fa-times-circle"/>
                  </div>
                </b-col>
              </b-row>
            </div>
          </draggable>

          <label
            class="cursor--pointer pull-right"
            @click="addMore('before')">
            + add more flow
          </label>

          <br>
          <br>

          <label class="c--blue">
            Before Head of Division
          </label>
          <hr>
          <label class="c--blue">
            After Head of Division
          </label>

          <br>
          <br>

          <draggable
            :list="v_add_division.flow_after"
            :disabled="false"
            class="list-group"
            ghost-class="ghost"
            @start="dragging = true"
            @end="dragging = false">

            <div
              v-for="(floww, index) in $v.v_add_division.flow_after.$each.$iter"
              :key="index">

              <b-row>
                <b-col cols="1">
                  <i class="fa fa-bars drag-icon-bars"/>
                </b-col>
                <b-col>
                  <b-form-group
                    :class="{'error-input' : floww.flow.$error}">
                    <label>Flow</label>
                    <j-single-select
                      v-model="floww.flow.$model"
                      :options="flow_list"
                      placeholder="Ex: CIO"
                      value-format="object"
                      label="name" />
                  </b-form-group>
                </b-col>

                <b-col cols="1">
                  <div
                    v-if="v_add_division.flow_before.length > 1"
                    class="delete--flow--icon cursor--pointer"
                    @click="deleteFlow(index)">
                    <i class="fa fa-times-circle"/>
                  </div>
                </b-col>
              </b-row>
            </div>
          </draggable>

          <label
            class="cursor--pointer pull-right"
            @click="addMore('after')">
            + add more flow
          </label>

        </b-col>

        <b-col cols="4">
          <!-- button create -->
          <div class="wrap-button-flow">
            <b-button
              class="btn blue"
              @click="createDivision">
              CREATE
            </b-button>
            <i
              class="fa fa-times"
              @click="$refs.divisionFloating.hide('/organigram')"/>
          </div>
        </b-col>
      </b-row>
    </jFloatingprompt>
  </div>

</template>

<script>
import jFloatingPrompt from '@/components/new-floating-templete/jFloatingPrompt';
import { mapActions, mapState, mapGetters } from 'vuex';
import { required, minLength } from 'vuelidate/lib/validators';
import draggable from 'vuedraggable';

export default {
  name: 'JNewDivision',
  components: {
    jFloatingPrompt, draggable,
  },
  data() {
    return {
      flow_type: [
        { id: 1, name: 'Before Head of Division' },
        { id: 2, name: 'After Head of Division' },
      ],
      v_add_division: {
        name: '',
        code: '',
        parent_division: { id: 0 },
        description: '',
        head_division: null,
        flow_before: [
          {
            id: 0,
            flow: null,
            order: null,
          },
        ],
        flow_after: [
          {
            id: 0,
            flow: null,
            order: null,
          },
        ],
      },
    };
  },
  validations: {
    v_add_division: {
      name: { required, minLength: minLength(3) },
      code: { required },
      parent_division: { },
      description: { },
      head_division: { },
      flow_before: {
        // required,
        $each: {
          flow: { },
        },
      },
      flow_after: {
        // required,
        $each: {
          flow: { },
        },
      },
    },
  },
  computed: {
    ...mapState('jOrganigram', ['division_list', 'title_list']),
    ...mapState('jFlows', ['flow_list']),
    ...mapGetters('jOrganigram', ['parent_divisions_list']),
    err_message() {
      if (!this.$v.v_add_division.name.required) {
        return 'Field is required';
      } else if (!this.$v.v_add_division.name.minLength) {
        return 'Minimum 3 characters';
      }
      return 'Field is required';
    },
    save_division() {
      return {
        data: this.v_add_division,
      };
    },
    send_data_division() {
      let headOrganigramId = '';
      let rootDivisionId = '';

      if (this.v_add_division.head_division !== null
        && this.v_add_division.head_division !== undefined) {
        headOrganigramId = typeof this.v_add_division.head_division.id !== 'undefined'
          ? this.v_add_division.head_division.id : '';
      }

      if (this.v_add_division.parent_division !== null
        && this.v_add_division.parent_division !== undefined) {
        rootDivisionId = typeof this.v_add_division.parent_division.id !== 'undefined'
          ? this.v_add_division.parent_division.id : '';
      }

      const sendData = {
        name: this.v_add_division.name,
        code: this.v_add_division.code,
        head_organigram_id: headOrganigramId,
        root_division_id: rootDivisionId,
        description: typeof this.v_add_division.description !== 'undefined' ?
          this.v_add_division.description : '',
        dynamic_flow: [],
      };
      this.v_add_division.flow_before.forEach((e, index) => {
        if (e.flow !== null) {
          const flow = {
            id: 0,
            flow_id: e.flow ? e.flow.id : '',
            order: index,
            type: 1,
            is_delete: false,
          };
          sendData.dynamic_flow.push(flow);
        }
      });

      this.v_add_division.flow_after.forEach((e, index) => {
        if (e.flow !== null) {
          const flow = {
            id: 0,
            flow_id: e.flow ? e.flow.id : '',
            order: index,
            type: 2,
            is_delete: false,
          };
          sendData.dynamic_flow.push(flow);
        }
      });
      return sendData;
    },
  },
  watch: {
    // eslint-disable-next-line func-names
    'v_add_division.code': function (newVal, oldVal) {
      if (!newVal.match(/^[A-Za-z0-9-_]+$/g) && newVal.length !== 0) {
        setTimeout(() => {
          this.v_add_division.code = oldVal;
        }, 0);
      }
    },
  },
  created() {
    const requestAll = {
      pagination: {
        limit: 0,
        page: 0,
        column: 'id',
        ascending: true,
        query: '',
      },
      export: false,
    };
    this.getAllDivisions(requestAll);
    this.getAllTitle(requestAll);
    this.getAllFlows(requestAll);
  },
  methods: {
    ...mapActions('jOrganigram', ['createNewDivision', 'getAllDivisions', 'getAllTitle']),
    ...mapActions('jFlows', ['getAllFlows']),
    createDivision() {
      this.$v.v_add_division.$touch();
      if (!this.$v.v_add_division.$error) {
        this.$refs.divisionFloating.hide('/organigram');
        setTimeout(() => {
          this.createNewDivision(this.send_data_division);
        }, 250);
      }
    },

    addMore(arg) {
      if (arg === 'before') {
        this.v_add_division.flow_before.push({ flow: null, order: null, id: 1 });
      } else {
        this.v_add_division.flow_after.push({ flow: null, order: null, id: 1 });
      }
    },

    deleteFlow(index) {
      this.v_add_division.flow_before.splice(Number(index), 1);
    },
    // exitFloating() {
    //   const elem = document.getElementsByClassName('selected-bottom-rules');
    //   elem[0].classList.remove('show-floating');
    //   elem[0].classList.add('hide-floating');
    //   setTimeout(() => {
    //     elem[0].classList.remove('selected-bottom-rules');
    //     this.$router.replace('/organigram');
    //   }, 200);
    // },
  },
};
</script>

<style lang="scss">


</style>



