<template>
  <jFloatingPrompt
    ref="divisionFloating"
    class="newDiv">
    <b-row class="jobTitleFloat">
      <b-col>
        <!-- eslint-disable-next-line -->
        <b-form-group :class="{'error-input' : $v.division.name.$error || !$v.division.name.minLength}">
          <label>Name*</label>
          <j-input
            v-model="$v.division.name.$model"
            :error-message="err_message"
            :max-length="50"
            placeholder="Job Title Name"/>
        </b-form-group>
        <b-form-group>
          <label>Member(s) Total</label>
          <j-input
            v-model="division.total_member"
            :disabled="true"
            placeholder="Members Total"/>
        </b-form-group>

        <b-form-group :class="{'error-input' : $v.division.description.$error}">
          <label>Description</label>
          <j-input
            v-model="$v.division.description.$model"
            :max-length="300"
            placeholder="Division(s)"/>
        </b-form-group>

      </b-col>
      <b-col>
        <b-form-group :class="{'error-input' : $v.division.code.$error}">
          <label>Code*</label>
          <j-input
            v-model="$v.division.code.$model"
            :max-length="42"
            placeholder="Code"/>
        </b-form-group>

        <!-- <b-form-group :class="{'error-input' : $v.division.description.$error}">
          <label>Description</label>
          <j-input
            v-model="$v.division.description.$model"
            :max-length="300"
            placeholder="Division(s)"/>
        </b-form-group> -->

        <b-form-group :class="{'error-input' : $v.division.parent_division.$error}">
          <label>Parent Division</label>
          <j-single-select
            v-model="division.parent_division"
            :options="parent_divisions_list"
            placeholder="Ex: C-Level"
            value-format="object"
            label="name" />
        </b-form-group>


        <b-form-group :class="{'error-input' : $v.division.head_division.$error}">
          <label>Head of Division</label>
          <j-single-select
            v-model="division.head_division"
            :options="title_list"
            placeholder="Ex: CTO"
            value-format="object"
            label="name" />
        </b-form-group>

      </b-col>

      <b-col cols="4">
        <!-- button UPDATE or DELETE -->
        <div class="wrap-button-flow">
          <b-button
            class="btn blue"
            @click="editDivision">
            UPDATE
          </b-button>
          <b-button
            class="btn blue outline"
            @click="deleteDivision">
            DELETE
          </b-button>
          <i
            class="fa fa-times"
            @click="hideDivisionFloating"/>
        </div>
      </b-col>
    </b-row>

    <b-row>
      <b-col cols="8">
        <label> Dynamic Flow</label>
        <draggable
          :list="division.flow_before"
          :disabled="false"
          class="list-group"
          ghost-class="ghost"
          @start="dragging = true"
          @end="dragging = false">

          <div
            v-for="(flow, index) in $v.division.flow_before.$each.$iter"
            :key="index">

            <b-row>
              <b-col cols="1">
                <i class="fa fa-bars drag-icon-bars"/>
              </b-col>
              <b-col cols="10">
                <b-form-group
                  :class="{'error-input' : flow.flow.$error}">
                  <label>Flow</label>
                  <j-single-select
                    v-model="flow.flow.$model"
                    :options="flow_list"
                    placeholder="Ex: CIO"
                    value-format="object"
                    label="name" />
                </b-form-group>
              </b-col>

              <b-col cols="1">
                <div
                  v-if="division.flow_before.length > 1"
                  class="delete--flow--icon cursor--pointer"
                  @click="deleteFlow(index, 'before')">
                  <i class="fa fa-times-circle"/>
                </div>
              </b-col>
            </b-row>
          </div>
        </draggable>

        <label
          class="cursor--pointer pull-right"
          @click="addMore('before')">
          + add more flow
        </label>

        <br>
        <br>

        <label class="c--blue">
          Before Head of Division
        </label>
        <hr>
        <label class="c--blue">
          After Head of Division
        </label>

        <br>
        <br>

        <draggable
          :list="division.flow_after"
          :disabled="false"
          class="list-group"
          ghost-class="ghost"
          @start="dragging = true"
          @end="dragging = false">

          <div
            v-for="(flow, index) in $v.division.flow_after.$each.$iter"
            :key="index">

            <b-row>
              <b-col cols="1">
                <i class="fa fa-bars drag-icon-bars"/>
              </b-col>
              <b-col cols="10">
                <b-form-group
                  :class="{'error-input' : flow.flow.$error}">
                  <label>Flow</label>
                  <j-single-select
                    v-model="flow.flow.$model"
                    :options="flow_list"
                    placeholder="Ex: CIO"
                    value-format="object"
                    label="name" />
                </b-form-group>
              </b-col>

              <b-col cols="1">
                <div
                  v-if="division.flow_after.length > 1"
                  class="delete--flow--icon cursor--pointer"
                  @click="deleteFlow(index, 'after')">
                  <i class="fa fa-times-circle"/>
                </div>
              </b-col>
            </b-row>
          </div>
        </draggable>

        <label
          class="cursor--pointer pull-right"
          @click="addMore('after')">
          + add more flow
        </label>
      </b-col>
      <b-col cols="4"/>
    </b-row>


  </jFloatingprompt>

</template>

<script>
import { mapActions, mapState, mapGetters } from 'vuex';
import jFloatingPrompt from '@/components/new-floating-templete/jFloatingPrompt';
import jModalSure from '@/components/modal/jModalSure';
import { required, minLength } from 'vuelidate/lib/validators';
import draggable from 'vuedraggable';

export default {
  components: {
    jFloatingPrompt,
    jModalSure,
    draggable,
  },
  props: {
    division: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      deleted_flow: [],
      flow_type: [
        { id: 1, name: 'Before Head of Division' },
        { id: 2, name: 'After Head of Division' },
      ],
    };
  },
  validations: {
    division: {
      name: { required, minLength: minLength(3) },
      code: { required },
      parent_division: { },
      description: { },
      head_division: { },
      flow_before: {
        // required,
        $each: {
          flow: { },
          // type: { },
        },
      },
      flow_after: {
        // required,
        $each: {
          flow: { },
          // type: { },
        },
      },
    },
  },
  computed: {
    ...mapState('jOrganigram', ['division_list', 'title_list']),
    ...mapState('jFlows', ['flow_list']),
    ...mapGetters('jOrganigram', ['parent_divisions_list']),
    err_message() {
      if (!this.$v.division.name.required) {
        return 'Field is required';
      } else if (!this.$v.division.name.minLength) {
        return 'Minimum 3 characters';
      }
      return 'Field is required';
    },

    delete_division() {
      return {
        id: this.division.id,
      };
    },
    update_division() {
      let headOrganigramId = '';
      let rootDivisionId = '';

      if (this.division.head_division !== null && this.division.head_division !== undefined) {
        headOrganigramId = typeof this.division.head_division.id !== 'undefined'
          ? this.division.head_division.id : '';
      }

      if (this.division.parent_division !== null && this.division.parent_division !== undefined) {
        rootDivisionId = typeof this.division.parent_division.id !== 'undefined'
          ? this.division.parent_division.id : '';
      }

      const update = {
        id: this.division.id,
        name: this.division.name,
        code: this.division.code,
        description: this.division.description,
        // parent_division: this.division.parent_division,
        // head_division: this.division.head_division,
        // flow: this.division.flow,
        head_organigram_id: headOrganigramId,
        root_division_id: rootDivisionId,
        dynamic_flow: [],
      };
      this.division.flow_before.forEach((e, index) => {
        if (e.flow !== null) {
          const flow = {
            id: e.id,
            flow_id: e.flow ? e.flow.id : '',
            order: index,
            type: 1,
            is_delete: e.flow ? e.is_delete : true,
          };
          update.dynamic_flow.push(flow);
        }
      });

      this.division.flow_after.forEach((e, index) => {
        if (e.flow !== null) {
          const flow = {
            id: e.id,
            flow_id: e.flow ? e.flow.id : '',
            order: index,
            type: 2,
            is_delete: e.flow ? e.is_delete : true,
          };
          update.dynamic_flow.push(flow);
        }
      });

      this.deleted_flow.forEach((e) => {
        const flow = {
          id: e.id,
          flow_id: e.flow ? e.flow.id : '',
          order: e.order,
          type: e.type,
          is_delete: e.flow ? e.is_delete : true,
        };
        update.dynamic_flow.push(flow);
      });
      return update;
    },
  },
  watch: {
    // eslint-disable-next-line func-names
    'division.code': function (newVal, oldVal) {
      if (!newVal.match(/^[A-Za-z0-9-_]+$/g) && newVal.length !== 0) {
        setTimeout(() => {
          this.division.code = oldVal;
        }, 0);
      }
    },
  },
  created() {
    const requestAll = {
      pagination: {
        limit: 0,
        page: 0,
        column: 'id',
        ascending: true,
        query: '',
      },
      export: false,
    };
    this.getAllDivisions(requestAll);
    this.getAllTitle(requestAll);
    this.getAllFlows(requestAll);
  },
  methods: {
    ...mapActions('jOrganigram', ['removeDivision', 'updateDivision', 'getAllDivisions', 'getAllTitle']),
    ...mapActions('jFlows', ['getAllFlows']),
    deleteDivision() {
      const text = `You want to delete "${this.division.name}"`;

      this.$swal({
        title: 'Are you sure?',
        text,
        type: 'question',
        showCancelButton: true,
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes',
      }).then((result) => {
        if (result.value) {
          this.removeDivision(this.delete_division);
          this.$emit('deleteDivision');
        }
      });
    },
    hideDivisionFloating() {
      this.$refs.divisionFloating.hide();
      this.$emit('hideFloatingPrompt');
    },
    editDivision() {
      const text = `You want to update "${this.division.name}"`;
      this.$v.division.$touch();
      if (!this.$v.division.$error) {
        this.$swal({
          title: 'Are you sure?',
          text,
          type: 'question',
          showCancelButton: true,
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes',
        }).then((result) => {
          if (result.value) {
            // console.log(this.update_division);
            this.updateDivision(this.update_division);
          }
        });
      }
    },

    addMore(arg) {
      if (arg === 'before') {
        this.division.flow_before.push({ flow: null, order: null, id: 0, is_delete: false });
      } else {
        this.division.flow_after.push({ flow: null, order: null, id: 0, is_delete: false });
      }
      // this.division.flow.push({ organigram: null, type: null });
    },

    deleteFlow(index, arg) {
      if (arg === 'before') {
        if (this.division.flow_before[index].id !== 0) {
          const flow = this.division.flow_before[index];
          this.$set(flow, 'is_delete', true);
          this.deleted_flow.push(flow);
        }
        this.division.flow_before.splice(Number(index), 1);
      } else {
        if (this.division.flow_after[index].id !== 0) {
          const flow = this.division.flow_after[index];
          this.$set(flow, 'is_delete', true);
          this.deleted_flow.push(flow);
        }
        this.division.flow_after.splice(Number(index), 1);
      }
    },
  },
};
</script>

<style lang="scss">

</style>



