/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions } from 'vuex';

export const jMixModelDeleteProjects = {
  computed: {
    delete_data() {
      if (this.selectedRows.length > 0) {
        return this.selectedRows.map(data => (
          {
            id: data.id,
          }
        ));
      }
      return null;
    },
  },

  methods: {
    deleteProjects() {
      if (this.delete_data !== null) {
        const text = 'You want to delete all checked project';

        this.$swal({
          title: 'Are you sure?',
          text,
          type: 'question',
          showCancelButton: true,
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes',
        }).then((result) => {
          if (result.value) {
            this.$refs.deleteProject.toggleIsVisible();
          }
        });
      }
    },
  },
};

export const jMixModelDeleteProject = {
  computed: {
    delete_data() {
      if (this.project_data.detail_project !== null) {
        return {
          id: Number(this.$route.params.id),
        };
      }
      return null;
    },
  },
};



