<template>
  <div>
    <j-title-header :options="options"/>
    <div
      v-show="load_first"
      class="jTableTitle">
      <b-row>
        <b-col cols="9">
          <j-import-file
            v-if="default_import"
            :href-sample="require('@/assets/file/FORMAT_CREATE_POSITION.zip')"
            @import-data="importData"/>
          <b-button
            v-if="is_error"
            class="btn outline red"
            @click="$refs.modalPosition.show()"
          >
            <i class="fa fa-close"/>
            OPEN ERROR DATA
          </b-button>
          <b-button
            v-else-if="data_import[0]"
            class="btn green"
            @click="uploadData">
            UPLOAD
          </b-button>

          <div class="float-to-left">
            <jExportTitle class="m__t--20" />
          </div>
        </b-col>

        <b-col>
          <div class="float-to-right">
            <b-button
              class="btn blue"
              @click="$emit('createTitle')">
              CREATE POSITION
            </b-button>
          </div>
        </b-col>
      </b-row>


      <v-server-table
        ref="titleServer"
        :columns="columns"
        :options="options"
        :url="env"
        class="jTable"
        @loading="loading"
        @loaded="loaded"
        @row-click="rowClick"
        @pagination="removeRowBackground">

        <div
          slot="beforeFilter"
          class="custom-search">
          <b-row>
            <b-col cols="3">
              <j-select
                v-model="select_type"
                :disabled="false"
                :options="options_search"
                placeholder=""/>
            </b-col>
            <b-col cols="4">
              <b-form
                @keyup.enter="filter(filter_data)">
                <b-form-group>
                  <j-input
                    v-model="filter_data"/>
                </b-form-group>
              </b-form>
            </b-col>
            <b-col
              class="p--1 p__l--2 p__l--25"
              style="align-self: center;"
              cols="5">
              <b-button
                class="btn blue outline"
                @click="filter(filter_data)"> <i class="fa fa-search bold" /> Search </b-button>
            </b-col>
          </b-row>
        </div>

        <div
          slot="created_date"
          slot-scope="props">
          {{ props.row.created_date | moment("DD/MM/YYYY") }}
        </div>

        <div
          slot="division"
          slot-scope="props">

          <div v-if="props.row.divisions[0]">
            <div
              v-for="(division, index) in props.row.divisions"
              :key="index">
              {{ division.name }}
            </div>
          </div>

        </div>

        <div
          slot="email"
          slot-scope="props">
          <div v-if="typeof props.row.user !== 'undefined'">
            {{ props.row.user.email }}
          </div>
        </div>

        <div
          slot="user_name"
          slot-scope="props">
          <div v-if="typeof props.row.user !== 'undefined'">
            {{ props.row.user.profile.first_name }}
            {{ props.row.user.profile.last_name }}
          </div>
        </div>

      </v-server-table>
    </div>

    <div
      v-if="!load_first">
      <jLoadingTable/>
    </div>

    <j-progress-screen
      ref="ImportFile"
      :data-kirim="data_import"
      :url-api="url"
      :headers="headers"
      @finish="uploadDone"/>
    <div>
      <b-modal
        ref="modalPosition"
        class="mod-form"
        centered>
        <h3 class="float-to-center c--red">
          Error Data
        </h3>
        <v-client-table
          v-if="error_data[0]"
          :data="error_data"
          :columns="column"
          :options="option"
          class="jTable">

          <div
            slot="ERROR"
            slot-scope="props">
            <div
              v-for="(err, index) in props.row.ERROR"
              :key="index">
              {{ err }}
            </div>
          </div>

        </v-client-table>
        <download-excel
          :data="error_data"
          class="float-to-right m__t--30"
          name="Position-data-error.xls">
          <b-button class="btn green">
            Export Error
          </b-button>
        </download-excel>
      </b-modal>
    </div>

  </div>
</template>

<script>
import { jTitle } from '@/mixins/components/organigram/jTitle';
import jLoadingTable from '@/components/skeleton-loading/jLoadingTable';
import { mapActions, mapState } from 'vuex';
import jProgressScreen from '@/components/progress-screen/jProgressScreen';
import Vue from 'vue';
import jExportTitle from './jExportTitle';


export default {
  components: { jLoadingTable, jProgressScreen, jExportTitle },
  mixins: [jTitle],
  data() {
    return {
      select_type: 'position_id',
      rowing: null,
      options: {
        icon: 'fa-user',
        main_title: 'POSITION',
        opt_title: '',
      },
      data_import: [],
      raw_data: null,
      index_position: 0,
      is_finish: false,
      list_error: [],
      error_data: [],
      is_error: false,
      column: ['POSITION_ID', 'POSITION', 'DIVISIONS', 'ERROR'],
      option: {
        filterable: false,
        perPage: 5,
        perPageValues: [5, 10, 20, 50, 100],
      },
      default_import: true,
      options_search: [
        {
          text: 'Position ID',
          value: 'position_id',
        },
        {
          text: 'Position',
          value: 'position',
        },
        {
          text: 'Name',
          value: 'name',
        },
        {
          text: 'Email',
          value: 'email',
        },
        // {
        //   text: 'Divisions',
        //   value: 'divisions',
        // },
      ],
    };
  },
  computed: {
    ...mapState('jFlows', ['divisions_list', 'users_list']),
    ...mapState('jOrganigram', ['email_id']),
    headers() {
      const token = Vue.ls.get('Token');
      const headers = {
        Authorization: `Bearer ${token}`,
      };
      return headers;
    },
    url() {
      const getEnv = 'jojoflow/organigram/createR1';
      return getEnv;
    },
    final_data_import() {
      if (this.data_import.length > 0) {
        const finalData = this.data_import.filter((thing, index, self) =>
          index === self.findIndex(t => (t.code === thing.code)),
        );
        return finalData;
      }
      return null;
    },
  },
  watch: {
    rowing(newData, oldData) {
      if (oldData) {
        this.removeRowClick(oldData);
      }
      // this.rowing = null;
    },
    email_id() {
      this.index_position += 1;
      if (this.index_position < this.raw_data.length) {
        this.list_error = [];
        this.error_data = this.raw_data;
        this.genData(this.raw_data[this.index_position], this.index_position);
      }
    },

    data_import() {
      this.index_position += 1;
      if (this.index_position < this.raw_data.length) {
        this.list_error = [];
        this.error_data = this.raw_data;
        this.genData(this.raw_data[this.index_position], this.index_position);
      } else {
        setTimeout(() => {
          this.$store.dispatch('jNotificationScreen/hideLoading', 'Please wait...');
        }, 1000);
      }
    },

  },
  created() {
    this.getListDivisions();
  },
  methods: {
    ...mapActions('jFlows', ['getListDivisions']),
    ...mapActions('jOrganigram', ['getEmailId']),

    uploadData() {
      this.$refs.ImportFile.toggleIsVisible();
    },
    refreshTable() {
      this.$refs.titleServer.getData();
    },
    nullOfRowing() {
      this.rowing = null;
    },
    // setIntervalToken() {
    //   setInterval(() => {
    //     if (!Vue.ls.get('idToken') && this.is_finish === false) {
    //       const refreshToken = {
    //         refresh_token: localStorage.getItem('refreshToken'),
    //       };
    //       this.$store.dispatch('jLogin/refreshTokenApi', refreshToken);
    //     }
    //   }, 1000);
    // },

    importData(val) {
      this.data_import = [];
      this.is_error = false;
      this.list_error = [];
      this.error_data = val;

      const keys = Object.keys(val[0]);
      if (keys[0] !== 'POSITION_ID' || keys[1] !== 'POSITION' || keys[2] !== 'EMAIL' || keys[3] !== 'DIVISIONS') {
        this.$store.dispatch('jNotificationScreen/toggleProblem', 'Please use header format correctly');
        return '';
      }

      // this.setIntervalToken();
      this.raw_data = val;
      this.genData(this.raw_data[0], 0);
      this.$store.dispatch('jNotificationScreen/showLoading', 'Please wait...');
      return '';
    },

    genData(val, indexData) {
      this.list_error[indexData] = [];
      this.$set(this.error_data[indexData], 'ERROR', []);
      const packing = {
        name: '',
        description: '',
        code: '',
        email: '',
        divisions: [],
      };

        // name validations
      const strName = val.POSITION ? val.POSITION.trim() : '';
      if (val.POSITION === undefined || strName.length === 0) {
        this.is_error = true;
        this.list_error[indexData].push('- field position is required');
        this.error_data[indexData].ERROR.push('- field position is required');
      } else {
        this.$set(packing, 'name', val.POSITION);
      }

      // code validation
      const strCode = val.POSITION_ID ? val.POSITION_ID.trim() : '';
      if (val.POSITION_ID === undefined || strCode.length === 0) {
        this.is_error = true;
        this.list_error[indexData].push('- field position id is required');
        this.error_data[indexData].ERROR.push('- field position id is required');
      } else {
        this.$set(packing, 'code', val.POSITION_ID);
      }

      // get divisions
      let trimDiv;
      if (val.DIVISIONS.indexOf(',') > -1) {
        const listDivision = val.DIVISIONS.split(',');
        trimDiv = listDivision.map(data => data.trim());
      } else {
        trimDiv = [val.DIVISIONS.trim()];
      }
      const divisionsData = [];
      trimDiv.forEach((data) => {
        if (data.length !== 0) {
          const matchDiv = this.divisions_list.filter(each => each.name === data);

          if (matchDiv[0] !== undefined) {
            divisionsData.push(matchDiv[0]);
          } else {
            this.is_error = true;
            this.list_error[indexData].push(`- division ${val.DIVISIONS.toLowerCase()} is not listed, please try another`);
            this.error_data[indexData].ERROR.push(`- division ${val.DIVISIONS.toLowerCase()} is not listed, please try another`);
          }
        }
      });

      const divisionID = divisionsData.map(data => data.id);

      packing.divisions = divisionID;


      // get user_company_id
      const strEmail = typeof val.EMAIL !== 'undefined' ? val.EMAIL.trim() : '';
      packing.email = strEmail;
      // if (strEmail.length !== 0) {
      //   packing.email = strEmail;
      //   // const dataUser = await this.getEmailId({ email: val.EMAIL });
      //   // if (dataUser === null) {
      //   //   this.is_error = true;
      // eslint-disable-next-line
      //   //   this.list_error[indexData].push(`- email ${val.EMAIL.toLowerCase()} is not listed, please try another`);
      // eslint-disable-next-line
      //   //   this.error_data[indexData].ERROR.push(`- email ${val.EMAIL.toLowerCase()} is not listed, please try another`);
      //   } else {
      //     packing.user_company_id = dataUser.data.id;
      //   }
      // } else {
      //   this.index_position += 1;
      //   if (this.index_position < this.raw_data.length) {
      //     this.genData(this.raw_data[this.index_position]);
      //   }
      // }

      this.data_import.push(packing);
      return '';
    },
    uploadDone() {
      this.raw_data = [];
      this.is_finish = true;
      this.index_position = 0;
      this.refreshTable();
      this.data_import = [];
      this.default_import = false;
      setTimeout(() => {
        this.default_import = true;
      }, 100);
      this.$emit('doneUpload');
    },
  },
};
</script>



