/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapState, mapActions } from 'vuex';

export const jMixOptionsFormCategory = {
  created() {
    if (this.mode === 'edit') {
      this.modeEdit();
    }
    this.getCategoryList();
    this.getLayerList();
  },
  data() {
    return {
      list_type: [
        {
          id: 1,
          name: 'Procurement',
        },
      ],
    };
  },
  computed: {
    ...mapState('jStoreCategory', ['list_category', 'layer_list']),
  },
  methods: {
    ...mapActions('jStoreCategory', ['getListCategory', 'getListLayer']),

    getCategoryList() {
      const req = {
        pagination: {
          limit: 0,
          page: 0,
          column: '',
          ascending: false,
          query: '',
          query_type: '',
        },
      };
      this.getListCategory(req);
    },

    getLayerList() {
      const req = {
        type: 1,
      };

      this.getListLayer(req);
    },
  },
};



