<script>
import { Doughnut } from 'vue-chartjs';

export default {
  extends: Doughnut,
  props: {
    data: {
      type: Object,
      default: () => {},
    },
  },
  mounted() {
    this.renderChart(this.data, { responsive: true, maintainAspectRatio: false });
  },
};
</script>



