<template>
  <div class="jNewForm jNewReport">
    <div class="close-icon float-to-right">
      <router-link
        to="/jojoform/form-report">
        <i class="fa fa-times"/>
      </router-link>
    </div>

    <div class="float-to-center">
      <h2>Create Report</h2>
    </div>

    <b-row>
      <b-col
        cols="1"
        sm="2" />
      <b-col>
        <div class="form-border">
          <div class="float-to-right">
            Created Date:
            {{ new Date() | moment("DD/MM/YYYY") }}
          </div>

          <b-form-group>
            <label>Select Chart</label>
            <b-form-radio-group
              id="radios2"
              v-model="selected_chart"
              name="radioSubComponent"
              class="jFormGroup">
              <div>
                <div
                  v-for="(chart, index) in list_chart"
                  :key="index"
                  class="inline--block">
                  <div>
                    <b-img :src="chart.src"/>
                  </div>
                  <br>
                  <div class="float-to-center">
                    <b-form-radio :value="chart.value"/>
                  </div>
                  <br>
                  <div class="float-to-center">
                    {{ chart.name }}
                  </div>
                </div>
              </div>

            </b-form-radio-group>
          </b-form-group>

          <b-form-group>
            <label>Report Name *</label>
            <j-input
              placeholder="Add report name"/>
          </b-form-group>

          <b-row>
            <b-col
              cols="12"
              md="5">
              <b-form-group>
                <!-- options=""
                v-model="" -->
                <label>Select Fields</label>
                <v-select

                  class="v-singleselect"
                  label="name"
                  placeholder="Select Field"/>
              </b-form-group>
            </b-col>
            <b-col
              cols="12"
              md="5">
              <b-form-group>
                <!-- options=""
                v-model="" -->
                <label>Based On</label>
                <v-select

                  class="v-singleselect"
                  label="name"
                  placeholder="Based On"/>
              </b-form-group>

            </b-col>
            <b-col/>
          </b-row>
          <b-row>
            <b-col
              cols="12"
              md="5">
              <b-form-group>
                <!-- options=""
                v-model="" -->
                <label>Select Operation</label>
                <v-select

                  class="v-singleselect"
                  label="name"
                  placeholder="Select Operation"/>
              </b-form-group>

            </b-col>
            <b-col/>


          </b-row>

          <div class="float-to-right">
            <b-button
              class="btn blue"
              @click="createChart">CREATE</b-button>
          </div>

        </div>
      </b-col>
      <b-col
        cols="1"
        sm="2"/>
    </b-row>


    <b-row v-if="show_chart && selected_chart">
      <b-col
        cols="1"
        sm="2" />
      <b-col
        cols="10"
        sm="8">
        <div class="form-border">
          <ListChart :list="list" />
        </div>
        <br>
        <div class="float-to-right">
          <b-button
            class="btn blue"
            @click="pinChart">PIN CHART
          </b-button>
        </div>
        <br>
      </b-col>
      <b-col
        cols="1"
        sm="2"/>
    </b-row>

  </div>
</template>

<script>
import vSelect from 'vue-select';
import ListChart from '../../my-board/jListChart';

const linkurl = 'https://jojo-receipt.s3.amazonaws.com/dev-receipts/';

export default {
  components: {
    vSelect,
    ListChart,
  },
  data() {
    return {
      show_chart: false,
      selected_chart: '',
      list_chart: [
        { src: `${linkurl}0ebbefc0ab0abfc32912c4b4f818d9b9_Bar_Chart.png`, name: 'Bar-Chart', value: 'BAR' },
        { src: `${linkurl}3ef11b21cf3114095c8cec2ced970d03_Line_Art.png`, name: 'Line-Chart', value: 'LINE' },
        { src: `${linkurl}5a2c69185a2e4408565a844e27f0d7f6_Donut_Chart.png`, name: 'Donut-Chart', value: 'DOUGHNUT' },
        { src: `${linkurl}259d9ba2a09f050db0a3311333fdcd23_Pie_Chart.png`, name: 'Pie-Chart', value: 'PIE' },
      ],
    };
  },
  computed: {
    list() {
      return {
        name: 'heroku',
        _id: 12,
        viewChart: {
          labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
          datasets: [
            {
              label: 'GitHub Commits',
              backgroundColor: '#42a5ce',
              data: [40, 20, 12, 39, 10, 40, 39, 80, 40, 20, 12, 11],
            },
          ],
        },
        chart: this.selected_chart,
      };
    },
  },
  methods: {
    createChart() {
      this.show_chart = true;
    },
    pinChart() {
      this.$router.replace('/jojoform/my-board');
    },
  },
};
</script>

<style lang="scss">
</style>



