/* eslint-disable no-shadow,consistent-return,no-param-reassign */
import apiNumberFormat from '@/services/jApiNumberFormat';
import router from '@/router';

const state = {
  // Set default state(s) here
  number_format_data: {
    detail_number_format: null,

    list_number_format: null,

    number_format_deleted: 0,
  },
  list_type_form: [],

};

const getters = {
  // Change value of state(s) with getter
};

const mutations = {

  showListTypeForm(state, data) {
    const arrType = data.data.data.map(data => ({
      id: data.type,
      name: data.name,
    }));
    state.list_type_form = arrType;
  },

  showDetailNumberFormat(state, data) {
    state.number_format_data.detail_number_format = data.data;
  },

  numberFormatList() {
    router.push('/number-format');
  },

  numberFormatDeleted(state) {
    state.number_format_data.number_format_deleted += 1;
  },

  initNumberFormatDelete(state) {
    state.number_format_data.number_format_deleted = 0;
  },

  showListNumberFormat(state, data) {
    state.number_format_data.list_number_format = data.data;
  },

};

const actions = {

  async getTypeListForm({ commit, dispatch }, rootId) {
    try {
      const listNumberFormat = await apiNumberFormat.getTypeList(rootId);
      commit('showListTypeForm', listNumberFormat);
    } catch (err) {
      dispatch('jStoreNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
    }
  },

  async getNumberFormatList({ commit, dispatch }, data) {
    try {
      const listNumberFormat = await apiNumberFormat.getNumberFormatList(data);
      commit('showListNumberFormat', listNumberFormat);
    } catch (err) {
      dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },

  async getNumberFormatDetail({ commit, dispatch }, numberFormatId) {
    try {
      const detailNumberFormat =
      await apiNumberFormat.getNumberFormatDetail(numberFormatId);
      commit('showDetailNumberFormat', detailNumberFormat);
    } catch (err) {
      dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },

  async updateNumberFormatDetail({ dispatch, commit }, numberFormatData) {
    dispatch('jNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      await apiNumberFormat.updateNumberFormatDetail(numberFormatData);
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      dispatch('jNotificationScreen/toggleSuccess', 'Number Format Edited', { root: true });
      commit('numberFormatList');
    } catch (err) {
      if (err.response.data.errors) {
        dispatch('jNotificationScreen/hideLoading', '', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
      } else {
        dispatch('jNotificationScreen/hideLoading', '', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
      }
    }
  },

  async createNumberFormat({ commit, dispatch }, newNumberFormatData) {
    dispatch('jNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      await apiNumberFormat.createNumberFormat(newNumberFormatData);
      dispatch('jNotificationScreen/hideLoading', 'Please wait...', { root: true });
      dispatch('jNotificationScreen/toggleSuccess', 'Number Format Created', { root: true });
      commit('numberFormatList');
    } catch (err) {
      if (err.response.data.errors) {
        dispatch('jNotificationScreen/hideLoading', 'Please wait...', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
      } else {
        dispatch('jNotificationScreen/hideLoading', 'Please wait...', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
      }
    }
  },

  async deleteNumberFormat({ commit, dispatch }, numberFormatId) {
    dispatch('jNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      await apiNumberFormat.deleteNumberFormat(numberFormatId);
      dispatch('jNotificationScreen/hideLoading', 'Please wait...', { root: true });
      dispatch('jNotificationScreen/toggleSuccess', 'Number Format Deleted', { root: true });
      commit('numberFormatDeleted');
      commit('numberFormatList');
    } catch (err) {
      if (err.response.data.errors) {
        dispatch('jNotificationScreen/hideLoading', 'Please wait...', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
      } else {
        dispatch('jNotificationScreen/hideLoading', 'Please wait...', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
      }
    }
  },

  async multiDeleteNumberFormat({ commit, dispatch }, numberFormatId) {
    try {
      await apiNumberFormat.deleteNumberFormat(numberFormatId);
      commit('numberFormatDelete');
    } catch (err) {
      if (err.response.data.errors) {
        dispatch('jNotificationScreen/hideLoading', '', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.errors[0].message, { root: true });
      } else {
        dispatch('jNotificationScreen/hideLoading', '', { root: true });
        dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
      }
    }
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};



