import jApi from './jApi';

export default {
  getTypeList(req) {
    const api = jApi.generateApi();
    return api.post('numbering/type/list', req)
      .then(res => res);
  },

  getNumberFormatList(req) {
    const api = jApi.generateApi();
    return api.post('numbering/list', req)
      .then(res => res);
  },

  createNumberFormat(newNumberFormatData) {
    const api = jApi.generateApi();
    return api.post('numbering/create', newNumberFormatData)
      .then(res => res.data);
  },

  getNumberFormatDetail(numberFormatId) {
    const api = jApi.generateApi();
    return api.post('numbering/detail', numberFormatId)
      .then(res => res.data);
  },

  updateNumberFormatDetail(numberFormatData) {
    const api = jApi.generateApi();
    return api.post('numbering/update', numberFormatData)
      .then(res => res.data);
  },

  deleteNumberFormat(numberFormatId) {
    const api = jApi.generateApi();
    return api.post('numbering/delete', numberFormatId)
      .then(res => res.data);
  },
};



