<template>
  <jFloatingPrompt
    v-if="detail_division"
    ref="divisionFloating">
    <b-row class="jobTitleFloat">
      <b-col>
        <b-form-group :class="{'error-input' : $v.division.name.$error}">
          <label>Name*</label>
          <j-input
            v-model="$v.division.name.$model"
            :max-length="50"
            placeholder="Job Title Name"/>
        </b-form-group>
        <b-form-group>
          <label>Member(s) Total</label>
          <j-input
            v-model="division.total_member"
            :disabled="true"
            placeholder="Members Total"/>
        </b-form-group>
      </b-col>
      <b-col>
        <b-form-group :class="{'error-input' : $v.division.code.$error}">
          <label>Code*</label>
          <j-input
            v-model="$v.division.code.$model"
            :max-length="42"
            placeholder="Code"/>
        </b-form-group>
        <b-form-group :class="{'error-input' : $v.division.description.$error}">
          <label>Description</label>
          <j-input
            v-model="$v.division.description.$model"
            :max-length="300"
            placeholder="Division(s)"/>
        </b-form-group>
      </b-col>
      <b-col cols="4">
        <!-- button UPDATE or DELETE -->
        <div class="wrap-button-flow">
          <b-button
            class="btn blue"
            @click="editDivision">
            UPDATE
          </b-button>
          <b-button
            class="btn blue outline"
            @click="deleteDivision">
            DELETE
          </b-button>
          <i
            class="fa fa-times"
            @click="hideDivisionFloating"/>
        </div>
      </b-col>
    </b-row>

    <!-- <jModalSure
      ref="modalSure"
      :data="division.name"
      @sure="deleteDivision">
      Delete Division
    </jModalSure> -->
  </jFloatingprompt>

</template>

<script>
import { mapActions, mapState } from 'vuex';
import jFloatingPrompt from '@/components/new-floating-templete/jFloatingPrompt';
import jModalSure from '@/components/modal/jModalSure';
import { required } from 'vuelidate/lib/validators';

export default {
  components: {
    jFloatingPrompt,
    jModalSure,
  },
  data() {
    return {
      division: {
        code: '',
        description: '',
        total_member: '',
        name: '',
      },
    };
  },
  validations: {
    division: {
      name: { required },
      code: { },
      description: { },
    },
  },
  computed: {
    ...mapState('jOrganigram', ['detail_division']),
    delete_division() {
      return {
        id: this.division.id,
      };
    },
    update_division() {
      return {
        id: this.division.id,
        name: this.division.name,
        code: this.division.code,
        description: this.division.description,
      };
    },
  },
  watch: {
    detail_division(newVal) {
      this.division = newVal;
    },
  },
  created() {
    if (this.$route.params.idDiv) {
      this.getDetailDivision({ id: Number(this.$route.params.idDiv) });
    }
  },
  methods: {
    ...mapActions('jOrganigram', ['removeDivision', 'updateDivision', 'getDetailDivision']),
    deleteDivision() {
      const text = `You want to delete "${this.division.name}"`;

      this.$swal({
        title: 'Are you sure?',
        text,
        type: 'question',
        showCancelButton: true,
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes',
      }).then((result) => {
        if (result.value) {
          this.removeDivision(this.delete_division);
          this.$emit('deleteDivision');
        }
      });
    },
    hideDivisionFloating() {
      this.$refs.divisionFloating.hide();
      setTimeout(() => {
        this.$router.replace('/organigram');
      }, 200);
    },
    editDivision() {
      const text = `You want to update "${this.division.name}"`;
      this.$v.division.$touch();
      if (!this.$v.division.$error) {
        this.$swal({
          title: 'Are you sure?',
          text,
          type: 'question',
          showCancelButton: true,
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes',
        }).then((result) => {
          if (result.value) {
            this.updateDivision(this.update_division);
          }
        });
      }
    },
  },
};
</script>

<style lang="scss">

</style>



