<template>
  <div>
    <div class="custom-table-search">
      <v-server-table
        v-show="load_first"
        ref="journeyModeList"
        :columns="columns"
        :options="options"
        class="jTable"
        @loading="loading"
        @loaded="loaded">
        <div
          slot="beforeFilter"
          class="custom-search">
          <b-row>
            <b-col
              cols="4"
              class="option">
              <j-select
                v-model="select_type"
                :disabled="false"
                :options="options_search"/>
            </b-col>
            <b-col
              cols="4"
              class="p__l--1">
              <b-form
                @keypress.13.prevent="filter(filter_data)">
                <b-form-group>
                  <j-input
                    v-model="filter_data"
                    placeholder="search"/>
                </b-form-group>
              </b-form>
            </b-col>
            <b-col
              cols="4"
              class="option">
              <b-button
                class="btn blue outline show-btn pull-right"
                @click="filter(filter_data)"> <i class="fa fa-search bold" />
                Search
              </b-button>
            </b-col>
          </b-row>
        </div>
        <div
          slot="checkbox"
          slot-scope="props"
          style="width: 5px"
          class="jTableReport">
          <b-form-group>
            <b-form-checkbox-group v-model="selected_rows">
              <b-form-checkbox :value="props.row"/>
            </b-form-checkbox-group>
          </b-form-group>
        </div>
        <div
          slot="name"
          slot-scope="props"
          @click="$router.replace(`/category/edit/${props.row.id}`)">
          {{ props.row.name }}
        </div>
        <div
          slot="icon"
          slot-scope="props"
          @click="$router.replace(`/category/edit/${props.row.id}`)"
        >
          <img
            :src="require(`@jojocoders/jojonomic-ui/src/Public/Assets/icon/${props.row.icon}.png`)"
            style="width: 45px;">
        </div>
        <div
          slot="description"
          slot-scope="props"
          @click="$router.replace(`/category/edit/${props.row.id}`)">
          {{ props.row.description }}
        </div>
      </v-server-table>
      <div>
        <jLoadingTable v-if="!load_first"/>
      </div>
      <jDeleteCategory
        ref="floating"
        :selected-rows="selected_rows"
        @finish="categoryDeleted" />
    </div>
  </div>
</template>

<script>
import { jMixPaginationCategory } from '@/mixins/paginations/category/jMixPaginationCategory';
import jLoadingTable from '@/components/skeleton-loading/jLoadingTable';
import jDeleteCategory from '@/views/category/delete/jCategoryDelete';

export default {
  components: {
    jLoadingTable,
    jDeleteCategory,
  },
  mixins: [
    jMixPaginationCategory,
  ],
  data() {
    return {
    };
  },
};
</script>



