import auth from '@/middleware/auth';
import jProfile from '@/views/profile/jProfilePage';

const jRouterProfile = [
  {
    path: '/profile',
    name: 'Profile',
    component: jProfile,
    meta: {
      title: 'Profile',
      layout: 'jWrapper',
    },
  },
];

jRouterProfile.map((dataPath) => {
  const eachData = dataPath;
  eachData.meta.middleware = auth;
  return eachData;
});


export default jRouterProfile;



