<template>
  <div>
    <j-title-header :options="title"/>
    <jCategoryForm
      ref="form"
      mode="edit"
      @data="getData"/>
    <b-row>
      <b-col>
        <div class="float-to-left">
          <b-button
            class="btn blue m__r--15"
            @click="saveCreateCategory">Update</b-button>
          <b-button
            class="btn grey"
            @click="$router.replace('/category/list')">Cancel</b-button>
        </div>
      </b-col>
      <b-col>
        <div class="float-to-right">
          <b-button
            class="btn red"
            @click="deleteData">Delete</b-button>
        </div>
      </b-col>
    </b-row>
    <br>

  </div>
</template>

<script>
import { jMixModelEditCategory } from '@/mixins/model/category/jMixModelEditCategory';
import { jMixModelDeleteCategory } from '@/mixins/model/category/jMixModelDeleteCategory';
import jCategoryForm from '@/components/form/category/jCategoryForm';

export default {
  components: {
    jCategoryForm,
  },
  mixins: [
    jMixModelEditCategory,
    jMixModelDeleteCategory,
  ],
  data() {
    return {
      title: {
        icon: 'fa-user',
        main_title: 'EDIT CATEGORY',
        opt_title: 'Administrator',
      },
      category_form: null,
    };
  },
  methods: {
    saveCreateCategory() {
      this.$refs.form.save();
      if (this.category_form !== null) {
        this.sendData();
      }
    },
    getData(data) {
      this.category_form = data;
    },
  },
};
</script>



