<template>
  <div>
    <v-server-table
      v-show="load_first"
      ref="numberFormatServer"
      :columns="columns"
      :options="options"
      class="jTable"
      @loading="loading"
      @loaded="loaded">

      <div
        slot="checkbox"
        slot-scope="props"
        class="jTableReport">
        <b-form-group>
          <b-form-checkbox-group v-model="selected_rows">
            <b-form-checkbox :value="props.row"/>
          </b-form-checkbox-group>
        </b-form-group>
      </div>

      <div
        slot="name"
        slot-scope="props"
        @click="$router.replace(`/number-format/edit/${props.row.id}`)">
        {{ props.row.name }}
      </div>

      <div
        slot="type"
        slot-scope="props"
        @click="$router.replace(`/number-format/edit/${props.row.id}`)">
        {{ props.row.type_string }}
      </div>

      <!-- <div
        slot="review"
        slot-scope="props"
        @click="$router.replace(`/number-format/edit/${props.row.id}`)">
        {{ props.row.pattern }}
      </div> -->

      <div
        slot="updated_date"
        slot-scope="props"
        @click="$router.replace(`/number-format/edit/${props.row.id}`)">
        {{ props.row.updated_date | moment("DD MMM, YYYY hh:mm:ss") }}
      </div>

      <div
        slot="created_date"
        slot-scope="props"
        @click="$router.replace(`/number-format/edit/${props.row.id}`)">
        {{ props.row.created_date | moment("DD MMM, YYYY hh:mm:ss") }}
      </div>

    </v-server-table>

    <div>
      <jLoadingTable v-if="!load_first"/>
    </div>

    <jDeleteNumberFormat
      ref="floating"
      :selected-rows="selected_rows"
      @finish="numberFormatDeleted" />
  </div>
</template>

<script>
import jLoadingTable from '@/components/skeleton-loading/jLoadingTable';
import { jMixPaginationNumberFormat } from '@/mixins/paginations/number-format/jMixPaginationNumberFormat';
import jDeleteNumberFormat from '@/views/number-format/delete/jDeleteNumberFormat';

export default {
  components: {
    jDeleteNumberFormat,
    jLoadingTable,
  },
  mixins: [
    jMixPaginationNumberFormat,
  ],
};
</script>



