<template>
  <div>
    <j-title-header :options="title"/>
    <b-button
      class="btn blue"
      @click="$router.replace('/category/create')">
      Create Category
    </b-button>
    <br>
    <j-category-list/>
  </div>
</template>

<script>
import jProgress from '@/components/progress-screen/jProgress';
import jCategoryList from './list/jCategoryList';

export default {
  components: {
    jProgress,
    jCategoryList,
  },
  data() {
    return {
      title: {
        icon: 'fa-building',
        main_title: 'Category',
      },
      columns: ['ID', 'NAME', 'CODE', 'DESCRIPTION', 'ICON', 'ERROR'],
      data_import: null,
      list_error: [],
      error_data: [],
      is_error: false,
      is_finish: false,
    };
  },
  computed: {
    url() {
      const getEnv = `${process.env.GATE_URL}category/create`;
      return getEnv;
    },
    headers() {
      const token = localStorage.getItem('refreshToken');
      const headers = {
        Authorization: `Bearer ${token}`,
      };
      return headers;
    },
  },
  methods: {

    reloadPage() {
      this.is_finish = true;
      this.$router.go();
    },

    uploadData() {
      this.$refs.ImportFile.toggleIsVisible();
    },
  },
};
</script>



