import jApi from './jApi';

export default {
  listFeature(req) {
    const api = jApi.generateApi();
    return api.post('costcenter/config/feature/list', req)
      .then(res => res.data);
  },

  updateFeature(req) {
    const api = jApi.generateApi();
    return api.post('costcenter/config/feature/update', req)
      .then(res => res.data);
  },

  listFeatureSales(req) {
    const api = jApi.generateApi();
    return api.get('sales/setup/company-setting/detail', req)
      .then(res => res.data);
  },

  updateFeatureSales(req) {
    const api = jApi.generateApi();
    return api.post('sales/setup/company-setting/update', req)
      .then(res => res.data);
  },
};



