<template>
  <div>
    <div v-show="title_data.length < 1">
      <b-button
        class="btn green"
        @click="exportPosition">
        Export Positions
      </b-button>
    </div>
    <div v-show="title_data.length > 1">
      <download-excel
        :data="title_data"
        name="Position-Data.xls">
        <b-button
          ref="exportPosition"
          class="btn green">
          Export Positions
        </b-button>
      </download-excel>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  data() {
    return {
      title_data: [],
      page: 1,
    };
  },

  computed: {
    ...mapState('jOrganigram', ['all_title_list', 'total_length_title']),
  },

  watch: {
    all_title_list(Val) {
      this.page += 1;
      const sortData = Val.map((newVal) => {
        const div = newVal.divisions.map(datDiv => datDiv.name);
        let nama;
        if (typeof newVal.user !== 'undefined') {
          const firstName = typeof newVal.user.profile !== 'undefined' ?
            newVal.user.profile.first_name : '';
          const lastName = typeof newVal.user.profile !== 'undefined' ?
            newVal.user.profile.last_name : '';
          nama = `${firstName} ${lastName}`;
        } else {
          nama = '';
        }
        const dataPosition = {
          ID: newVal.id,
          POSITION_ID: newVal.code,
          POSITION: newVal.name,
          NAME: nama,
          EMAIL: typeof newVal.user !== 'undefined' ? newVal.user.email : '',
          DIVISIONS: div,
          CREATED_DATE: newVal.created_date,
          UPDATED_DATE: typeof newVal.updated_date !== 'undefined' ? newVal.updated_date : '',
          CREATED_BY: typeof newVal.created_by !== 'undefined' ? newVal.created_by.email : '',
          UPDATED_BY: typeof newVal.updated_by !== 'undefined' ? newVal.updated_by.email : '',
        };
        return dataPosition;
      });
      this.title_data.push(...sortData);
      const requestAll = {
        pagination: {
          limit: 50,
          page: this.page,
          column: 'id',
          ascending: true,
          query: '',
        },
        export: 'yes',
      };
      if (this.title_data.length < this.total_length_title) {
        this.$store.dispatch('jOrganigram/getAllTitle', requestAll);
      } else {
        this.$store.dispatch('jNotificationScreen/hideLoading', 'Please wait...');
        this.$refs.exportPosition.click();
        this.title_data = [];
        this.page = 1;
      }
    },

  },

  methods: {
    exportPosition() {
      const requestAll = {
        pagination: {
          limit: 50,
          page: 1,
          column: 'id',
          ascending: true,
          query: '',
        },
      };
      this.$store.dispatch('jNotificationScreen/showLoading', 'Please wait...');
      this.$store.dispatch('jOrganigram/getAllTitle', requestAll);
    },
  },

  // created() {
  //   const requestAll = {
  //     pagination: {
  //       limit: 5,
  //       page: 1,
  //       column: '',
  //       ascending: false,
  //       query: '',
  //     },
  //   };
  //   this.$store.dispatch('jOrganigram/getAllTitle', requestAll);
  // },
};
</script>



