/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions } from 'vuex';

export const jMixModelEditProjectPeriod = {
  computed: {
    generateData() {
      if (this.period !== null) {
        const genData = {
          period: {
            id: Number(this.$route.params.id),
            name: this.period.name,
            description: this.period.description,
            start_date: this.period.start_date,
            end_date: this.period.end_date,
            backdate_settlement: null,
            type: 2,
            blackout_date: {
              start_date: '2019-01-19',
              end_date: '2019-01-29',
            },
          },
        };
        return genData;
      }
      return null;
    },
  },
  methods: {
    ...mapActions('jStoreProject', ['updatePeriod']),
    sendData() {
      if (this.generateData !== null) {
        const text = `You want to update period ${this.generateData.period.name}`;
        this.$swal({
          title: 'Are you sure?',
          text,
          type: 'question',
          showCancelButton: true,
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes',
        }).then((result) => {
          if (result.value) {
            this.updatePeriod(this.generateData);
          }
        });
      }
    },
  },
};



