/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';
import apiSales from '@/services/jApiSales';
import jReusableFunction from '@/mixins/paginations/jReusableFunction';

export const jMixPaginationSales = {
  data() {
    return {
      data_table: [],
      selected_rows: [],
      load_first: false,
      columns: ['checkbox', 'name', 'type', 'review', 'updated_date', 'created_date'],
      options: {
        headings: {
          name: 'NAME',
          type: 'TYPE',
          review: 'REVIEW',
          updated_date: 'UPDATED DATE',
          created_date: 'CREATED DATE',
          checkbox(h) {
            const objThis = this;
            return jReusableFunction.checkbox(h, objThis);
          },
        },
        texts: {
          filterPlaceholder: 'Search',
          limit: 'Rows per page',
        },
        sortIcon: {
          base: 'fa',
          up: 'fa-chevron-up',
          down: 'fa-chevron-down',
          is: 'fa-sort',
        },
        debounce: 1000,
        filterable: true,
        sortable: ['updated_date'],
        perPage: 5,
        perPageValues: [5, 10, 20, 50, 100],
        requestAdapter(data) {
          return {
            pagination: {
              limit: data.limit,
              page: data.page,
              column: data.orderBy ? data.orderBy : '',
              ascending: !!data.ascending,
              query: data.query ? data.query : '',
            },
            type: 0,
          };
        },
        requestFunction(data) {
          const getListNumberFormat = apiSales.getSalesList(data);
          return getListNumberFormat.catch((e) => {
            this.$store.dispatch('jNotificationScreen/toggleProblem', e.response.data.errors[0].message).bind(this);
          });
        },
        responseAdapter({ data }) {
          return {
            data: data.data ? data.data : [],
            count: data.length ? data.length : 0,
          };
        },
        templates: {
          review(h, row) {
            // eslint-disable-next-line
            var previewNumber = '';
            // eslint-disable-next-line
            var arrPattern = row.pattern.split('|');

            if (arrPattern.length > 0) {
              // eslint-disable-next-line
              for (var i in arrPattern) {
                const code = arrPattern[i];
                if (code === '.') {
                  previewNumber = previewNumber.concat('.');
                } else if (code === '/') {
                  previewNumber = previewNumber.concat('/');
                } else if (code === '-') {
                  previewNumber = previewNumber.concat('-');
                } else if (code === 'date') {
                  previewNumber = previewNumber.concat('29');
                } else if (code === 'month01') {
                  previewNumber = previewNumber.concat('12');
                } else if (code === 'month02') {
                  previewNumber = previewNumber.concat('DEC');
                } else if (code === 'year01') {
                  previewNumber = previewNumber.concat('17');
                } else if (code === 'year02') {
                  previewNumber = previewNumber.concat('2017');
                } else if (code === 'id') {
                  previewNumber = previewNumber.concat('003');
                } else {
                  previewNumber = previewNumber.concat(code.substr(1, code.length - 2));
                }
              }
            } else {
              previewNumber = '';
            }
            return previewNumber;
          },
        },
      },
    };
  },
  computed: {
    // ...mapState('jStoreProject', ['project_data']),
  },
  methods: {
    ...mapActions('jNotificationScreen', ['toggleProblem', 'showLoading', 'hideLoading']),

    loading() {
      if (this.load_first) {
        this.showLoading();
      }
    },

    loaded(data) {
      this.data_table = data.data.data;
      if (this.load_first) {
        this.hideLoading();
      } else {
        this.load_first = !this.load_first;
      }
    },

    refreshTable() {
      this.$refs.numberFormatServer.getData();
    },

    numberFormatDeleted() {
      this.refreshTable();
      this.selected_rows = [];
    },

  },
  watch: {
    selected_rows() {
      if (this.selected_rows.length === 0) {
        this.$refs.floating.hide();
      } else {
        this.$refs.floating.show();
      }
    },
  },
};



