<template>
  <div>
    <j-title-header :options="title"/>

    <j-number-format-form
      ref="form"
      mode="edit"
      @data="getData"/>

    <b-row>
      <b-col>
        <div class="float-to-left m__t--50">
          <b-button
            class="btn blue m__r--15"
            @click="saveEditNumberFormat">Save</b-button>
          <b-button
            class="btn grey"
            @click="$router.replace('/number-format')">Cancel</b-button>
        </div>
      </b-col>

      <b-col>
        <div class="float-to-right m__t--50">
          <b-button
            class="btn red"
            @click="deleteData">Delete</b-button>
        </div>
      </b-col>

    </b-row>

    <br>
  </div>
</template>

<script>
import jNumberFormatForm from '@/components/number-format-form/jNumberFormatForm';
import { jMixModelEditNumberFormat } from '@/mixins/model/number-format/jMixModelEditNumberFormat';
import { jMixModeEditNumberFormat } from '@/mixins/mode-form/number-format/jMixModeEditNumberFormat';
import { jMixModelDeleteNumberFormat } from '@/mixins/model/number-format/jMixModelDeleteNumberFormat';

export default {
  components: {
    jNumberFormatForm,
  },
  mixins: [
    jMixModelEditNumberFormat,
    jMixModeEditNumberFormat,
    jMixModelDeleteNumberFormat,
  ],
  data() {
    return {
      number_format_form: null,
      title: {
        icon: 'fa-user',
        main_title: 'EDIT NUMBER FORMAT',
      },
    };
  },
  methods: {

    saveEditNumberFormat() {
      this.$refs.form.save();
      if (this.number_format_form !== null) {
        this.sendData();
      }
    },

    getData(data) {
      this.number_format_form = data;
    },
  },
};
</script>



