/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapState, mapActions } from 'vuex';

export const jMixModeDetailFeatureSales = {
  data() {
    return {
      is_first: true,
    };
  },
  computed: {
    ...mapState('jStoreFeature', ['jojo_sales']),
  },
  watch: {
    'jojo_sales.list_company_setting'(val) {
      const listFeatureSales = this.generateData(val);
      this.set_jojosales = listFeatureSales;
    },
    'jojo_sales.update_res'() {
      this.getList();
    },

    'set_jojosales.lock_product'() {
      this.setSalesConfiguration();
    },

    'set_jojosales.lock_lead_pre_activity'() {
      this.setSalesConfiguration();
    },
    'set_jojosales.lock_lead_opportunity'() {
      this.setSalesConfiguration();
    },
  },
  created() {
    this.getList();
  },
  methods: {
    ...mapActions('jStoreFeature', ['getListFeatureSales', 'updateFeatureSales']),
    getList() {
      this.getListFeatureSales();
    },
    generateData(val) {
      const settingSales = {
        lock_product: val[0].lock_product.status,
        lock_lead_pre_activity: val[1].lock_lead_pre_activity.status,
        lock_lead_opportunity: val[2].lock_lead_opportunity.status,
        result_reminder: {
          status: val[3].result_reminder.status,
          value: val[3].result_reminder.status ? val[3].result_reminder.value : '',
          type: val[3].result_reminder.status ? val[3].result_reminder.type : 1,
        },
      };
      return settingSales;
    },
    setSalesConfiguration() {
      const val = this.set_jojosales;
      const req = {
        lock_product: {
          status: val.lock_product,
          value: 0,
          type: 0,
        },
        lock_lead_pre_activity: {
          status: val.lock_lead_pre_activity,
          value: 0,
          type: 0,
        },
        lock_lead_opportunity: {
          status: val.lock_lead_opportunity,
          value: 0,
          type: 0,
        },
        result_reminder: {
          status: val.result_reminder.value && val.result_reminder.type
            ? val.result_reminder.status : false,
          value: val.result_reminder.value && val.result_reminder.type
            ? val.result_reminder.value : 0,
          type: val.result_reminder.value && val.result_reminder.type
            ? val.result_reminder.type : 0,
        },
      };

      if (this.is_first) {
        setTimeout(() => {
          this.is_first = false;
        }, 100);
      } else {
        this.updateFeatureSales(req);
      }
    },
    setAutoIncrement(val) {
      const req = {
        company_features: [
          {
            setting: 1,
            status : val ? 1 : 0,
            value: this.set_cost_center.percentage,
          },
        ],
      };
      this.updateFeature(req);
    },
  },
};



