/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions } from 'vuex';

export const jMixModelCreateProject = {
  computed: {
    generateData() {
      if (this.project !== null) {
        // sort position by
        let organigrams;
        if (this.project.organigrams !== null) {
          organigrams = this.project.organigrams.map(data => data.id);
        } else {
          organigrams = [];
        }

        const genData = {
          name: this.project.name,
          code: this.project.code,
          period_id: this.project.period ? this.project.period.id : '',
          description: this.project.description ? this.project.description : '',
          organigrams,
          division_id: this.project.division ? this.project.division.id : null,
          company_users: [],
        };
        return genData;
      }
      return null;
    },
  },
  methods: {
    ...mapActions('jStoreProject', ['createNewProject']),
    sendData() {
      if (this.generateData !== null) {
        this.createNewProject(this.generateData);
      }
    },
  },
};



