<template>
  <div
    id="login-html"
    class="login-html">
    <div v-if="!is_cimb">
      <a class="logo-cimb"/>
    </div>
    <div class="login-box">
      <b-link
        v-if="is_cimb"
        href="https://jojonomic.com/"
        target="_blank">
        <b-img :src="logo_company"/>
      </b-link>
      <b-form
        @reset="onReset"
        @keyup.enter="onSubmit">
        <b-form-group>
          <b-form-input
            id="exampleInput1"
            :class="{'error-input' : $v.form.email.$error}"
            :error-message="null"
            :placeholder="placeholder"
            v-model="$v.form.email.$model"
            type="email"/>
        </b-form-group>
        <b-form-group id="exampleInputGroup2">
          <b-form-input
            id="exampleInput2"
            :type="password_id"
            :error-message="null"
            :class="{'error-input' : $v.form.password.$error}"
            v-model="$v.form.password.$model"
            placeholder="Password"/>
        </b-form-group>
        <b-form-checkbox v-model="show_pass">show password</b-form-checkbox>
        <b-row>
          <b-col
            class="btn-left">
            <b-button
              :class="{'disabled': $v.form.$invalid}"
              variant="primary"
              @click="onSubmit">
              Login
            </b-button>
          </b-col>
          <b-col
            class="btn-right">
            <b-button
              type="reset"
              variant="danger">
              Register
            </b-button>
          </b-col>
        </b-row>
        <b-link
          href="https://jojonomic.com/"
          target="_blank"
          class="forgot_password">
          Forgot Password?
        </b-link>
      </b-form>
    </div>
  </div>
</template>

<script>

import { required, minLength } from 'vuelidate/lib/validators';
import { mapActions } from 'vuex';
import customize from '@/utils/customize/company';

export default {
  name: 'JLogin',
  data() {
    return {
      form: {
        email: '',
        password: '',
      },
      show_pass: false,
    };
  },
  validations: {
    form: {
      email: {
        required,
      },
      password: {
        required,
        minLength: minLength(5),
      },
    },
  },
  computed: {
    process_env() {
      return process.env.COMPANY ? process.env.COMPANY.toUpperCase() : 'JOJONOMIC';
    },

    is_cimb() {
      if (this.process_env === 'CIMB') {
        return false;
      }
      return true;
    },

    placeholder() {
      return customize[this.process_env].placeholder;
    },

    password_id() {
      if (this.show_pass) {
        return 'text';
      }
      return 'password';
    },

    logo_company() {
      const logo = customize[this.process_env].logo;
      const pathFolder = this.process_env.toLowerCase();
      // eslint-disable-next-line
      return require(`@/assets/logo/${pathFolder}/${logo}`);
    },
  },
  methods: {
    ...mapActions('jLogin', ['loginAccount']),
    onSubmit() {
      this.$v.form.$touch();
      // eslint-disable-next-line
      if (!this.form.email.match(/^([a-zA-Z0-9_\-\.]+)@([a-zA-Z0-9_\-\.]+)\.([a-zA-Z]{2,5})$/)) {
        // eslint-disable-next-line
        if (!this.form.email.match(/^[A-Za-z0-9-_]+$/g)) {
          this.$store.dispatch('jNotificationScreen/toggleProblem', 'Please input email correctly');
        } else {
          this.loginAccount(this.form);
        }
      } else if (!this.$v.form.$error) {
        this.loginAccount(this.form);
      }
    },
    onReset() {
      this.form.email = '';
      this.form.password = '';
    },
  },
};
</script>



