<template>
  <div>
    <b-card>
      <h4 class="c--primary">
        COMPANY SETTING - ACTIVITY RESULT REMINDER
      </h4>

      <hr>

      <b-row>
        <b-col
          cols="8"
          class="p__r--1">
          <label class="m__b--1">Cannot create any activity from
          a lead which has an ongoing pre-activity
          </label>
        </b-col>
        <b-col
          cols="4"
          class="p__l--1">
          <b-form-group class="m__b--1">
            <j-toggle v-model="set_jojosales.lock_lead_pre_activity"/>
          </b-form-group>
        </b-col>
      </b-row>

      <hr>

      <b-row>
        <b-col
          cols="8"
          class="p__r--1">
          <label class="m__b--1">Cannot creat any activity and opportunity
          from a lead which has an ongoing opportunity
          </label>
        </b-col>
        <b-col
          cols="4"
          class="p__l--1">
          <b-form-group class="m__b--1">
            <j-toggle v-model="set_jojosales.lock_lead_opportunity"/>
          </b-form-group>
        </b-col>
      </b-row>

      <hr>

      <b-row>
        <b-col
          cols="8"
          class="p__r--1">
          <label class="m__b--1">Cannot add product which is used
          in an ongoing opportunity
          </label>
        </b-col>
        <b-col
          cols="4"
          class="p__l--1">
          <b-form-group class="m__b--1">
            <j-toggle v-model="set_jojosales.lock_product"/>
          </b-form-group>
        </b-col>
      </b-row>

      <hr>

      <b-form-group>
        <label class="bold">Result Reminder</label>
        <b-row>
          <b-col cols="4">
            <j-input
              v-model="set_jojosales.result_reminder.value"
              :max-length="25"
              type="number"
              placeholder="Select Number"/>
          </b-col>
          <b-col cols="4">
            <j-select
              v-model="set_jojosales.result_reminder.type"
              :disabled="false"
              :options="options_reminder"
              class="jInput"
              placeholder=""/>
          </b-col>
          <b-col cols="3">
            <b-button
              class="btn btn blue outline btn-secondary btn-edit"
              @click="setSalesConfiguration">
              <i class="fa fa-pencil"/>
            </b-button>
          </b-col>
        </b-row>

      </b-form-group>

    </b-card>
  </div>
</template>
<script>
import { jMixModeDetailFeatureSales } from '@/mixins/mode-form/feature/jMixModeDetailFeatureSales';

export default {
  mixins: [
    jMixModeDetailFeatureSales,
  ],
  data() {
    return {
      options_reminder: [
        {
          id: 1,
          text: 'Minute',
          value: 1,
        },
        {
          id: 2,
          text: 'Hour',
          value: 2,
        },
        {
          id: 3,
          text: 'Day',
          value: 3,
        },
      ],
      set_jojosales: {
        lock_product: false,
        lock_lead_pre_activity: false,
        lock_lead_opportunity: false,
        result_reminder: {
          status: '',
          value: '',
          type: 1,
        },
      },
    };
  },
};
</script>



