<template>
  <div>
    <j-title-header :options="opsi"/>
    <div v-show="load_first">

      <b-row>
        <b-col cols="9">
          <div class="float-to-left">
            <j-import-file
              v-if="default_import"
              :href-sample="require('@/assets/file/FORMAT_CREATE_FLOW_RULES.zip')"
              @import-data="importData"/>
            <b-button
              v-if="is_error"
              class="btn outline red"
              @click="$refs.modalRule.show()"
            >
              <i class="fa fa-close"/>
              OPEN ERROR DATA
            </b-button>
            <b-button
              v-else-if="data_import"
              class="btn green"
              @click="uploadData">
              UPLOAD
            </b-button>
          </div>
          <div>
            <div class="float-to-left">
              <jExportRules class="m__t--20" />
            </div>
          </div>

        </b-col>

        <b-col>
          <div class="create-flow">
            <b-button
              class="btn blue"
              @click="createRules">CREATE RULES</b-button>
          </div>
        </b-col>
      </b-row>


      <v-server-table
        ref="rulesServer"
        :url="env"
        :columns="columns"
        :options="options"
        class="jTable"
        @loading="loading"
        @loaded="loaded"
        @row-click="rowClick"
        @pagination="removeRowBackground">

        <div
          slot="beforeFilter"
          class="custom-search">
          <b-row>
            <b-col cols="7">
              <b-form
                @keyup.enter="filter(filter_data)">
                <b-form-group>
                  <j-input
                    v-model="filter_data"/>
                </b-form-group>
              </b-form>
            </b-col>
            <b-col
              class="p--1 p__l--2"
              style="align-self: center;"
              cols="5">
              <b-button
                class="btn blue outline"
                @click="filter(filter_data)"> <i class="fa fa-search bold" /> Search </b-button>
            </b-col>
          </b-row>
        </div>

        <div
          slot="flow"
          slot-scope="props">
          <div
            v-for="(flow, index) in props.row.flows"
            :class="{'light-blue-flow' : index % 2 === 0}"
            :key="index"
            class="flow-table flow-arrow">
            <i
              v-if="index === 0"
              class="fa fa-square"/>
            {{ flow.name }}
          </div>
        </div>

        <div
          slot="created_date"
          slot-scope="props">
          {{ props.row.created_date | moment("DD/MM/YYYY") }}
        </div>

      </v-server-table>
    </div>

    <div
      v-if="!load_first">
      <jLoadingTable/>
    </div>

    <j-progress-screen
      ref="ImportFile"
      :data-kirim="data_import"
      :url-api="url"
      :headers="headers"
      @finish="uploadDone"/>

    <div>
      <b-modal
        ref="modalRule"
        class="mod-form"
        centered>
        <h3 class="float-to-center c--red">
          Error Data
        </h3>
        <v-client-table
          v-if="error_data[0]"
          :data="error_data"
          :columns="column"
          :options="option"
          class="jTable">

          <div
            slot="ERROR"
            slot-scope="props">
            <div
              v-for="(err, index) in props.row.ERROR"
              :key="index">
              {{ err }}
            </div>
          </div>

        </v-client-table>
        <download-excel
          :data="error_data"
          class="float-to-right m__t--30"
          name="Rules-data-error.xls">
          <b-button class="btn green">
            Export Error
          </b-button>
        </download-excel>
      </b-modal>
    </div>
  </div>

</template>

<script>
import { mapState, mapActions } from 'vuex';
import { jRules } from '@/mixins/components/jojoflow/jRules';
import jLoadingTable from '@/components/skeleton-loading/jLoadingTable';
import jProgressScreen from '@/components/progress-screen/jProgressScreen';
import Vue from 'vue';
import jExportRules from './jExportRules';

export default {
  components: { jLoadingTable, jProgressScreen, jExportRules },
  mixins: [jRules],
  data() {
    return {
      rowing: null,
      default_import: true,
      opsi: {
        icon: 'fa-user',
        main_title: 'RULES',
        opt_title: '',
      },
      data_import: null,
      is_finish: false,
      list_error: [],
      error_data: [],
      is_error: false,
      column: ['POSITION_ID_OWNER', 'RULES_NAME', 'FLOWS', 'ERROR'],
      option: {
        filterable: false,
        perPage: 5,
        perPageValues: [5, 10, 20, 50, 100],
      },
    };
  },
  computed: {
    ...mapState('jRules', ['rules_table']),
    url() {
      const getEnv = 'jojoflow/rule/import/single';
      return getEnv;
    },
    headers() {
      const token = Vue.ls.get('Token');
      const headers = {
        Authorization: `Bearer ${token}`,
      };
      return headers;
    },
  },
  watch: {
    rowing(newData, oldData) {
      if (oldData) {
        this.removeRowClick(oldData);
      }
      // this.rowing = null;
    },
    // data_import() {
    //   this.$refs.ImportFile.toggleIsVisible();
    // },
  },
  created() {
    // this.getListRules();
  },
  methods: {
    ...mapActions('jRules', ['getListRules']),
    createRules() {
      this.$router.replace('/rules/new');
    },
    rowClick(data) {
      const elem = document.getElementsByClassName(`row-${data.row.id}`);
      elem[0].setAttribute('style', 'background-color: #b3e8f8;');
      this.rowing = data.row.id;
      this.$emit('row-click', data);
    },
    removeRowClick(data) {
      // console.log(data);
      if (this.rowing) {
        const elem = document.getElementsByClassName(`row-${data}`);
        if (elem.length === 1) {
          elem[0].setAttribute('style', 'background-color: none;');
        }
      }
    },
    removeRowBackground() {
      if (this.rowing) {
        const elem = document.getElementsByClassName(`row-${this.rowing}`);
        if (elem.length === 1) {
          elem[0].setAttribute('style', 'background-color: none;');
        }
      }
      this.rowing = null;
      this.$emit('pagination');
    },

    refreshTable() {
      this.$refs.rulesServer.getData();
    },

    // setIntervalToken() {
    //   setInterval(() => {
    //     if (!Vue.ls.get('idToken') && this.is_finish === false) {
    //       const refreshToken = {
    //         refresh_token: localStorage.getItem('refreshToken'),
    //       };
    //       this.$store.dispatch('jLogin/refreshTokenApi', refreshToken);
    //     }
    //   }, 1000);
    // },

    importData(val) {
      const fileData = val;
      const genNewData = [];
      const keys = Object.keys(val[0]);
      if (keys[0] !== 'POSITION_ID_OWNER' || keys[1] !== 'RULES_NAME' || keys[2] !== 'FLOW_IDS' || keys[3 !== 'FLOWS']) {
        this.$store.commit('jNotificationScreen/getProblemMessage', 'Please use header format correctly');
        this.$store.dispatch('jNotificationScreen/toggleProblem', 'Please use header format correctly');
      }
      // this.setIntervalToken();
      this.is_error = false;
      this.list_error = [];
      this.error_data = val;
      fileData.forEach((data, index) => {
        const pack = this.convertData(data, index);
        genNewData.push(pack);
      });
      this.data_import = genNewData;
      // this.$refs.ImportFile.toggleIsVisible();
    },
    convertData(passingData, indexData) {
      this.list_error[indexData] = [];
      this.$set(this.error_data[indexData], 'ERROR', []);
      // packing
      const packing = {
        name: '',
        description: '',
        flows: [],
      };

      // validation name
      const strRuleName = passingData.RULES_NAME ? passingData.RULES_NAME.trim() : '';
      const strPositionId = passingData.POSITION_ID_OWNER ? passingData.POSITION_ID_OWNER.trim() : '';
      if (strRuleName.length === 0 || strPositionId.length === 0) {
        this.is_error = true;
        this.list_error[indexData].push('- field rules name and position id owner is required');
        this.error_data[indexData].ERROR.push('- field rules name and position id owner is required');
      } else {
        this.$set(packing, 'name', `${passingData.RULES_NAME} - ${passingData.POSITION_ID_OWNER}`);
      }

      const strFlows = passingData.FLOWS ? passingData.FLOWS.trim() : '';
      if (strFlows.length === 0) {
        this.is_error = true;
        this.list_error[indexData].push('- field flows is required');
        this.error_data[indexData].ERROR.push('- field flows is required');
      } else {
        // split by semicolons
        let splitSemicolon;
        if (passingData.FLOWS.indexOf(';') > -1) {
          splitSemicolon = passingData.FLOWS.split(';');
        } else {
          splitSemicolon = [passingData.FLOWS];
        }

        // each flow
        const eachFlow = splitSemicolon.map((data) => {
          const re = /\s*(\([0-9a-zA-Z_]+\))\s*/g;
          const splt = data.split(re);
          const removeSpace = splt.filter(space => space !== '');
          return removeSpace;
        });

        // remove curly
        const removeCurly = eachFlow.map((data) => {
          const getData = data;
          data.forEach((strings, index) => {
            const replace1 = strings.replace(')', '');
            const replace2 = replace1.replace('(', '');
            getData[index] = replace2;
          });
          return getData;
        });

        const generateObj = removeCurly.map((data) => {
          const newObj = {
            name: data[1],
            type: parseInt(data[0], 10),
            order: data[2],
            members: data[3],
          };

          // is order?
          if (newObj.order === 'ORDER') {
            newObj.type = 1;
            newObj.minimum_action = 0;
          } else if (newObj.order === 'NO_ORDER') {
            newObj.type = 2;
            newObj.minimum_action = 0;
          } else if (newObj.order === 'MIN_1') {
            newObj.type = 3;
            newObj.minimum_action = 1;
          } else if (newObj.order === 'MIN_2') {
            newObj.type = 3;
            newObj.minimum_action = 2;
          } else if (newObj.order === 'MIN_3') {
            newObj.type = 3;
            newObj.minimum_action = 3;
          }

          // generate obj of members flow
          const splitMember = newObj.members.split(',');
          newObj.members = splitMember;

          // generate Obj in members
          const memberFlow = [];
          newObj.members.forEach((memdata, index) => {
            const memb = {
              code: memdata,
              order: index + 1,
            };
            memberFlow.push(memb);
          });

          newObj.members = memberFlow;

          return newObj;
        });

        packing.flows = generateObj;
      }

      return packing;
    },
    uploadData() {
      this.$refs.ImportFile.toggleIsVisible();
    },
    uploadDone() {
      this.refreshTable();
      this.$store.commit('jRules/showUploadRuleFinished');
      this.data_import = null;
      this.default_import = false;
      setTimeout(() => {
        this.default_import = true;
      }, 100);
    },
  },
};
</script>

<style lang="scss">

</style>



