/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';

export const jMixModeEditProjectPeriod = {
  created() {
    if (this.mode === 'edit') {
      this.modeEdit();
    }
  },
  methods: {
    ...mapActions('jStoreProject', ['getDetailPeriodProject']),

    modeEdit() {
      const req = {
        period: {
          id: Number(this.$route.params.id),
        },
      };
      this.getDetailPeriodProject(req);
    },

    generateData(val) {
      const period = {
        id: val.id,
        name: val.name,
        description: val.description,
        start_date: val.start_date,
        end_date: val.end_date,
      };
      return period;
    },
  },
  computed: {
    ...mapState('jStoreProject', ['detail_period']),
  },
  watch: {
    detail_period(val) {
      const detailPeriod = this.generateData(val);
      this.period_form = detailPeriod;
    },
  },
};



