<template>
  <div>
    <j-title-header :options="title"/>
    <jCategoryForm
      ref="form"
      @data="getData"/>
    <div class="float-to-left">
      <b-button
        class="btn blue m__r--15"
        @click="saveCreateCategory">Submit</b-button>
      <b-button
        class="btn grey"
        @click="$router.replace('/category/list')">Cancel</b-button>
    </div>
    <br>
  </div>
</template>

<script>
import { jMixModelCreateCategory } from '@/mixins/model/category/jMixModelCreateCategory';
import jCategoryForm from '@/components/form/category/jCategoryForm';

export default {
  components: {
    jCategoryForm,
  },
  mixins: [
    jMixModelCreateCategory,
  ],
  data() {
    return {
      title: {
        icon: 'fa-user',
        main_title: 'CREATE CATEGORY',
        opt_title: 'Administrator',
      },
      category_form: null,
    };
  },
  methods: {
    saveCreateCategory() {
      this.$refs.form.save();
      if (this.category_form !== null) {
        this.sendData();
      }
    },
    getData(data) {
      this.category_form = data;
    },
  },
};
</script>



