/* eslint-disable import/no-duplicates,import/prefer-default-export,object-shorthand */
/* eslint-disable no-unused-expressions,no-unused-vars,key-spacing */
import { mapActions, mapState } from 'vuex';
import jApi from '@/services/jApi';

export const jDivision = {
  data() {
    return {
      filter_data: '',
      env: 'jojoflow/setup/division/list',
      load_first: false,
      length_table: '',
      columns: ['code', 'name', 'total_member', 'description', 'created_date'],
      options: {
        perPage: 5,
        perPageValues: [5, 10, 20, 50, 100],
        sortIcon: {
          base: 'fa',
          up: 'fa-chevron-up',
          down: 'fa-chevron-down',
          is: 'fa-sort',
        },
        filterable: true,
        sortable: ['name', 'created_date'],
        headings: {
          code: 'CODE',
          name: 'NAME',
          total_member: 'MEMBER(S) TOTAL',
          description: 'DESCRIPTION',
          created_date: 'CREATED DATE',
        },
        texts: {
          filterPlaceholder: 'Search',
          limit: 'Rows per page',
        },
        rowClassCallback(row) {
          return `rowdiv-${row.id}`;
        },
        requestAdapter(data) {
          return {
            pagination: {
              limit: data.limit,
              page: data.page,
              column: data.orderBy ? data.orderBy : '',
              ascending: !!data.ascending,
              query: data.query ? data.query : '',
            },
          };
        },
        requestFunction(data) {
          const api = jApi.generateApi();
          return api.post(this.url, data).catch((e) => {
            this.dispatch('error', e);
            this.$store.commit('jNotificationScreen/getProblemMessage', e.message);
          });
        },
        responseAdapter({ data }) {
          if (data.length) {
            this.length_table = data.length;
          }
          return {
            data: data.data ? data.data : [],
            count: data.length,
          };
        },
      },
    };
  },
  computed: {
    ...mapState('jNotificationScreen', ['loading_screen']),
  },
  watch: {
    filter_data(val) {
      if (val === '') {
        this.$refs.divisionServer.setFilter('');
      }
    },
  },
  methods: {
    ...mapActions('jNotificationScreen', ['toggleLoading', 'showLoading', 'hideLoading', 'toggleProblem']),

    filter(val) {
      this.removeRowBackground();
      this.$refs.divisionServer.setFilter(val);
    },

    loading() {
      if (this.loading_screen) {
        this.showLoading();
      }

      if (this.load_first) {
        this.showLoading();
      }
    },
    loaded() {
      if (this.load_first) {
        this.hideLoading();
      } else {
        this.load_first = !this.load_first;
      }
    },
    rowClick(data) {
      const elem = document.getElementsByClassName(`rowdiv-${data.row.id}`);
      elem[0].setAttribute('style', 'background-color: #b3e8f8;');
      this.rowing = data.row.id;
      this.$emit('row-click', data);
    },
    removeRowClick(data) {
      if (this.rowing) {
        const elem = document.getElementsByClassName(`rowdiv-${data}`);
        elem[0].setAttribute('style', 'background-color: none;');
      }
    },
    removeRowBackground() {
      if (this.rowing) {
        const elem = document.getElementsByClassName(`rowdiv-${this.rowing}`);
        elem[0].setAttribute('style', 'background-color: none;');
      }
      this.rowing = null;
      this.$emit('pagination');
    },
  },
};



