<template>
  <div>
    <div v-show="division_data.length < 1">
      <b-button
        class="btn green"
        @click="exportDivision">
        Export Divisions
      </b-button>
    </div>
    <div v-show="division_data.length > 1">
      <download-excel
        :data="division_data"
        name="Division-Data.xls">
        <b-button
          ref="exportDivision"
          class="btn green">
          Export Divisions
        </b-button>
      </download-excel>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  data() {
    return {
      division_data: [],
      page: 1,
    };
  },

  computed: {
    ...mapState('jOrganigram', ['all_division_list', 'total_length_division']),
  },

  watch: {
    all_division_list(Val) {
      this.page += 1;
      const sortData = Val.map((newVal) => {
        const dataDivision = {
          DIVISION_ID: newVal.id,
          CODE: newVal.code,
          NAME: newVal.name,
          TOTAL_MEMBER: newVal.total_member,
          DESCRIPTION: newVal.description,
          CREATED_DATE: newVal.created_date,
          UPDATED_DATE: typeof newVal.updated_date !== 'undefined' ? newVal.updated_date : '',
          CREATED_BY: typeof newVal.created_by !== 'undefined' ? newVal.created_by.email : '',
          UPDATED_BY: typeof newVal.updated_by !== 'undefined' ? newVal.updated_by.email : '',
        };
        return dataDivision;
      });
      // this.division_data = sortData;
      this.division_data.push(...sortData);
      const requestAll = {
        pagination: {
          limit: 50,
          page: this.page,
          column: 'id',
          ascending: true,
          query: '',
        },
        export: 'yes',
      };
      if (this.division_data.length < this.total_length_division) {
        this.$store.dispatch('jOrganigram/getAllDivisions', requestAll);
      } else {
        this.$store.dispatch('jNotificationScreen/hideLoading', 'Please wait...');
        this.$refs.exportDivision.click();
        this.division_data = [];
        this.page = 1;
      }
    },

  },

  methods: {
    exportDivision() {
      const requestAll = {
        pagination: {
          limit: 50,
          page: 1,
          column: 'id',
          ascending: true,
          query: '',
        },
      };
      this.$store.dispatch('jNotificationScreen/showLoading', 'Please wait...');
      this.$store.dispatch('jOrganigram/getAllDivisions', requestAll);
    },
  },
};
</script>



