<template>
  <div class="jNumberFormatForm">
    <b-row>
      <b-col
        md="12"
        sm="6"
        cols="12">

        <b-row>
          <b-col
            md="6"
            sm="6"
            cols="12">
            <b-form-group :class="{'error-input' : $v.number_format_form.name.$error}">
              <label>Name</label>
              <j-input
                v-model="$v.number_format_form.name.$model"
                placeholder="Name"/>
            </b-form-group>
          </b-col>
          <b-col
            md="6"
            sm="6"
            cols="12">
            <b-form-group :class="{'error-input' : $v.number_format_form.type.$error}">
              <label>Type</label>
              <j-single-select
                v-model="$v.number_format_form.type.$model"
                :options="list_type_form"
                value-format="object"
                placeholder="Select Type"
                label="name"/>
            </b-form-group>
          </b-col>
        </b-row>

        <b-form-group :class="{'error-input' : $v.number_format_form.is_default.$error}">
          <label>Set As Default</label>
          <j-toggle
            v-model="$v.number_format_form.is_default.$model"
            off="No"
            on="Yes"/>
        </b-form-group>

        <div>
          <drop
            class="drop-no-border list"
            @drop="handleDrop(lists, ...arguments)">
            <drag
              v-for="item in lists"
              :key="item"
              :class="{ [item]: true }"
              :transfer-data="{ item: item, list: lists, example: 'lists' }"
              class="drag">
              {{ item }}
            </drag>
          </drop>
        </div>
        <div>
          <i class="fa fa-arrow-down drag-down"/>
        </div>
        <div>
          <drop
            class="drop list"
            @drop="handleDrop(tolists, ...arguments)">
            <drag
              v-for="(item, index) in tolists"
              :key="index"
              :class="{ [item]: true }"
              :transfer-data="{ item: item, list: tolists, example: 'tolists' }"
              class="drag">
              <div class="draggable-close c-red">
                <i
                  class="fa fa-times-circle"
                  @click="deleteItem(item, index)"/>
              </div>
              <div
                v-if="editable[index]"
                class="cursorText">
                <span
                  v-if="array_edit_text[index] === false"
                  @click="yourClickHandler(index)">{{ item }}</span>
                <input
                  v-else
                  v-model="text_input"
                  style="width: 80px"
                  @keyup.enter="onSubmit(index, item)">
              </div>
              <div v-else>
                {{ item }}
              </div>
            </drag>
          </drop>
        </div>

        <b-form-group :class="{'error-input' : $v.number_format_form.pattern.$error || epattern}">
          <label>Preview No</label>
          <j-input
            v-model="$v.number_format_form.pattern.$model"
            :disabled="true"/>
        </b-form-group>

      </b-col>
    </b-row>
  </div>
</template>

<script>
/* eslint-disable max-len */
import jMixNumberFormat from '@/mixins/components/get-list-option-form/jMixNumberFormat';
import { jMixValidationFormNumberFormat } from '@/mixins/validations/number-format/jMixValidationFormNumberFormat';
import { jMixOptionsFormNumberFormat } from '@/mixins/list-options/number-format/jMixOptionsFormNumberFormat';
import { jMixModeCreateNumberFormat } from '@/mixins/mode-form/number-format/jMixModeCreateNumberFormat';
import { jMixModeEditNumberFormat } from '@/mixins/mode-form/number-format/jMixModeEditNumberFormat';
import { Drag, Drop } from 'vue-drag-drop';

export default {
  components: { Drag, Drop },
  mixins: [
    jMixNumberFormat,
    jMixValidationFormNumberFormat,
    jMixOptionsFormNumberFormat,
    jMixModeCreateNumberFormat,
    jMixModeEditNumberFormat,
  ],
  props: {
    mode: {
      type: String,
      default: 'create',
    },
  },
  data() {
    return {
      array_edit_text: [],
      editable: [],
      number_format_form: {
        id: 0,
        name: '',
        type: null,
        is_default: false,
        pattern: '',
        patterns: '',
      },
      lists: [
        'ID', 'Date', 'Month (No)', 'Month (Alpha)', 'Year (2D)', 'Year (4D)', 'Text', '. (Dot)', '/ (Slash)', '- (Dash)',
      ],
      tolists: [],
      data_pattern: '',
      default_data: null,
      text_input: '',
      edit_text: [],
      flag_toList: false,
      epattern: false,
    };
  },
  computed: {
    sort_form_data() {
      const data = this.number_format_form;
      return data;
    },
  },
  watch: {
    default_data(newVal) {
      // eslint-disable-next-line
      var previewNumber = '';
      // eslint-disable-next-line
      var previewList = [];
      // eslint-disable-next-line
      var arrPattern = newVal.split('|');

      if (arrPattern.length > 0) {
        // eslint-disable-next-line
        for (var i in arrPattern) {
          const code = arrPattern[i];
          if (code !== '') {
            this.array_edit_text.push(false);
          }

          if (code === '.') {
            previewNumber = previewNumber.concat('.');
            previewList.push('. (Dot)');
            this.data_pattern = this.data_pattern.concat('.,');
          } else if (code === '/') {
            previewNumber = previewNumber.concat('/');
            previewList.push('/ (Slash)');
            this.data_pattern = this.data_pattern.concat('/,');
          } else if (code === '-') {
            previewNumber = previewNumber.concat('-');
            previewList.push('- (Dash)');
            this.data_pattern = this.data_pattern.concat('-,');
          } else if (code === 'date') {
            previewNumber = previewNumber.concat('29');
            previewList.push('Date');
            this.data_pattern = this.data_pattern.concat('date,');
          } else if (code === 'month01') {
            previewNumber = previewNumber.concat('12');
            previewList.push('Month (No)');
            this.data_pattern = this.data_pattern.concat('month01,');
          } else if (code === 'month02') {
            previewNumber = previewNumber.concat('DEC');
            previewList.push('Month (Alpha)');
            this.data_pattern = this.data_pattern.concat('month02,');
          } else if (code === 'year01') {
            previewNumber = previewNumber.concat('17');
            previewList.push('Year (2D)');
            this.data_pattern = this.data_pattern.concat('year01,');
          } else if (code === 'year02') {
            previewNumber = previewNumber.concat('2017');
            previewList.push('Year (4D)');
            this.data_pattern = this.data_pattern.concat('year02,');
          } else if (code === 'id') {
            previewNumber = previewNumber.concat('003');
            previewList.push('ID');
            this.data_pattern = this.data_pattern.concat('id,');
          } else if (code !== '') {
            previewNumber = previewNumber.concat(code.substr(1, code.length - 2));
            previewList.push(code.substr(1, code.length - 2));
            // eslint-disable-next-line
            this.data_pattern = this.data_pattern.concat('[' + code.substr(1, code.length - 2) + '],');
            this.edit_text.push(code.substr(1, code.length - 2));
          }
        }
      } else {
        previewNumber = '';
        previewList = [];
      }
      this.number_format_form.pattern = previewNumber;
      this.tolists = previewList;
    },
    tolists(val) {
      if (this.mode === 'edit') {
        val.forEach((e) => {
          const exists = this.edit_text.find(a => a === e);
          if (this.flag_toList === false) {
            if (exists !== undefined) {
              this.editable.push(true);
            } else {
              this.editable.push(false);
            }
          }
        });
      }
      this.flag_toList = true;
    },
    data_pattern(val) {
      if (val === '') {
        this.epattern = true;
      } else if (val !== '') {
        this.epattern = false;
      }
    },
  },
  methods: {
    save(val) {
      this.$v.number_format_form.$touch();
      if (!this.$v.number_format_form.$error && !this.epattern) {
        this.$emit('data', this.sort_form_data, val);
      } else {
        this.$emit('data', null);
      }
    },

    yourClickHandler(index) {
      this.$set(this.array_edit_text, index, true);
    },

    deleteItem(item, index) {
      this.tolists.splice(index, 1);
      this.editable.splice(index, 1);
      this.array_edit_text.splice(index, 1);

      if (item === '. (Dot)') {
        this.number_format_form.pattern = this.number_format_form.pattern.replace('.', '');
        this.data_pattern = this.data_pattern.replace('.,', '');
      } else if (item === '/ (Slash)') {
        this.number_format_form.pattern = this.number_format_form.pattern.replace('/', '');
        this.data_pattern = this.data_pattern.replace('/,', '');
      } else if (item === '- (Dash)') {
        this.number_format_form.pattern = this.number_format_form.pattern.replace('-', '');
        this.data_pattern = this.data_pattern.replace('-,', '');
      } else if (item === 'Date') {
        this.number_format_form.pattern = this.number_format_form.pattern.replace('29', '');
        this.data_pattern = this.data_pattern.replace('date,', '');
      } else if (item === 'Month (No)') {
        this.number_format_form.pattern = this.number_format_form.pattern.replace('12', '');
        this.data_pattern = this.data_pattern.replace('month01,', '');
      } else if (item === 'Month (Alpha)') {
        this.number_format_form.pattern = this.number_format_form.pattern.replace('DEC', '');
        this.data_pattern = this.data_pattern.replace('month02,', '');
      } else if (item === 'Year (2D)') {
        this.number_format_form.pattern = this.number_format_form.pattern.replace('17', '');
        this.data_pattern = this.data_pattern.replace('year01,', '');
      } else if (item === 'Year (4D)') {
        this.number_format_form.pattern = this.number_format_form.pattern.replace('2017', '');
        this.data_pattern = this.data_pattern.replace('year02,', '');
      } else if (item === 'ID') {
        this.number_format_form.pattern = this.number_format_form.pattern.replace('003', '');
        this.data_pattern = this.data_pattern.replace('id,', '');
      } else if (item === 'Text') {
        this.number_format_form.pattern = this.number_format_form.pattern.replace('Text', '');
        this.data_pattern = this.data_pattern.replace('[text],', '');
      } else {
        this.number_format_form.pattern = this.number_format_form.pattern.replace(item, '');
        // eslint-disable-next-line
        this.data_pattern = this.data_pattern.replace('[' + item + '],', '');
      }

      this.number_format_form.patterns = this.data_pattern.replace(/,/g, '|');
    },

    onSubmit(index, item) {
      this.$set(this.array_edit_text, index, false);
      this.$set(this.tolists, index, this.text_input);

      if (this.number_format_form.pattern.includes('Text')) {
        this.number_format_form.pattern = this.number_format_form.pattern.replace('Text', this.text_input);
        // eslint-disable-next-line
        this.data_pattern = this.data_pattern.replace('[text],', '[' + this.text_input + '],');
      } else {
        this.number_format_form.pattern = this.number_format_form.pattern.replace(item, this.text_input);
        // eslint-disable-next-line
        this.data_pattern = this.data_pattern.replace('[' + item + '],', '[' + this.text_input + '],');
      }
      this.number_format_form.patterns = this.data_pattern.replace(/,/g, '|');
      this.text_input = '';
    },

    handleDrop(toList, data) {
      const fromList = data.list;
      if (fromList.length !== toList.length) {
        if (fromList.length === 10) {
          toList.push(data.item);

          if (data.item === '. (Dot)') {
            this.number_format_form.pattern = this.number_format_form.pattern.concat('.');
            this.data_pattern = this.data_pattern.concat('.,');
            this.array_edit_text.push(false);
            this.editable.push(false);
          } else if (data.item === '/ (Slash)') {
            this.number_format_form.pattern = this.number_format_form.pattern.concat('/');
            this.data_pattern = this.data_pattern.concat('/,');
            this.array_edit_text.push(false);
            this.editable.push(false);
          } else if (data.item === '- (Dash)') {
            this.number_format_form.pattern = this.number_format_form.pattern.concat('-');
            this.data_pattern = this.data_pattern.concat('-,');
            this.array_edit_text.push(false);
            this.editable.push(false);
          } else if (data.item === 'Date') {
            this.number_format_form.pattern = this.number_format_form.pattern.concat('29');
            this.data_pattern = this.data_pattern.concat('date,');
            this.array_edit_text.push(false);
            this.editable.push(false);
          } else if (data.item === 'Month (No)') {
            this.number_format_form.pattern = this.number_format_form.pattern.concat('12');
            this.data_pattern = this.data_pattern.concat('month01,');
            this.array_edit_text.push(false);
            this.editable.push(false);
          } else if (data.item === 'Month (Alpha)') {
            this.number_format_form.pattern = this.number_format_form.pattern.concat('DEC');
            this.data_pattern = this.data_pattern.concat('month02,');
            this.array_edit_text.push(false);
            this.editable.push(false);
          } else if (data.item === 'Year (2D)') {
            this.number_format_form.pattern = this.number_format_form.pattern.concat('17');
            this.data_pattern = this.data_pattern.concat('year01,');
            this.array_edit_text.push(false);
            this.editable.push(false);
          } else if (data.item === 'Year (4D)') {
            this.number_format_form.pattern = this.number_format_form.pattern.concat('2017');
            this.data_pattern = this.data_pattern.concat('year02,');
            this.array_edit_text.push(false);
            this.editable.push(false);
          } else if (data.item === 'ID') {
            this.number_format_form.pattern = this.number_format_form.pattern.concat('003');
            this.data_pattern = this.data_pattern.concat('id,');
            this.array_edit_text.push(false);
            this.editable.push(false);
          } else if (data.item === 'Text') {
            this.number_format_form.pattern = this.number_format_form.pattern.concat('Text');
            this.data_pattern = this.data_pattern.concat('[text],');
            this.array_edit_text.push(false);
            this.editable.push(true);
          }
        }
        this.number_format_form.patterns = this.data_pattern.replace(/,/g, '|');
      }
    },

  },
};
</script>



