import jCategory from '@/views/category/jCategory';
import jCategoryCreate from '@/views/category/create/jCategoryCreate';
import jCategoryEdit from '@/views/category/edit/jCategoryEdit';
import auth from '@/middleware/auth';
import admin from '@/middleware/admin';

const category = [
  {
    path: '/category/list',
    name: 'jCategory',
    component: jCategory,
    meta: {
      title: 'Home',
      layout: 'jWrapper',
    },
  },
  {
    path: '/category/create',
    name: 'jCategoryCreate',
    component: jCategoryCreate,
    meta: {
      title: 'Create Category',
      layout: 'jWrapper',
    },
  },
  {
    path: '/category/edit/:id',
    name: 'jCategoryEdit',
    component: jCategoryEdit,
    meta: {
      title: 'Edit Category',
      layout: 'jWrapper',
    },
  },
];

category.map((dataPath) => {
  const eachData = dataPath;
  eachData.meta.middleware = [admin, auth];
  return eachData;
});

export default category;



