/* eslint-disable no-shadow,consistent-return,no-param-reassign */
import router from '@/router';
import apiRule from '@/services/jApiRule';

const state = {
  // Set default state(s) here
  rules_table: [],
  is_rules_deleted: false,
  rules_detail: null,
  upload_rules_finished: false,
  all_rule_list: null,
  total_length_rule: null,
  // users_list: [],
  // divisions_list: [],
  // by_divisions_list: [],
  // new_division: null,
};

const getters = {
  // Change value of state(s) with getter

};

const mutations = {
  // list rules
  showListRules(state, data) {
    state.rules_table = data;
  },

  // rules was Deleted
  rulesDeleted(state) {
    state.is_rules_deleted = !state.is_rules_deleted;
  },

  // show detail rules
  showDetailRules(state, data) {
    state.rules_detail = data;
  },

  showUploadRuleFinished(state) {
    state.upload_rules_finished = !state.upload_rules_finished;
  },

  // set all rules
  setAllRules(state, payload) {
    state.total_length_rule = payload.length;
    state.all_rule_list = payload.data;
  },
  //
  // // list select users
  // showListUsers(state, data) {
  //   state.users_list = data;
  // },
  //
  // // list divisions
  // showListDivisions(state, data) {
  //   state.divisions_list = data;
  // },
  //
  // // list by divisions
  // showListByDivisions(state, data) {
  //   state.by_divisions_list = data;
  // },
  //
  // // show new division
  // showNewDivisions(state, data) {
  //   state.new_division = data;
  // },
};

const actions = {
  // get list table flow
  async getListRules({ commit, dispatch }) {
    try {
      const listRule = await apiRule.listRule();
      commit('showListRules', listRule);
    } catch (err) {
      dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },

  // create new rules
  async createNewRules({ dispatch, commit }, data) {
    dispatch('jNotificationScreen/showLoading', 'Please wait', { root: true });
    try {
      const createRule = await apiRule.createRule(data);
      commit('showListRules', createRule);
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      router.push('/flows');
    } catch (err) {
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },

  // update rule
  async updateRule({ dispatch }, data) {
    dispatch('jNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const updateRule = await apiRule.updateRule(data);
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      dispatch('jNotificationScreen/toggleSuccess', updateRule.message, { root: true });
      setTimeout(() => {
        router.push('/flows');
      }, 2000);
    } catch (err) {
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },

  // remove rules
  async removeRules({ dispatch, commit }, data) {
    dispatch('jNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const removeRule = await apiRule.removeRule(data);
      commit('rulesDeleted');
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      dispatch('jNotificationScreen/toggleSuccess', removeRule.message, { root: true });
    } catch (err) {
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },

  // detail rules
  async getDetailRules({ dispatch, commit }, data) {
    dispatch('jNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const detailRule = await apiRule.detailRule(data);
      commit('showDetailRules', detailRule);
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
    } catch (err) {
      dispatch('jNotificationScreen/hideLoading', '', { root: true });
      dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },

  async getAllRules({ dispatch, commit }, data) {
    // dispatch('jNotificationScreen/showLoading', 'Please wait...', { root: true });
    try {
      const getList = await apiRule.listRule(data);
      commit('setAllRules', getList);
      // dispatch('jNotificationScreen/hideLoading', '', { root: true });
    } catch (err) {
      // dispatch('jNotificationScreen/hideLoading', {}, { root: true });
      dispatch('jNotificationScreen/toggleProblem', err.response.data.message, { root: true });
    }
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};



