<template>
  <div class="jTableReport">
    <v-client-table
      :data="tableData"
      :columns="columns"
      :options="options"
      class="jTable">

      <div
        slot="checkbox"
        slot-scope="props">
        <b-form-group>
          <b-form-checkbox-group v-model="selectedRows">
            <b-form-checkbox :value="props.row"/>
          </b-form-checkbox-group>
        </b-form-group>
      </div>
      <a
        slot="form"
        slot-scope="props"
        @click="$router.replace(`/jojoform/form-creator/new/${props.row.id}`)">
        {{ props.row.form }} hap
      </a>

    </v-client-table>

    <jFloatingPrompt
      v-if="show_floating"
      ref="floating">
      <b-row>
        <b-col cols="1">
          <div class="count">
            {{ selectedRows.length }}
          </div>
        </b-col>
        <b-col cols="2">
          <div class="text">
            <div>Forms</div>
            <div>Selected</div>
          </div>
        </b-col>
        <b-col>
          <div class="float-to-right">
            <b-button class="btn blue outline">DELETE</b-button>
          </div>
        </b-col>

      </b-row>
    </jFloatingPrompt>
  </div>
</template>

<script>
import jFloatingPrompt from '@/components/new-floating-templete/jFloatingPrompt';

const datasen = [
  {
    id: 90,
    form: 'haham',
    created_date: '12/02/2018',
  },
  {
    id: 901,
    form: 'pompin',
    created_date: '12/02/2018',
  },
  {
    id: 920,
    form: 'pintom',
    created_date: '12/02/2018',
  },
];

export default {
  components: {
    jFloatingPrompt,
  },
  data() {
    return {
      columns: ['checkbox', 'form', 'created_date'],
      tableData: datasen,
      selectedRows: [],
      show_floating: false,
      options: {
        // see the options API
        headings: {
          // eslint-disable-next-line
          checkbox(h) {
            // eslint-disable-next-line
            const _this = this;
            if (this.selectedRows.length === this.tableData.length) {
              return h(
                'i',
                {
                  attrs: {
                    id: 'select-all-unchecked',
                    class: 'fa fa-square-o i--center-block',
                    style: 'display: block; font-size: 20px;',
                  },
                  on: {
                    click() {
                      _this.$refs.floating.hide();
                      setTimeout(() => {
                        _this.selectedRows = [];
                        _this.show_floating = false;
                      }, 200);
                    },
                  },
                },
                [''],
              );
            } if (this.selectedRows.length !== this.tableData.length) {
              return h(
                'i',
                {
                  attrs: {
                    id: 'select-all-checked',
                    class: 'fa fa-check-square i--center-block',
                    style: 'display: block; font-size: 20px;',
                  },
                  on: {
                    click() {
                      _this.selectedRows = _this.tableData;
                    },
                  },
                },
                [''],
              );
            }
          },
          form: 'FORM',
          created_date: 'CREATED DATE',
        },
        sortIcon: { base: 'fa', up: 'fa-chevron-up', down: 'fa-chevron-down', is: 'fa-sort' },
        dateFormat: 'DD/MM/YYYY',
        filterable: false,
        sortable: ['form', 'created_date'],
      },
    };
  },
  watch: {
    selectedRows(newData) {
      if (this.selectedRows[0]) {
        this.show_floating = true;
      } else if (!newData[0]) {
        this.$refs.floating.hide();
        setTimeout(() => {
          this.show_floating = false;
        }, 200);
      }
    },
  },
};
</script>

<style lang="scss">

</style>



