/* eslint-disable import/no-unresolved,import/first */
import * as all from '@jojocoders/jojonomic-ui';
import { layout } from 'vue-extend-layout';
import Vue from 'vue';
import stores from './stores/index';
import router from './router';
import Storage from 'vue-ls';
import JsonExcel from 'vue-json-excel';
import customize from './utils/customize/company';
// import VueDraggable from 'vue-draggable';

// Vue.use(VueDraggable)

// axios global
window.axios = require('axios');

// vendor
const keys = Object.keys(all);
keys.forEach((data) => {
  if (Array.isArray(all[data])) {
    Vue.use(...all[data]);
  } else {
    Vue.use(all[data]);
  }
});


Vue.use(Storage);
Vue.component('downloadExcel', JsonExcel);

// import this project scss
import './sass/main.scss';

// eslint-disable-next-line
let customizeCimb;
const processEnv = process.env.COMPANY ? process.env.COMPANY.toUpperCase() : 'JOJONOMIC';

if (customize[processEnv].style) {
  // const styeCustomize = customize[processEnv].style;
  // eslint-disable-next-line
  // customizeCimb = require(`./sass/customize-by-company/${styeCustomize}.scss`);
}


// register global component here
// ex: Vue.component('header', Header)

Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store: stores,
  ...layout,
});



